"use client";
//API
import axiosInstance from "@/api/interceptor";
//Redux
import { quotelist } from "@/redux/slices/userSlice";
//Components
import ToastMessage from "@/Components/ToastMessage";

export async function SendQuotationApi(
  firstname,
  email,
  phonenumber,
  bussinessname,
  country,
  state,
  city,
  pincode,
  comment,
  qoutedetails,
  array,
  dispatch,
  router,
  quan,
  total
) {
  const data = {
    firstname: firstname,
    email: email,
    telephone: phonenumber,
    total: qoutedetails,
    businessName: bussinessname,
    postalCode: pincode,
    city: city,
    state: state?.label,
    stateId: state?.value,
    country: country?.label,
    countryId: country?.value,
    comment: comment,
    currencyId: 57,
    quantity: quan,
    total: total,
    productDetails: array,
  };
  const result = await axiosInstance.post(`quotation`, data);
  if (result && result.data.status == 1) {
    ToastMessage({ type: "success", message: result.data.message });
    dispatch(quotelist(""));
    router.push("/quotation-success");
  }
}

//quotationlist api
export const getQuotationlist = async (status) => {
  const data = await axiosInstance.get(
    `quotation?statusId=${status}&limit=5&offset=0`
  );
  return data?.data;
};

//getQuotationlistApi
export const getQuotationlistapi = async (
  status,
  setQuatationList,
  offset,
  setLoader
) => {
  const result = await axiosInstance.get(
    `quotation?statusId=${status}&limit=5&offset=${offset ? offset : 0}`
  );
  //return data?.data;
  if (result && result.data && result.data.data) {
    setQuatationList(result.data.data);
    setLoader(true);
  }
};

//getQuotationlistApiCount
export const getQuotationlistapiCount = async (
  status,
  setgetQuotationlistApiCount
) => {
  const result = await axiosInstance.get(
    `quotation?statusId=${status}&limit=0&offset=0&count=1`
  );
  //return data?.data;
  if (result && result.data && result.data.data) {
    setgetQuotationlistApiCount(result.data.data);
  }
};
