"use client";
//React and Next
import { Fragment, useEffect, useState } from "react";
//3rd Party
import {
  Accordion,
  AccordionContent,
  AccordionPanel,
  AccordionTitle,
} from "flowbite-react";
//API
import { filterListApi } from "./SpecificationApi";
//Redux
import { useDispatch, useSelector } from "react-redux";
import {
  setAttributeFilter,
  setSelectedFilterIds,
} from "@/redux/slices/variant";
//Component
import { useTranslation } from "react-i18next";

export default function AttributeFilter({ params }) {
  const [filterList, setFilterList] = useState([]);
  const [attmanuIdArr, setattmanuIdArr] = useState([]);
  const [search, setSearch] = useState("");

  const attibutefilter = useSelector((s) => s.VariantRedux.attributeFilter);
  const selectedval = useSelector((s) => s.VariantRedux.selectedFilterIds);
  const dispatch = useDispatch();
  const { t } = useTranslation();
  useEffect(() => {
    filterListApi(params, setFilterList);
    dispatch(setAttributeFilter([]));
    dispatch(setSelectedFilterIds([]));
  }, [params]);

  const changesetchecked = (e, itemId, id, headId, type) => {
    window.scrollTo({
      top: 0,
      behavior: "smooth", // smooth scrolling animation
    });
    let manuSubArr = [...attibutefilter];
    if (manuSubArr?.indexOf(itemId) !== -1) {
      manuSubArr = manuSubArr?.filter((manufactId) => manufactId != itemId);
    } else {
      manuSubArr.push(itemId);
    }

    setattmanuIdArr(manuSubArr);
    dispatch(setAttributeFilter(manuSubArr));

    if (selectedval?.indexOf(id) !== -1) {
      dispatch(setSelectedFilterIds(selectedval?.filter((ind) => ind != id)));
    } else {
      dispatch(setSelectedFilterIds([...selectedval, id]));
    }
    const array = selectedval;
  };

  const handleSearch = (e, section) => {
    setSearch(e.target.value);
    const datasort = section.sort((a, b) => {
      if (a.itemName.toLowerCase().includes(e.target.value.toLowerCase()))
        return -1;
      if (b.itemName.toLowerCase().includes(e.target.value.toLowerCase()))
        return 1;
      return 0;
    });
  };

  return (
    filterList &&
    filterList?.map((data, index) => (
      <Fragment key={index}>
        <Accordion className="shadow-none mb-[16px] [&_button]:!mb-0 pb-[16px] border-[#EAEAEA] border-0 border-b-[1px] border-solid rounded-none">
          <AccordionPanel>
            <AccordionTitle className='bg-white hover:bg-[bg-white] mb-[16px] p-0 [&~[data-testid="flowbite-accordion-content"]]:!border-t-0 focus:ring-0'>
              <span className="font-semibold text-[#303030] text-[14px] leading-[17px]">
                {" "}
                {data.sectionName}{" "}
              </span>
            </AccordionTitle>
            <AccordionContent className="mt-[12px] p-0">
              {data && data?.sectionItem && data?.sectionItem?.length > 4 && (
                <div className="w-full mb-4">
                  <input
                    type="text"
                    placeholder={t("orderhistory.Search")}
                    onChange={(e) => handleSearch(e, data?.sectionItem)}
                    className="w-full text-sm text-black bg-no-repeat border rounded focus:shadow-none ps-8 border-slate-300 focus:border-slate-300 focus:ring-0 h-9 text-dark placeholder:text-slate-400 search-icon"
                  />
                </div>
              )}
              <div className="block pr-3">
                {data &&
                  data?.sectionItem &&
                  data?.sectionItem?.map((item, index) => (
                    <div
                      className="relative flex items-center space-x-[8px] mb-[12px] last-of-type:mb-0"
                      key={index}
                    >
                      <div className="flex items-center rtl:ml-3 h-[13px] shrink-0">
                        <input
                          id="comments"
                          name={item?.id}
                          type="checkbox"
                          onChange={(e) => {}}
                          checked={selectedval.includes(item?.id)}
                          className="block checked:bg-[#000000] border-[#373737] focus:ring-0 focus:ring-transparent w-3 max-w-[14px] h-3 text-black cursor-pointer"
                          onClick={(e) =>
                            changesetchecked(
                              e,
                              data?.sectionName + "~" + item?.itemName,
                              item?.id,
                              data?.sectionItem,
                              data?.sectionType
                            )
                          }
                        />
                      </div>
                      <div className="top-[1px] relative w-full text-sm leading-6">
                        <label
                          htmlFor={item?.id}
                          onClick={(e) =>
                            changesetchecked(
                              e,
                              data?.sectionName + "~" + item?.itemName,
                              item?.id,
                              data?.sectionItem,
                              data?.sectionType
                            )
                          }
                          className="flex justify-between items-center font-normal text-[#333333] text-sm leading-3 cursor-pointer"
                        >
                          {item?.itemName}
                        </label>
                      </div>
                    </div>
                  ))}
              </div>
            </AccordionContent>
          </AccordionPanel>
        </Accordion>
      </Fragment>
    ))
  );
}
