"use client";
//React and Next
import { useEffect, useState } from "react";
import { useRouter } from "nextjs-toploader/app";
//3rd party
import Select from "react-select";
//Redux
import { useDispatch, useSelector } from "react-redux";
//API
import { CountryValue, StateValue } from "@/api/clientsideaction/buyerapi";
import { SendQuotationApi } from "../QuotationApi";
//Utils
import {
  EmailValidator,
  NameValidator,
  PhonenoValidator,
  exceptThisSymbols,
} from "@/utilities/regex";
import { PriceCalculate } from "@/utilities/PriceCalculate";
//Components
import { useTranslation } from "react-i18next";

export default function QuotationContact({ comment }) {
  const [firstname, setFirstname] = useState("");
  const [firstnameerr, setFirstnameerr] = useState("");
  const [email, setEmail] = useState("");
  const [emailerr, setEmailerr] = useState("");
  const [phonenumber, setPhonenumber] = useState("");
  const [phonenumbererr, setPhonenumbererr] = useState("");
  const [bussinessname, setBussinessname] = useState("");
  const [bussinessnameerr, setBussinessnameerr] = useState("");
  const [country, setCountry] = useState("");
  const [countryErr, setCountryErr] = useState("");
  const [state, setState] = useState("");
  const [stateErr, setStateErr] = useState("");
  const [city, setCity] = useState("");
  const [cityErr, setCityErr] = useState("");
  const [pincode, setPincode] = useState("");
  const [pincodeErr, setPincodeErr] = useState("");
  const [createcheck, setCreatecheck] = useState(false);
  const [inducountry, setInduCountry] = useState(false);
  const [countryOption, setCountryOption] = useState();
  const [statedependent, setStateDependent] = useState();
  const [indu, setIndu] = useState(false);
  const [stateOption, setStateOption] = useState();
  const dispatch = useDispatch();
  const qoutedetails = useSelector((state) => state.userReducer.quote);
  const router = useRouter();
  const { t } = useTranslation();

  const hanldequotepost = () => {
    setCreatecheck(true);
    if (validate()) {
      let array = [];
      let arr = [];
      let quantity = [];

      qoutedetails?.map((datas, index) => {
        let obj = {};
        (obj.productId = datas?.productId),
          (obj.productName = datas?.name),
          (obj.quantity = datas?.quantity),
          (obj.productPrice = datas?.pricerefer !== "" ? datas?.pricerefer : 0),
          (obj.total =
            datas?.quantity *
            PriceCalculate(
              datas?.price,
              datas?.pricerefer,
              datas?.flag,
              datas?.taxType,
              datas?.taxValue
            )),
          (obj.vendorId = datas?.vendorId),
          (obj.sku = datas?.sku),
          (obj.basePrice = datas?.price),
          (obj.taxType = datas?.taxType),
          (obj.taxValue = datas?.taxValue);
        array.push(obj);
        quantity.push(datas?.quantity);
        arr.push(
          datas?.quantity *
            PriceCalculate(
              datas?.price,
              datas?.pricerefer,
              datas?.flag,
              datas?.taxType,
              datas?.taxValue
            )
        );
      });
      let quan = quantity.reduce((a, b) => parseInt(a) + parseInt(b));
      let total = arr.reduce((a, b) => parseInt(a) + parseInt(b));
      SendQuotationApi(
        firstname,
        email,
        phonenumber,
        bussinessname,
        country,
        state,
        city,
        pincode,
        comment,
        qoutedetails?.length,
        array,
        dispatch,
        router,
        quan,
        total
      );
    }
  };

  useEffect(() => {
    if (createcheck) {
      validate();
    }
  }, [
    firstname,
    email,
    phonenumber,
    bussinessname,
    country,
    state,
    city,
    pincode,
  ]);

  const validate = () => {
    let ValidateObj = {
      name: true,
      email: true,
      number: true,
      bussinesname: true,
      country: true,
      state: true,
      city: true,
      pincode: true,
    };
    if (email == "") {
      setEmailerr(t("validationMessage.emailEmpty"));
      ValidateObj.email = false;
    } else {
      if (EmailValidator(email)) {
        setEmailerr("");
        ValidateObj.email = true;
      } else {
        setEmailerr(t("validationMessage.incorrectEmail"));
        ValidateObj.email = false;
      }
    }

    if (phonenumber == "") {
      setPhonenumbererr(t("validationMessage.phoneNumberEmpty"));
      ValidateObj.number = false;
    } else if (PhonenoValidator(phonenumber)) {
      setPhonenumbererr("");
      ValidateObj.number = true;
    } else {
      setPhonenumbererr(t("validationMessage.minimumPhoneNumberLength"));
      ValidateObj.number = false;
    }
    if (firstname == "") {
      setFirstnameerr(t("validationMessage.nameEmpty"));
      ValidateObj.name = false;
    } else {
      setFirstnameerr("");
      ValidateObj.name = true;
    }
    if (bussinessname == "") {
      setBussinessnameerr(t("validationMessage.businessNameEmpty"));
      ValidateObj.bussinesname = false;
    } else {
      setBussinessnameerr("");
      ValidateObj.bussinesname = true;
    }
    if (city == "") {
      setCityErr(t("validationMessage.cityEmpty"));
      ValidateObj.city = false;
    } else {
      setCityErr("");
      ValidateObj.city = true;
    }
    if (country == "") {
      setCountryErr(t("validationMessage.countryEmpty"));
      ValidateObj.country = false;
    } else {
      setCountryErr("");
      ValidateObj.country = true;
    }
    if (state == "") {
      setStateErr(t("validationMessage.stateEmpty"));
      ValidateObj.state = false;
    } else {
      setStateErr("");
      ValidateObj.state = true;
    }
    if (pincode == "") {
      setPincodeErr(t("validationMessage.pincodeEmpty"));
      ValidateObj.pincode = false;
    } else if (pincode.length !== 6) {
      setPincodeErr(t("validationMessage.pincodeLength"));
      ValidateObj.pincode = false;
    } else {
      setPincodeErr("");
      ValidateObj.pincode = true;
    }

    if (
      ValidateObj.name == true &&
      ValidateObj.email == true &&
      ValidateObj.number == true &&
      ValidateObj.bussinesname == true &&
      ValidateObj.country == true &&
      ValidateObj.state == true &&
      ValidateObj.city == true &&
      ValidateObj.pincode == true
    ) {
      return true;
    } else {
      return false;
    }
  };

  const handleinduscountry = (e) => {
    setCountry(e);
    setInduCountry(true);
  };

  const colourStylesError = {
    control: (styles, state) => ({
      ...styles,
      cursor: "pointer",
      borderRadius: "0.25rem",
      height: "2.75rem",
      boxShadow: state.isFocused ? 0 : 0,
    }),
    option: (base, state) => ({
      ...base,
      cursor: "pointer",
      backgroundColor: state.isSelected ? "#ffffff" : "#ffffff",

      "&:hover": { backgroundColor: "#F9F9FC" },

      fontSize: ".8125rem",
      padding: "8px",
      color: "#333333",
    }),
  };

  useEffect(() => {
    if (country != "" && statedependent != undefined) {
      const staoptionsget =
        statedependent &&
        statedependent?.filter(
          (option) => option?.country?.countryId === country?.value
        );
      const filterstate =
        staoptionsget &&
        staoptionsget?.map((data) => ({
          label: data.name,
          value: data.zoneId,
        }));
      setStateOption(filterstate);
      const found = filterstate?.find((obj) => obj?.label === state?.label);
      if (found == undefined) {
        setState("");
      }
    }
  }, [country, state]);
  const handleindus = (e) => {
    setState(e);
    setIndu(true);
  };
  useEffect(() => {
    CountryValue(setCountryOption);
    StateValue(setStateDependent);
  }, []);
  const handlePhoneno = (e) => {
    if (/^[+\d\s]+$/.test(e) || e == "") {
      if (e.length <= 15) {
        setPhonenumber(e);
      }
    }
  };

  const handlepincode = (e) => {
    if (e.length <= 6) {
      setPincode(e);
    }
  };

  const handleNameChange = (value) => {
    if (value.length <= 32) {
      if (NameValidator(value)) {
        setFirstname(value);
      }
    }
  };

  const handleSendQuotation = (e) => {
    if (e.key === "Enter") {
      e.preventDefault();
      hanldequotepost();
    }
  };

  return (
    qoutedetails &&
    qoutedetails?.length >= 1 && (
      <div className="w-full bg-white md:w-1/4">
        <div className="p-4 border-b border-lightgrey-100">
          <h3 className="text-base font-black text-black">
            {t("myprofile.ContactInfo")}
          </h3>
        </div>
        <div className="flex flex-col p-6 space-y-6">
          <div className="relative w-full" data-twe-input-wrapper-init>
            <input
              type="text"
              value={firstname}
              onChange={(e) => handleNameChange(e.target.value.trimStart())}
              onKeyDown={(e) => handleSendQuotation(e)}
              className={`peer block h-11 w-full rounded border border-grey-400 bg-transparent px-3 py-[0.32rem] leading-[1.6] outline-none transition-all duration-200 ease-linear focus:placeholder:opacity-100 text-black peer-focus:text-primary data-[twe-input-state-active]:placeholder:opacity-100 motion-reduce:transition-none dark:text-white dark:placeholder:text-grey-400 dark:autofill:shadow-autofill dark:peer-focus:text-primary [&:not([data-twe-input-placeholder-active])]:placeholder:opacity-0 focus:border-grey-400 focus:shadow-none focus:ring-transparent focus:border-grey-400  ${
                createcheck == true && firstnameerr !== "" && "border-valid"
              }`}
              id="exampleFormControlInput1"
              placeholder={t("signin.Examplelabel")}
            />
            <label
              htmlFor="exampleFormControlInput1"
              className={
                firstname == ""
                  ? `pointer-events-none absolute left-3 top-2  mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[1.01rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.8rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-white ${
                      createcheck == true && "text-red-invalid"
                    }`
                  : `translate-y-[-0.8rem] scale-[0.8] pointer-events-none absolute left-3 top-1 mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[0.8rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.9rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-white text-red-valid ${
                      createcheck == true &&
                      firstnameerr != "" &&
                      "text-red-invalid"
                    }`
              }
            >
              {t("signin.FullName")}*
            </label>
            {firstnameerr && firstname == "" && (
              <p className="mt-1 text-xs font-normal text-red-invalid">
                {firstnameerr}
              </p>
            )}
          </div>
          <div className="relative w-full" data-twe-input-wrapper-init>
            <input
              type="text"
              value={email}
              onChange={(e) => setEmail(e.target.value.trimStart())}
              onKeyDown={(e) => handleSendQuotation(e)}
              className={`peer block h-11 w-full rounded border border-grey-400 bg-transparent px-3 py-[0.32rem] leading-[1.6] outline-none transition-all duration-200 ease-linear focus:placeholder:opacity-100 text-black peer-focus:text-primary data-[twe-input-state-active]:placeholder:opacity-100 motion-reduce:transition-none dark:text-white dark:placeholder:text-grey-400 dark:autofill:shadow-autofill dark:peer-focus:text-primary [&:not([data-twe-input-placeholder-active])]:placeholder:opacity-0 focus:border-grey-400 focus:shadow-none focus:ring-transparent focus:border-grey-400  ${
                createcheck == true && emailerr !== "" && "border-valid"
              }`}
              id="exampleFormControlInput2"
              placeholder={t("signin.Examplelabel")}
            />
            <label
              htmlFor="exampleFormControlInput2"
              className={
                email == ""
                  ? `pointer-events-none absolute left-3 top-2  mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[1.01rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.8rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-white ${
                      createcheck == true && "text-red-invalid"
                    }`
                  : `translate-y-[-0.8rem] scale-[0.8] pointer-events-none absolute left-3 top-1 mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[0.8rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.9rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-white ${
                      createcheck == true &&
                      emailerr !== "" &&
                      "text-red-invalid"
                    }`
              }
            >
              {t("signin.EmailId")}*
            </label>
            {emailerr && (
              <p className="mt-1 text-xs font-normal text-red-invalid">
                {emailerr}
              </p>
            )}{" "}
          </div>
          <div className="relative w-full" data-twe-input-wrapper-init>
            <input
              type="text"
              value={phonenumber}
              onKeyDown={(e) =>
                exceptThisSymbols.includes(e.key) && e.preventDefault()
              }
              onKeyUp={(e) => handleSendQuotation(e)}
              onChange={(e) => handlePhoneno(e.target.value.trimStart())}
              className={`peer block h-11 w-full rounded border border-grey-400 bg-transparent px-3 py-[0.32rem] leading-[1.6] outline-none transition-all duration-200 ease-linear focus:placeholder:opacity-100 text-black peer-focus:text-primary data-[twe-input-state-active]:placeholder:opacity-100 motion-reduce:transition-none dark:text-white dark:placeholder:text-grey-400 dark:autofill:shadow-autofill dark:peer-focus:text-primary [&:not([data-twe-input-placeholder-active])]:placeholder:opacity-0 focus:border-grey-400 focus:shadow-none focus:ring-transparent focus:border-grey-400  ${
                createcheck == true && phonenumbererr !== "" && "border-valid"
              }`}
              id="exampleFormControlInput3"
              placeholder={t("signin.Examplelabel")}
            />
            <label
              htmlFor="exampleFormControlInput3"
              className={
                phonenumber == ""
                  ? `pointer-events-none absolute left-3 top-2  mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[1.01rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.8rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-white ${
                      createcheck == true && "text-red-invalid"
                    }`
                  : `translate-y-[-0.8rem] scale-[0.8] pointer-events-none absolute left-3 top-1 mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[0.8rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.9rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-white text-red-valid ${
                      createcheck == true &&
                      phonenumbererr != "" &&
                      "text-red-invalid"
                    }`
              }
            >
              {t("myprofile.Phone")}*
            </label>
            {createcheck && phonenumbererr != "" && (
              <p className="mt-1 text-xs font-normal text-red-invalid">
                {phonenumbererr}
              </p>
            )}
          </div>
          <div className="relative w-full" data-twe-input-wrapper-init>
            <input
              type="text"
              value={bussinessname}
              onChange={(e) => setBussinessname(e.target.value.trimStart())}
              onKeyDown={(e) => handleSendQuotation(e)}
              className={`peer block h-11 w-full rounded border border-grey-400 bg-transparent px-3 py-[0.32rem] leading-[1.6] outline-none transition-all duration-200 ease-linear focus:placeholder:opacity-100 text-black peer-focus:text-primary data-[twe-input-state-active]:placeholder:opacity-100 motion-reduce:transition-none dark:text-white dark:placeholder:text-grey-400 dark:autofill:shadow-autofill dark:peer-focus:text-primary [&:not([data-twe-input-placeholder-active])]:placeholder:opacity-0 focus:border-grey-400 focus:shadow-none focus:ring-transparent focus:border-grey-400  ${
                createcheck == true && bussinessnameerr !== "" && "border-valid"
              }`}
              id="exampleFormControlInput4"
              placeholder={t("signin.Examplelabel")}
            />
            <label
              htmlFor="exampleFormControlInput4"
              className={
                bussinessname == ""
                  ? `pointer-events-none absolute left-3 top-2  mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[1.01rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.8rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-white ${
                      createcheck == true && "text-red-invalid"
                    }`
                  : `translate-y-[-0.8rem] scale-[0.8] pointer-events-none absolute left-3 top-1 mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[0.8rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.9rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-white text-red-valid ${
                      createcheck == true &&
                      bussinessnameerr != "" &&
                      "text-red-invalid"
                    }`
              }
            >
              {t("myprofile.BizName")}*
            </label>
            {bussinessnameerr && bussinessname == "" && (
              <p className="mt-1 text-xs font-normal text-red-invalid">
                {bussinessnameerr}
              </p>
            )}
          </div>
          <div
            className={
              inducountry == true
                ? `relative w-full mb-0 selected profile-select cursor-pointer ${
                    createcheck && country == "" && "invalid"
                  }`
                : `relative w-full mb-0 profile-select select cursor-pointer ${
                    createcheck && country == "" && "invalid"
                  }`
            }
          >
            <Select
              name="select"
              value={country}
              options={countryOption}
              onChange={(e) => handleinduscountry(e)}
              styles={colourStylesError}
              className={` block w-full px-0 mt-0 bg-transparent border-0  appearance-none z-1 focus:outline-none focus:ring-0 focus:border-black border-gray-200 cursor-pointer${
                createcheck == true && countryErr !== "" && "border-valid"
              }`}
            />
            <label
              htmlFor="exampleFormControlInput5"
              className={
                country == ""
                  ? `pointer-events-none absolute left-3 top-2  mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[1.01rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.8rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-blue-100 ${
                      createcheck && "text-red-invalid"
                    }`
                  : `translate-y-[-0.8rem] scale-[0.8] pointer-events-none absolute left-3 top-1 mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[0.8rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.9rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-blue-100 ${
                      createcheck && countryErr !== "" && "text-red-invalid"
                    }`
              }
            >
              {t("myprofile.Country")}*
            </label>
            {createcheck && countryErr !== "" && (
              <p className="mt-1 text-xs font-normal text-red-invalid">
                {countryErr}
              </p>
            )}
          </div>
          <div
            className={
              indu == true
                ? `relative w-full mb-0 selected profile-select cursor-pointer  ${
                    createcheck && state == "" && "invalid"
                  }`
                : `relative w-full mb-0 profile-select select cursor-pointer ${
                    createcheck && state == "" && "invalid"
                  }`
            }
          >
            <Select
              name="select"
              value={state}
              options={stateOption}
              onChange={(e) => handleindus(e)}
              styles={colourStylesError}
              className={` block w-full px-0 mt-0 bg-transparent border-0  appearance-none z-1 focus:outline-none focus:ring-0 focus:border-black border-gray-200 cursor-pointer${
                createcheck == true && stateErr !== "" && "border-valid"
              }`}
            />
            <label
              htmlFor="exampleFormControlInput6"
              className={
                state == ""
                  ? `pointer-events-none absolute left-3 top-2  mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[1.01rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.8rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-blue-100 ${
                      createcheck && "text-red-invalid"
                    }`
                  : `translate-y-[-0.8rem] scale-[0.8] pointer-events-none absolute left-3 top-1 mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[0.8rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.9rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-blue-100 ${
                      createcheck && stateErr !== "" && "text-red-invalid"
                    }`
              }
            >
              {t("myprofile.State")}*
            </label>
            {createcheck && stateErr !== "" && (
              <p className="mt-1 text-xs font-normal text-red-invalid">
                {stateErr}
              </p>
            )}
          </div>
          <div className="relative w-full" data-twe-input-wrapper-init>
            <input
              type="text"
              value={city}
              maxLength={32}
              onChange={(e) => setCity(e.target.value.trimStart())}
              onKeyDown={(e) => handleSendQuotation(e)}
              className={`peer block h-11 w-full rounded border border-grey-400 bg-transparent px-3 py-[0.32rem] leading-[1.6] outline-none transition-all duration-200 ease-linear focus:placeholder:opacity-100 text-black peer-focus:text-primary data-[twe-input-state-active]:placeholder:opacity-100 motion-reduce:transition-none dark:text-white dark:placeholder:text-grey-400 dark:autofill:shadow-autofill dark:peer-focus:text-primary [&:not([data-twe-input-placeholder-active])]:placeholder:opacity-0 focus:border-grey-400 focus:shadow-none focus:ring-transparent focus:border-grey-400  ${
                createcheck == true && cityErr !== "" && "border-valid"
              }`}
              id="exampleFormControlInput7"
              placeholder={t("signin.Examplelabel")}
            />
            <label
              htmlFor="exampleFormControlInput7"
              className={
                city == ""
                  ? `pointer-events-none absolute left-3 top-2  mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[1.01rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.8rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-white ${
                      createcheck == true && "text-red-invalid"
                    }`
                  : `translate-y-[-0.8rem] scale-[0.8] pointer-events-none absolute left-3 top-1 mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[0.8rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.9rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-white text-red-valid ${
                      createcheck == true && cityErr != "" && "text-red-invalid"
                    }`
              }
            >
              {t("myprofile.City")}*
            </label>
            {cityErr && city == "" && (
              <p className="mt-1 text-xs font-normal text-red-invalid">
                {cityErr}
              </p>
            )}
          </div>
          <div className="relative w-full" data-twe-input-wrapper-init>
            <input
              type="number"
              onWheel={(e) => e.target.blur()}
              value={pincode}
              onKeyDown={(e) =>
                exceptThisSymbols.includes(e.key) && e.preventDefault()
              }
              onChange={(e) => handlepincode(e.target.value.trimStart())}
              onKeyUp={(e) => handleSendQuotation(e)}
              className={`peer block h-11 w-full rounded border border-grey-400 bg-transparent px-3 py-[0.32rem] leading-[1.6] outline-none transition-all duration-200 ease-linear focus:placeholder:opacity-100 text-black peer-focus:text-primary data-[twe-input-state-active]:placeholder:opacity-100 motion-reduce:transition-none dark:text-white dark:placeholder:text-grey-400 dark:autofill:shadow-autofill dark:peer-focus:text-primary [&:not([data-twe-input-placeholder-active])]:placeholder:opacity-0 focus:border-grey-400 focus:shadow-none focus:ring-transparent focus:border-grey-400  ${
                createcheck == true && pincodeErr !== "" && "border-valid"
              }`}
              id="exampleFormControlInput8"
              placeholder={t("signin.Examplelabel")}
            />
            <label
              htmlFor="exampleFormControlInput8"
              className={
                pincode == ""
                  ? `pointer-events-none absolute left-3 top-2  mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[1.01rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.8rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-white ${
                      createcheck == true && "text-red-invalid"
                    }`
                  : `translate-y-[-0.8rem] scale-[0.8] pointer-events-none absolute left-3 top-1 mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[0.8rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.9rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-white text-red-valid ${
                      createcheck == true &&
                      pincodeErr != "" &&
                      "text-red-invalid"
                    }`
              }
            >
              {t("myprofile.Pincode")}*
            </label>
            {createcheck && pincodeErr !== "" && (
              <p className="mt-1 text-xs font-normal text-red-invalid">
                {pincodeErr}
              </p>
            )}
          </div>
          <button
            className="w-full h-12 text-sm font-black text-white rounded bg-boldblack hover:bg-zinc-800 btn"
            onClick={hanldequotepost}
          >
            {t("myprofile.SendQuote")}
          </button>
        </div>
      </div>
    )
  );
}
