"use client";
//React and Next
import { useState } from "react";
import Link from "next/link";
//Utils
import { imageUrl } from "@/utilities/url";
//Component
import CustomCurrencyFormatter from "@/Components/CustomCurrencyFormatter";
import { useTranslation } from "react-i18next";

export default function PostQuestion({
  postQuestion,
  setPostQuestion,
  productDetail,
  handleAddQuestionChange,
  handleClickAddQuestion,
  question,
  questionError,
}) {
  const [loader, setLoader] = useState(false);
  const { t } = useTranslation();

  const image = productDetail?.productOriginalImage?.find(
    (img) => img?.defaultImage == 1
  );
  return (
    <div
      aria-modal="true"
      id="static-modal"
      data-modal-backdrop="static"
      tabIndex="-1"
      aria-hidden="true"
      className={`${
        postQuestion
          ? "overflow-y-auto overflow-x-hidden fixed top-0 right-0 left-0 z-100 justify-center bg-light-modal items-center w-full md:inset-0 h-full max-h-full px-4 flex "
          : " hidden"
      }`}
    >
      <div className="relative w-full max-h-full max-w-800">
        {/* <!-- Modal content --> */}
        <div className="relative bg-white shadow dark:bg-gray-700">
          {/* <!-- Modal header --> */}
          <div className="top-6 right-6 rtl:right-[unset] rtl:left-6 absolute">
            <button
              type="button"
              data-modal-hide="static-modal"
              onClick={() => setPostQuestion(false)}
            >
              <img src="/img/modal-x.svg" alt="modal-x" />
            </button>
          </div>

          {/* <!-- Modal body --> */}
          <div className="p-4 padding-8">
            <h3 className="mb-1.5 font-bold text-black-300 text-xl">
              {t("productcontent.PstQ")}
            </h3>
            {/* <p className="text-sm font-normal leading-relaxed text-black-300">
                                {t("productcontent.PstQCont")}
                            </p> */}
            <div className="flex space-x-4 rtl:space-x-[unset] bg-lightgrey my-6 p-4">
              {productDetail !== undefined && (
                <div className="rtl:ml-4 rounded-md w-[120px] h-[116px]">
                  <img
                    // src="/img/list-detail-product1.png"
                    src={
                      imageUrl +
                      "?path=" +
                      image?.containerName +
                      "&name=" +
                      image?.image +
                      "&width=200&height=200"
                    }
                    alt={productDetail?.name}
                    className="object-contain w-full h-full max-w-full m-auto rounded-md"
                  />
                </div>
              )}
              <div className="flex flex-col justify-between">
                {productDetail !== undefined && (
                  <div className="flex flex-col space-y-2.5">
                    <h3 className="text-base font-semibold text-boldblack">
                      {productDetail?.productNameTrans
                        ? productDetail?.productNameTrans
                        : productDetail?.name}
                    </h3>
                    <p className="mb-1 text-lg font-black text-boldblack">
                      <CustomCurrencyFormatter price={productDetail?.price} />
                    </p>
                  </div>
                )}
                <div className="flex items-center space-x-1.5"></div>
              </div>
            </div>
            <div className="mb-3">
              <textarea
                value={question}
                onChange={handleAddQuestionChange}
                className={
                  questionError == ""
                    ? "w-full  px-4 py-3.5 text-black-300 h-148 placeholder: text-gray-500 text-sm font-normal  rounded-md focus:ring-0 border border-gray-25 "
                    : "w-full  px-4 py-3.5 text-black-300  h-148 placeholder: text-gray-500 text-sm font-normal  rounded-md focus:border-red border border-red"
                }
                placeholder={t("myprofile.MentDetails")}
              ></textarea>
            </div>
            <div className="mb-0 margin-10">
              <p className="text-sm font-medium text-bold-black">
                {t("productcontent.submitquestion")}
              </p>
              <Link
                prefetch={false}
                href="/terms-conditions"
                className="text-sm font-medium text-link"
              >
                {t("signin.TandC")}
              </Link>
            </div>
          </div>
          {/* <!-- Modal footer --> */}
          <div className="flex items-center justify-end p-4 bg-gray-slate-100 md:px-8 md:py-4 dark:border-gray-600">
            <button
              onClick={() => handleClickAddQuestion(setLoader)}
              className="bg-black hover:bg-zinc-800 px-8 rounded h-[49px] font-semibold text-white text-sm uppercase"
            >
              {loader == true ? (
                <svg
                  aria-hidden="true"
                  role="status"
                  className="inline w-24 h-5 text-white me-3 animate-spin"
                  viewBox="0 0 100 101"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M100 50.5908C100 78.2051 77.6142 100.591 50 100.591C22.3858 100.591 0 78.2051 0 50.5908C0 22.9766 22.3858 0.59082 50 0.59082C77.6142 0.59082 100 22.9766 100 50.5908ZM9.08144 50.5908C9.08144 73.1895 27.4013 91.5094 50 91.5094C72.5987 91.5094 90.9186 73.1895 90.9186 50.5908C90.9186 27.9921 72.5987 9.67226 50 9.67226C27.4013 9.67226 9.08144 27.9921 9.08144 50.5908Z"
                    fill="#E5E7EB"
                  />
                  <path
                    d="M93.9676 39.0409C96.393 38.4038 97.8624 35.9116 97.0079 33.5539C95.2932 28.8227 92.871 24.3692 89.8167 20.348C85.8452 15.1192 80.8826 10.7238 75.2124 7.41289C69.5422 4.10194 63.2754 1.94025 56.7698 1.05124C51.7666 0.367541 46.6976 0.446843 41.7345 1.27873C39.2613 1.69328 37.813 4.19778 38.4501 6.62326C39.0873 9.04874 41.5694 10.4717 44.0505 10.1071C47.8511 9.54855 51.7191 9.52689 55.5402 10.0491C60.8642 10.7766 65.9928 12.5457 70.6331 15.2552C75.2735 17.9648 79.3347 21.5619 82.5849 25.841C84.9175 28.9121 86.7997 32.2913 88.1811 35.8758C89.083 38.2158 91.5421 39.6781 93.9676 39.0409Z"
                    fill="currentColor"
                  />
                </svg>
              ) : (
                t("productcontent.PostQstn")
              )}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
