"use client";
//React and Next
import { Fragment, useEffect, useState } from "react";
import Link from "next/link";
import { useRouter } from "nextjs-toploader/app";
import { usePathname } from "next/navigation";
//Redux
import { useDispatch, useSelector } from "react-redux";
import { breadcrumb, subproduct } from "@/redux/slices/userSlice";
//3rd Party
import StarRatings from "react-star-ratings";
//API
import {
  GetCompareId,
  RelatedProductListApi,
  productCompareApi,
  widgetDeleteApi,
  widgetPostApi,
} from "./RelatedProductApi";
//Utils
import { PriceCalculate, PriceTaxCalculate } from "@/utilities/PriceCalculate";
import { GetAddonList } from "@/utilities/localStorage";
import { imageUrl } from "@/utilities/url";
//Components
import ToastMessage from "@/Components/ToastMessage";
import CustomCurrencyFormatter from "@/Components/CustomCurrencyFormatter";
import RelatedProductSkeleton from "./RelatedProductSkeleton";
import useCarousel from "@/utilities/useCarousel";
import { useTranslation } from "react-i18next";

export default function RelatedProduct({ token }) {
  const router = useRouter();
  const pathname = usePathname();
  const slug = pathname.split("/").at(-1);
  const dispatch = useDispatch();
  const productIds = GetCompareId();
  const { t } = useTranslation();
  const [trigger, setTrigger] = useState(0);
  const [relatedProductList, setRelatedProductList] = useState([]);
  const [comparetrigger, setCompareTrigger] = useState(0);
  const [loader, setLoader] = useState(true);

  const wishCount = useSelector((state) => state.userReducer.wishCount);

  // Carousel Design
  const { sliderRef, canScrollLeft, canScrollRight, scrollLeft, scrollRight } =
    useCarousel();

  let compareProductIds =
    typeof window !== "undefined"
      ? JSON.parse(localStorage.getItem("compareId") || "[]")
      : [];
  const addonList = GetAddonList();

  useEffect(() => {
    if (slug) {
      RelatedProductListApi(slug, setRelatedProductList, setLoader);
    }
  }, [slug]);

  // for dispatch value
  const handleDetails = (id) => {
    dispatch(breadcrumb(true));
    dispatch(subproduct(false));
  };
  const handleWishiList = (data) => {
    if (token) {
      relatedProductList?.map((relatedproduct) => {
        if (data.productId == relatedproduct.productId) {
          relatedproduct.wishListStatus =
            relatedproduct.wishListStatus == 0 ? 1 : 0;
        }
      });
      if (data?.wishListStatus == 0) {
        widgetDeleteApi(data.productId, dispatch, wishCount);
      } else {
        widgetPostApi(data.productId, dispatch, wishCount);
      }
    } else {
      router.push("/buyer-login");
    }
    setTrigger(trigger + 1);
  };
  const handleAddItemToCompare = (e, productId) => {
    let data = 0;
    if (productIds !== null && productIds !== undefined) {
      var productIdIndex = productIds?.indexOf(productId);
      if (productIdIndex !== -1) {
        let localCompareId = GetCompareId();
        localCompareId.splice(productIdIndex, 1);
        localStorage.setItem("compareId", JSON.stringify(localCompareId));
        setCompareTrigger(comparetrigger + 1);
        ToastMessage({
          type: "success",
          message: t("toastMessage.compareProductRemove"),
        });
      } else {
        const compareIds = [...productIds, productId];
        if (compareIds && compareIds.length > 2) {
          ToastMessage({
            type: "error",
            message: t("toastMessage.compareProductLimit"),
          });
        } else {
          productCompareApi(
            data,
            compareIds,
            setCompareTrigger,
            comparetrigger,
            productIds,
          );
        }
      }
    } else {
      let productIds = [];
      productIds.push(productId);
      productCompareApi(data, productIds, setCompareTrigger, comparetrigger);
    }
  };

  function compareCheckFunction(productId) {
    let productIds =
      typeof window !== "undefined"
        ? JSON.parse(localStorage.getItem("compareId") || "[]")
        : [];
    if (productIds && productIds.length !== 0) {
      let compareCheck = productIds.some((value) => value === productId);
      return compareCheck;
    }
  }

  return (
    <div className="mb-14 relative">
      <h3 className="text-xl font-black text-black sm:text-2xl">
        {t("productdetails.RelProd")}
      </h3>
      {loader ? (
        <RelatedProductSkeleton />
      ) : relatedProductList?.length > 0 ? (
        <>
          {/* {(canScrollRight || canScrollLeft) && (
            <> */}
              {/* Scroll Arrows */}
              <a
                type="button"
                onClick={scrollLeft}
                className={`absolute left-[5] top-[40%] z-[2] rounded-full bg-white p-3 shadow-md ${
                  canScrollLeft ? "opacity-100 cursor-pointer" : "opacity-40 cursor-default"
                }`}
              >
                <img src="/img/arroew-prev.svg" alt="arrow" />
              </a>

              <a
                type="button"
                onClick={scrollRight}
                className={`absolute right-[5] top-[40%] z-[2] rounded-full bg-white p-3 shadow-md ${
                  canScrollRight ? "opacity-100 cursor-pointer " : "opacity-40 cursor-default"
                }`}
              >
                <img src="/img/arrow-next.svg" alt="arrow" />
              </a>
            {/* </>
          )} */}

          <div ref={sliderRef} className="flex items-center gap-4 mt-6 overflow-auto scrollbar-visible">
            {relatedProductList?.map((datachild, index) => (
              <Fragment key={index}>
                {/* Compare product route */}

                {compareProductIds && compareProductIds?.length > 0 && (
                  <Link
                    prefetch={false}
                    href={"/product-compare"}
                    className={
                        "inline-flex cursor-pointer justify-center uppercase font-medium items-center rounded-md bg-[var(--color)] text-white text-base gap-x-4 py-4 px-8 fixed bottom-11 right-11 z-[2] w-36 h-10"
                      }
                  >
                    {t("compare.Compare")}
                    <span className="bg-green-slate flex min-w-2 items-center justify-center">
                      {compareProductIds?.length}
                    </span>
                  </Link>
                )}

                {/* List card  */}

                <div
                  className="group group-item block relative hover:shadow-[0px_0px_10px_#00000014] rounded-[5px] overflow-hidden min-w-[238px] w-[238px]"
                  key={index}
                >
                  {/* Wishlist and compare buttons */}
                 <div className="top-2 right-2 rtl:right-[unset] rtl:left-2 z-[2] absolute flex items-center space-x-3">
                    {/* Compare button */}
                    <div
                      onClick={(e) =>
                        handleAddItemToCompare(e, datachild?.productId)
                      }
                      className={
                        compareCheckFunction(datachild?.productId)
                          ? "cursor-pointer rounded-full flex justify-center items-center shadow-md h-10 w-10 text-2xl bg-white tooltip3"
                          : `item cursor-pointer rounded-full flex justify-center items-center shadow-md h-10 w-10 text-2xl bg-white tooltip3`
                      }
                    >
                      {compareCheckFunction(datachild?.productId) ? (
                        <img
                          src="/img/compare-outline-active.svg"
                          alt="compare-outline-active"
                          className="w-5"
                        />
                      ) : (
                        <img
                          src="/img/compare-outline.svg"
                          alt="compare-outline"
                          className="w-5"
                        />
                      )}
                      <span className="tooltip-text shadow-md">
                        {t("tooltip.compare")}
                      </span>
                    </div>
                    {/* Wishlist button */}
                    <div
                      onClick={() => handleWishiList(datachild)}
                      className={
                        datachild?.wishListStatus == 0
                          ? "item cursor-pointer   z-20 rounded-full flex justify-center items-center shadow-md h-10 w-10 text-2xl bg-white tooltip3"
                          : `cursor-pointer  rounded-full flex justify-center items-center shadow-md h-10 w-10 text-2xl bg-white tooltip3`
                      }
                    >
                      {datachild?.wishListStatus == 0 ? (
                        <img
                          src="/img/heart-outline-wisit.svg"
                          alt="heart-outline-wisit"
                          className="w-5"
                        />
                      ) : (
                        <img
                          src="/img/heart-select-red-wisit.svg"
                          alt="heart-select-red-wisit"
                          className="w-5"
                        />
                      )}

                      <span className="tooltip-text shadow-md">
                        {t("Breadcrumb.wishList")}
                      </span>
                    </div>
                  </div>

                  <Link
                    prefetch={false}
                    href={`/products/${datachild?.productSlug}`}
                    onClick={() => handleDetails(datachild?.productId)}
                  >
                    {/* Product Image */}
                    <div className="h-[228px] bg-[#f8f8f8]">
                      <img
                        className="h-full w-full object-contain"
                        src={
                          imageUrl +
                          "?path=" +
                          datachild?.containerName +
                          "&name=" +
                          datachild?.image +
                          "&width=288&height=288"
                        }
                        width={288}
                        height={288}
                        alt={datachild?.name}
                        onError={({ currentTarget }) => {
                          currentTarget.onerror = null;
                          currentTarget.src = "/img/no-image.png";
                          currentTarget.alt = "no-image";
                        }}
                      />
                    </div>

                    {/* Product Title, Price, Rating */}
                    <div className="p-[12px]">
                      <span className="mb-[10px] line-clamp-2 min-h-[40px] text-[14px] font-normal leading-[20px] text-[#8B8BA3] hover:underline">
                        {datachild?.productNameTrans
                          ? datachild?.productNameTrans
                          : datachild?.name}
                      </span>
                      <div className="mb-[10px] flex flex-wrap items-center gap-[8px]">
                        <p className="whitespace-nowrap text-[18px] font-[900] leading-[22px] text-[#333333]">
                          <CustomCurrencyFormatter
                            price={PriceCalculate(
                              datachild?.price,
                              datachild?.pricerefer,
                              datachild?.flag,
                              datachild?.taxType,
                              datachild?.taxValue,
                            )}
                          />
                        </p>
                        {datachild?.flag == 1 && (
                          <>
                            <p className="whitespace-nowrap text-[14px] font-[600] leading-[17px] text-[#8B8BA3] line-through">
                              {t("productdetails.Mrp")}{" "}
                              <CustomCurrencyFormatter
                                price={PriceTaxCalculate(
                                  datachild?.price,
                                  datachild?.taxType,
                                  datachild?.taxValue,
                                )}
                              />
                            </p>
                            <p className="font-[600] text-[12px] text-[var(--color)] leading-[15px] whitespace-nowrap">
                              {Math.round(
                                ((PriceTaxCalculate(
                                  datachild?.price,
                                  datachild?.taxType,
                                  datachild?.taxValue,
                                ) -
                                  PriceTaxCalculate(
                                    datachild?.pricerefer,
                                    datachild?.taxType,
                                    datachild?.taxValue,
                                  )) /
                                  PriceTaxCalculate(
                                    datachild?.price,
                                    datachild?.taxType,
                                    datachild?.taxValue,
                                  )) *
                                  100,
                              )}
                              {t("productdetails.OFF")}
                            </p>
                          </>
                        )}
                      </div>
                      {addonList &&
                        addonList?.["rating-review"] &&
                        addonList?.["rating-review"]?.status == 1 &&
                        datachild?.ratingCount > 0 &&
                        datachild?.reviewCount > 0 && (
                          <div className="flex items-center gap-[5px]">
                            <StarRatings
                              rating={
                                datachild?.ratingCount &&
                                parseFloat(datachild?.ratingCount)
                              }
                              starRatedColor="black"
                              starDimension="13px"
                              starSpacing="3px"
                              numberOfStars={5}
                              name="rating"
                              svgIconPath="M12.489,5.03a.665.665,0,0,0-.572-.457L8.3,4.245,6.873.9A.666.666,0,0,0,5.649.9L4.22,4.245.6,4.573A.666.666,0,0,0,.227,5.738l2.732,2.4-.806,3.548a.665.665,0,0,0,.99.72l3.117-1.864L9.377,12.4a.666.666,0,0,0,.991-.72L9.562,8.134l2.732-2.4A.666.666,0,0,0,12.489,5.03ZM6.321,10.5"
                              svgIconViewBox="0 0 12.522 12"
                            />
                            <p className="mt-1 text-[12px] font-normal leading-[20px] text-[#8B8BA3]">
                              {`(${datachild?.reviewCount} ${
                                datachild?.reviewCount <= 1
                                  ? t("productcontent.review")
                                  : t("productcontent.reviews")
                              })`}
                            </p>
                          </div>
                        )}
                    </div>
                  </Link>
                </div>
              </Fragment>
            ))}
          </div>
        </>
      ) : (
        <p className="text-black-300 mt-5">
          {t("productdetails.NoRelProd")}
        </p>
      )}
    </div>
  );
}
