"use client";
//React and Next
import { useRouter } from "nextjs-toploader/app";
import { useEffect, useState } from "react";
//3rd Party
import moment from "moment";
//API
import {
  Get_SupportAttachment_Download_Api,
  Get_SupportDetail_Api,
  Media_Post_api,
  Support_Close_Ticket_Api,
  Support_ReOpen_Ticket_Api,
  Support_Ticket_Reply_Api,
} from "../SupportApi";
//Utils
import { imageUrl } from "@/utilities/url";
//Component
import Sidebar from "../SideBar";
import Breadcrumb from "@/Components/Breadcrumbs";
import SupportDetail_ImageAttachment_Modal from "./SupportDetail_ImageAttachment_Modal";
import ToastMessage from "@/Components/ToastMessage";
import { Support_Message_Skeleton } from "../Skeleton/Support_Message_Skeleton";
import { Page_loader } from "../Loader/PageLoader";
import { Button_loader } from "../Loader/ButtonLoader";
import { useTranslation } from "react-i18next";


const SupportDetail = ({ supportId }) => {
  const router = useRouter();
  const [Support_Detail_Result, setSupport_Detail_Result] = useState([]);
  const [isOpen, setIsOpen] = useState(false);
  const [selected_Attachment, setselected_Attachment] = useState("");
  const [ticket_Response_value, setticket_Response_value] = useState("");
  const [ticket_Response_error, setticket_Response_error] = useState("");
  const [submit, setsubmit] = useState(0);
  const [selectedFile, setSelectedFile] = useState(null);
  const [media_Api_result, setmedia_Api_result] = useState([]);
  const [loader, setloader] = useState(false);
  const [media_Loader, setmedia_Loader] = useState(false);
  const [closeTicket_Loader, setcloseTicket_Loader] = useState(false);
  const [Reply_Loader, setReply_Loader] = useState(false);
   const { t } = useTranslation();

  useEffect(() => {
    Function_To_Call_Support_Detail_APi();
  }, []);

  const Function_To_Call_Support_Detail_APi = () => {
    setloader(true);
    Get_SupportDetail_Api(supportId, setSupport_Detail_Result, setloader);
  };
  const handleclick_cancel = (e) => {
    router.push(`/account-settings/support`);
  };

  const handleclick_Attachments = (e, val) => {
    const extension = val.fileName.split(".").pop().toLowerCase();

    if (extension === "pdf") {
      let data = val?.filePath + val?.fileName;
      Get_SupportAttachment_Download_Api(data);
    } else if (["jpg", "jpeg", "png", "gif", "webp"].includes(extension)) {
      setIsOpen(true);
      setselected_Attachment(val);
    } else {
      console.log("Unknown file type");
    }
  };

  useEffect(() => {
    if (submit == 1) {
      validation();
    }
  }, [ticket_Response_value]);

  const validation = () => {
    let validateObj = {
      ticket_Response_value: true,
    };

    if (!["", null, undefined].includes(ticket_Response_value)) {
      setticket_Response_error("");
      validateObj.ticket_Response_value = true;
    } else {
      setticket_Response_error(t("support.enterResponse"));
      validateObj.ticket_Response_value = false;
    }

    if (validateObj.ticket_Response_value == true) {
      return true;
    } else {
      return false;
    }
  };

  const handlechange_ticketResponse = (e) => {
    let value = e.target.value.trimStart();

    // Allow: letters, numbers, space, comma, period, question mark, exclamation, and basic symbols
    value = value.replace(/[^a-zA-Z0-9 .,?!@#$%&*()\-_'"]/g, "");

    setticket_Response_value(value);
    setticket_Response_error("");
  };

  const handleclick_Replay = (e) => {
    setsubmit(1);
    if (validation()) {
      Function_To_Call_Reply_Post_Api();
    }
  };

  const Function_To_Call_Reply_Post_Api = () => {
    const attachments_array = media_Api_result.length
      ? media_Api_result.map(({ file, path }) => ({
          fileName: file,
          filePath: path,
        }))
      : [];

    let obj = {
      message: ticket_Response_value,
      attachments: attachments_array,
      ticketId: supportId,
    };
    setReply_Loader(true);
    Support_Ticket_Reply_Api(
      obj,
      Function_To_Call_Support_Detail_APi,
      setReply_Loader,
      clear_function
    );
  };

  const clear_function = () => {
    setmedia_Api_result([]);
    setticket_Response_value("");
    setsubmit(0);
    setticket_Response_error("");
  };
  const handleclick_close_ticket = (e) => {
    let obj = {
      status: "3",
      ticketId: supportId,
    };
    setcloseTicket_Loader(true);
    Support_Close_Ticket_Api(obj, router, supportId, setcloseTicket_Loader);
  };

  const handleclick_Reopen = (e) => {
    if (validation()) {
      let obj = {
        status: "1",
        ticketId: supportId,
      };
      setReply_Loader(true);
      Support_ReOpen_Ticket_Api(
        obj,
        router,
        supportId,
        setcloseTicket_Loader,
        Function_To_Call_Reply_Post_Api
      );
    }
  };

  const handleFile = (file) => {
    const allowedTypes = [
      "application/pdf",
      "image/jpeg",
      "image/png",
      "image/jpg",
    ];
    const maxSize = 5 * 1024 * 1024; // 5MB

    if (!allowedTypes.includes(file.type)) {
      ToastMessage({
        type: "error",
        message: "Unsupported file format. Please upload PDF or IMAGE.",
      });
      return;
    }

    if (file.size > maxSize) {
      ToastMessage({ type: "error", message: "File size exceeds 5MB." });
      return;
    }

    setSelectedFile(file);

    const reader = new FileReader();
    reader.onloadend = () => {
      const base64Data = reader.result;
      const isPDF = file.type === "application/pdf";

      const obj = {
        documentId: 3,
        fileName: file.name,
        fileType: isPDF ? 1 : 0,
        image: base64Data,
        path: isPDF ? "documents/" : "ticket/",
      };
      setmedia_Loader(true);
      Media_Post_api(obj, setmedia_Api_result, setmedia_Loader);
    };

    reader.readAsDataURL(file);
  };

  const handleclick_delete_file = (e, index) => {
    e.preventDefault();
    setmedia_Api_result((prev) => prev.filter((_, i) => i !== index));
  };

  const breadCrumb = [
    {
      text: t("Breadcrumb.home"),
      url: "/",
    },
    {
      text: t("Breadcrumb.accountSettings"),
      url: "/account-settings",
    },
    {
      text: t("Breadcrumb.Support"),
      url: "/account-settings/support",
    },
    {
      text: `${Support_Detail_Result?.refId} - ${Support_Detail_Result?.subject}`,
      url: "javascript:void(0)",
    },
  ];

  return (
    <>
      <div className="mx-auto px-4 py-2.5 max-w-screen-2xl">
        <Breadcrumb breadCrumb={breadCrumb} />

        <main className="flex rtl:space-x-[unset] md:space-x-8 mx-auto mt-4 mb-7">
          <Sidebar />
          <div className="w-full p-0 md:p-0 md:pe-8">
            <div className="flex max-lg:flex-wrap justify-between items-center gap-4 mb-[23px]">
              <div className="flex items-center gap-[8px]">
                {loader ? (
                  <>
                    <div className="bg-gray-200 mb-2 rounded w-[300px] h-[28px] animate-pulse"></div>
                    <span className="inline-block bg-gray-200 rounded w-[60px] h-[20px] animate-pulse"></span>
                  </>
                ) : (
                  <>
                    <h3 className="font-medium text-[#333333] text-lg">
                      {Support_Detail_Result?.refId +
                        " - " +
                        Support_Detail_Result?.subject}
                    </h3>

                    {Support_Detail_Result.status == "open" ? (
                      <span className="inline-block bg-[#DFF3F3] p-[3px_5px] rounded font-medium text-[#164766] text-sm leading-4">
                        {t("support.Open")}
                      </span>
                    ) : Support_Detail_Result.status == "resolved" ? (
                      <span className="inline-block bg-[#DFF3E7] p-[3px_5px] rounded font-medium text-[#166624] text-sm leading-4">
                        {t("support.Resolved")}
                      </span>
                    ) : Support_Detail_Result.status == "pending" ? (
                      <span className="inline-block bg-[#F3F2DF] p-[3px_5px] rounded font-medium text-[#8C5C0F] text-sm leading-4">
                        {t("support.Pending")}
                      </span>
                    ) : (
                      <span className="inline-block bg-[#DFF3F3] p-[3px_5px] rounded font-medium text-[#164766] text-sm leading-4">
                        {Support_Detail_Result.status}
                      </span>
                    )}
                  </>
                )}
              </div>
              <div className="flex items-center space-x-[10px] rtl:space-x-[unset]">
                <button
                  className="flex justify-center items-center bg-[#E8E8E8] hover:bg-[#f5f5f5] px-[20px] border border-[#E8E8E8] rounded-lg h-10 font-medium text-[#191919] text-sm transition-all duration-300 btn"
                  onClick={(e) => handleclick_cancel(e)}
                >
                  {t("support.Cancel")}
                </button>
                {Support_Detail_Result.status == "resolved" ? (
                  <></>
                ) : closeTicket_Loader ? (
                  <Button_loader
                    class_Name={`flex justify-center items-center  px-[20px] border bg-[#191919] border-black hover:bg-[#3a3939] transition-all duration-300 rounded-lg h-10 font-medium text-sm text-white btn`}
                    width_val={"140px"}
                  />
                ) : (
                  <button
                    className="flex justify-center items-center bg-[#191919] hover:bg-[#3a3939] px-[20px] border border-black rounded-lg h-10 font-medium text-white text-sm transition-all duration-300 btn"
                    onClick={(e) => handleclick_close_ticket(e)}
                    style={{ width: "140px" }}
                  >
                    {t("support.Close")}
                  </button>
                )}
              </div>
            </div>
            <div className="bg-[#F5F6F8] p-[20px_25px] rounded-[8px]">
              <p className="font-normal text-[#191919] text-sm leading-[17px]">
                {t('accountsettings.SupportContent')}
              </p>
            </div>

            <div className="flex items-start lg:items-center flex-col lg:flex-row gap-3 lg:gap-0 bg-[#F5F6F8] mt-[20px] p-[12px] rounded-[8px]">
              <div className="flex justify-start items-start gap-[5px] lg:before:bg-[#CFCFE8] lg:before:mx-[24px] first-of-type:lg:before:mx-0 lg:before:w-[1px] first-of-type:lg:before:w-0 lg:before:h-[17px]">
                <h3 className="font-semibold text-[#262729] text-[14px] leading-[20px]">
                  {t("support.Category")}:
                </h3>{" "}
                <p className="font-normal text-[#262729] text-[14px] leading-[20px]">
                  {Support_Detail_Result?.categoryName}
                </p>{" "}
              </div>
              <div className="flex justify-start items-start gap-[5px] lg:before:bg-[#CFCFE8] lg:before:mx-[24px] first-of-type:lg:before:mx-0 lg:before:w-[1px] first-of-type:lg:before:w-0 lg:before:h-[17px]">
                <h3 className="font-semibold text-[#262729] text-[14px] leading-[20px] whitespace-nowrap">
                  {t("support.SubCategory")}:
                </h3>{" "}
                <p className="font-normal text-[#262729] text-[14px] leading-[20px]">
                  {Support_Detail_Result?.subCategoryName}
                </p>{" "}
              </div>
            </div>

            <div className="mt-[30px]">
              <div className="gap-[20px] grid grid-cols-[1fr_1fr] max-md:grid-cols-1">
                <div className="grid">
                  <label
                    htmlFor=""
                    className="block mb-[5px] font-medium text-[#353740] text-[14px] leading-[17px]"
                  >
                    {t("support.TicketResponse")}*
                  </label>
                  <textarea
                    name=""
                    id=""
                    placeholder={t("support.Response")}
                    className={`p-[15px_20px] w-full text-[14px] font-normal leading-[17px] text-[#737782] resize-none h-[200px] rounded-[8px] border border-solid border-[#BEC0C5]  ${
                      ticket_Response_error !== ""
                        ? "border-red-600"
                        : "border-[#E6E6E8]"
                    }`}
                    value={ticket_Response_value}
                    onChange={(e) => handlechange_ticketResponse(e)}
                  ></textarea>
                </div>
                <div
                  className="group relative flex flex-col justify-center items-center bg-[#FCFCFC] mt-auto p-[25px_20px] border border-[#D9D9E3] border-dashed rounded-[8px] w-full max-sm:max-w-full min-h-[200px]"
                  onDragOver={(e) => e.preventDefault()}
                  onDrop={(e) => {
                    e.preventDefault();
                    const file = e.dataTransfer.files?.[0];
                    if (file) handleFile(file);
                  }}
                >
                  {media_Loader ? (
                    <Page_loader width_val={"30px"} height_val={"30px"} />
                  ) : (
                    <>
                      <input
                        type="file"
                        className="top-0 left-0 z-[2] absolute opacity-0 w-full h-full text-[0px] cursor-pointer"
                        onChange={(e) => handleFile(e.target.files?.[0])}
                      />
                      <h3 className="mb-[6px] font-medium text-[#70707B] text-sm text-center leading-[20px]">
                        {t("support.Drag")}
                      </h3>
                      <p className="font-normal text-[#353740] text-[14px] text-center leading-[20px]">
                        {t("support.Format")}
                      </p>
                      <p className="hidden top-0 left-0 z-1 absolute place-items-center group-hover:grid bg-[#F6F6F6] rounded-[8px] w-full h-full font-medium text-[#191919] text-[14px] text-center leading-[20px]">
                        {t("support.Drop")}
                      </p>
                    </>
                  )}
                </div>
              </div>
              <p className="mt-1 text-xs font-normal text-red-invalid">
                {ticket_Response_error}
              </p>

              <div className="flex flex-wrap items-center gap-[20px] mt-[16px]">
                {Array.isArray(media_Api_result) &&
                  media_Api_result.length > 0 &&
                  media_Api_result.map((val, index) => (
                    <div key={index} className="flex items-center gap-2">
                      {val.file?.toLowerCase().endsWith(".pdf") ? (
                        <img
                          src="/img/pdf-doc.svg"
                          alt="pdf"
                          className="w-5 h-5"
                        />
                      ) : (
                        <img
                          src={
                            imageUrl +
                            "?path=" +
                            val.path +
                            "&name=" +
                            val.file +
                            "&width=20&height=20"
                          }
                          alt={val.file}
                          className="object-cover w-5 h-5"
                        />
                      )}

                      <p className="font-normal text-[#191919] text-[14px] underline underline-offset-4 leading-5">
                        {val.file}
                      </p>
                      <a
                        role="button"
                        onClick={(e) => handleclick_delete_file(e, index)}
                        className="cursor-pointer"
                      >
                        <img src="/img/remove-doc.svg" alt="remove" />
                      </a>
                    </div>
                  ))}
              </div>
              {Reply_Loader ? (
                <Button_loader
                  class_Name={`flex justify-center items-center  px-[20px] border bg-[#191919] border-black hover:bg-[#3a3939] transition-all duration-300 rounded-lg h-10 font-medium text-sm text-white btn mt-[30px]`}
                  width_val={"80px"}
                />
              ) : Support_Detail_Result.status == "resolved" ? (
                <button
                  className="flex justify-center items-center bg-[#191919] hover:bg-[#3a3939] mt-[30px] px-[20px] border border-black rounded-lg h-10 font-medium text-white text-sm transition-all duration-300 btn"
                  onClick={(e) => handleclick_Reopen(e)}
                  style={{ width: "80px" }}
                >
                  {t("support.Reopen")}
                </button>
              ) : (
                <button
                  className="flex justify-center items-center bg-[#191919] hover:bg-[#3a3939] mt-[30px] px-[20px] border border-black rounded-lg h-10 font-medium text-white text-sm transition-all duration-300 btn"
                  onClick={(e) => handleclick_Replay(e)}
                  style={{ width: "80px" }}
                >
                  {t("support.Reply")}
                </button>
              )}
            </div>

            <div className="mt-[30px]">
              {loader
                ? Array(2)
                    .fill(0)
                    .map((_, index) => <Support_Message_Skeleton key={index} />)
                : Support_Detail_Result?.ticketMessage?.map((val, index) => (
                    <div
                      key={index}
                      className="bg-[#F7F7F7] mb-[10px] last-of-type:mb-0 px-5 py-5 border border-[#DDDDDD] rounded-[8px] w-full min-h-[158px]"
                    >
                      <div className="flex max-sm:flex-wrap justify-between items-start gap-[10px] mb-4 pb-4 border-[#E2E4E8] border-b border-solid">
                        <div className="flex items-center space-x-[10px]">
                          {val.postedBy?.avatarPath &&
                            val.postedBy?.avatar ? (
                              <img
                                src={
                                  imageUrl +
                                  "?path=" +
                                  val.postedBy?.avatarPath +
                                  "&name=" +
                                  val.postedBy?.avatar +
                                  "&width=42&height=42"
                                }
                                alt="Admin"
                                className="rounded-full w-[42px] h-[42px]"
                              />
                            ) : (
                              <div className="flex justify-center items-center border border-grey rounded-full w-8 h-8">
                                <p className="font-semibold text-black text-lg">
                                  {val?.postedBy &&
                                    val?.postedBy?.firstName
                                      ?.charAt(0)
                                      ?.toUpperCase()}
                                </p>
                              </div>
                            )}
                          <div>
                            <h4 className="font-semibold text-[#191919] text-sm">
                              {val.postedBy?.firstName} {val.postedBy?.lastName}
                            </h4>
                            <p className="font-normal text-[#616161] text-sm leading-[17px]">
                              {val.postedBy?.username}
                            </p>
                          </div>
                        </div>
                        <span className="font-normal text-[#636363] text-sm leading-[17px] whitespace-nowrap">
                          {moment(val?.createdDate)?.format(
                            "DD MMM YY, hh:mm a"
                          )}
                        </span>
                      </div>
                      <p className="font-normal text-[#191919] text-sm leading-[22px]">
                        {val?.message}
                      </p>

                      {val.ticketAttachments.map((val2, index2) => (
                        <div
                          key={index2}
                          className="flex flex-wrap items-center gap-[20px] mt-[16px] cursor-pointer"
                          onClick={(e) => handleclick_Attachments(e, val2)}
                        >
                          <div className="flex items-center gap-2">
                            {val2.fileName?.toLowerCase().endsWith(".pdf") ? (
                              <img
                                src="/img/pdf-doc.svg"
                                alt="pdf"
                                className="w-5 h-5"
                              />
                            ) : (
                              <img
                                src={
                                  imageUrl +
                                  "?path=" +
                                  val2.filePath +
                                  "&name=" +
                                  val2.fileName +
                                  "&width=20&height=20"
                                }
                                alt={val2.fileName}
                                className="object-cover w-5 h-5"
                              />
                            )}

                            <p className="font-normal text-[#191919] text-[14px] underline underline-offset-4 leading-5">
                              {val2.fileName}
                            </p>
                          </div>
                        </div>
                      ))}
                    </div>
                  ))}
            </div>
          </div>
        </main>
      </div>

      <SupportDetail_ImageAttachment_Modal
        isOpen={isOpen}
        setIsOpen={setIsOpen}
        selected_Attachment={selected_Attachment}
      />
    </>
  );
};

export default SupportDetail;
