"use client";
//Components
import CustomCurrencyFormatter from "@/Components/CustomCurrencyFormatter";
import ConnectPlugin from "@/Components/connectPlugins";
import { useTranslation } from "react-i18next";

import { useSelector } from "react-redux";

const CheckoutPriceDetails = ({
  token,
  handlePayNow,
  cartPath,
  addonlist,
  loader,
  valid,
}) => {
  const { t } = useTranslation();

  const couponData = useSelector((s) => s.CheckoutRedux.checkoutCouponData);
  const couponDiscountValues = useSelector(
    (s) => s.CheckoutRedux.couponDiscountValue
  );
  const totalPriceWithoutTax = useSelector(
    (s) => s.CheckoutRedux.totalPriceWithoutTax
  );
  const totalPriceWithTax = useSelector(
    (s) => s.CheckoutRedux.totalPriceWithTax
  );
  const enableFailedOrder = useSelector(
    (s) => s.CheckoutRedux.enableFailedOrder
  );

  return (
    <div
      className={`top-[100px] max-lg:static sticky max-lg:w-full ${
        valid ? "opacity-100" : "opacity-60"
      }`}
    >
      <div className="bg-white lg:shadow-[0px_4px_20px_0px_#0000001F] border border-[#E5E5E5] border-solid min-w-full lg:min-w-[363px]">
        <div className="static z-40 w-full bg-white lg:bg-transparent">
          {/* Payment Addon */}
          <div className="p-[20px] border-[#F3F3F3] border-b border-solid">
            <h3 className="font-semibold text-[#787C87] text-base leading-[19px]">
              {t("checkout.CostInfo")}
            </h3>
          </div>
          <div className="">
            <div className="flex flex-col">
              <div className="flex justify-between items-center mt-0 p-[16px_20px]">
                <h3 className="font-normal text-[#202124E5] text-sm leading-4">
                  {t("checkout.TotalAmt")}
                </h3>
                <p className="font-normal text-[#202124E5] text-sm leading-4">
                  <CustomCurrencyFormatter price={totalPriceWithoutTax} />
                </p>
              </div>
              <div className="flex justify-between items-center mt-0 p-[16px_20px]">
                <h3 className="font-normal text-[#202124E5] text-sm leading-4">
                  {t("checkout.TotalTax")}
                </h3>
                <p className="font-normal text-[#202124E5] text-sm leading-4">
                  <CustomCurrencyFormatter price={totalPriceWithTax} />
                </p>
              </div>
              <div className="flex justify-between items-center mt-0 p-[16px_20px]">
                <h3 className="font-normal text-[#202124E5] text-sm leading-4">
                  {t("checkout.TotalShip")}
                </h3>
                <p className="font-normal text-[#202124E5] text-sm leading-4">
                  <CustomCurrencyFormatter price={0.0} />
                </p>
              </div>
            </div>

            {((couponData && cartPath == "cartItems") ||
              (couponDiscountValues != 0 && enableFailedOrder)) && (
              <div className="flex justify-between items-center mx-[20px] p-[24px_0px] border-[#E5E5E5] border-y border-dashed">
                <div>
                  <h3 className="font-bold text-[#202124E5] text-base leading-[19px]">
                    {t("checkout.DisAmt")}
                  </h3>
                  <span className="font-bold text-[#202124E5] text-base leading-[19px]">
                    {t("checkout.Coupon")}
                  </span>
                </div>
                <p className="text-base font-normal text-black-300 whitespace-nowrap">
                  - <CustomCurrencyFormatter price={couponDiscountValues} />
                </p>
              </div>
            )}
            <div className="flex justify-between items-center mx-[20px] p-[24px_0px] border-[#E5E5E5] border-y border-dashed">
              <h3 className="font-bold text-[#202124E5] text-base leading-[19px]">
                {t("checkout.TotalAmtPay")}
              </h3>
              <p className="font-bold text-[#202124E5] text-base leading-[19px] whitespace-nowrap">
                {" "}
                <CustomCurrencyFormatter
                  price={
                    totalPriceWithoutTax +
                    totalPriceWithTax -
                    (cartPath == "cartItems" ? couponDiscountValues : 0)
                  }
                />
              </p>
            </div>

            <div className="p-[20px_20px_30px_20px]">
              {loader == true ? (
                <button className="bg-black hover:bg-zinc-800 px-8 rounded w-full h-[49px] font-medium text-white text-sm leading-4 btn">
                  <svg
                    aria-hidden="true"
                    role="status"
                    className="inline w-5 h-5 text-white me-3 animate-spin"
                    viewBox="0 0 100 101"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M100 50.5908C100 78.2051 77.6142 100.591 50 100.591C22.3858 100.591 0 78.2051 0 50.5908C0 22.9766 22.3858 0.59082 50 0.59082C77.6142 0.59082 100 22.9766 100 50.5908ZM9.08144 50.5908C9.08144 73.1895 27.4013 91.5094 50 91.5094C72.5987 91.5094 90.9186 73.1895 90.9186 50.5908C90.9186 27.9921 72.5987 9.67226 50 9.67226C27.4013 9.67226 9.08144 27.9921 9.08144 50.5908Z"
                      fill="#E5E7EB"
                    />
                    <path
                      d="M93.9676 39.0409C96.393 38.4038 97.8624 35.9116 97.0079 33.5539C95.2932 28.8227 92.871 24.3692 89.8167 20.348C85.8452 15.1192 80.8826 10.7238 75.2124 7.41289C69.5422 4.10194 63.2754 1.94025 56.7698 1.05124C51.7666 0.367541 46.6976 0.446843 41.7345 1.27873C39.2613 1.69328 37.813 4.19778 38.4501 6.62326C39.0873 9.04874 41.5694 10.4717 44.0505 10.1071C47.8511 9.54855 51.7191 9.52689 55.5402 10.0491C60.8642 10.7766 65.9928 12.5457 70.6331 15.2552C75.2735 17.9648 79.3347 21.5619 82.5849 25.841C84.9175 28.9121 86.7997 32.2913 88.1811 35.8758C89.083 38.2158 91.5421 39.6781 93.9676 39.0409Z"
                      fill="currentColor"
                    />
                  </svg>
                </button>
              ) : (
                <button
                  disabled={!valid}
                  onClick={handlePayNow}
                  className={`bg-black px-8 rounded w-full h-[45px] font-semibold text-white text-base leading-4 btn ${
                    valid && "hover:bg-zinc-800"
                  }`}
                >
                  {/* {t("checkout.PayNow")} */}
                  {t("chooseAddress.proceed")}
                </button>
              )}
            </div>
          </div>
        </div>
      </div>

      {token &&
        !enableFailedOrder &&
        cartPath == "cartItems" &&
        addonlist &&
        addonlist?.["coupon"] &&
        addonlist?.["coupon"]?.status == 1 &&
        ConnectPlugin.CouponApply && (
          <ConnectPlugin.CouponApply token={token} valid={valid} />
        )}
    </div>
  );
};

export default CheckoutPriceDetails;
