//Redux
import { viewCartCount } from "@/redux/slices/userSlice";

export const AddToCartLocal = (productDetail, dispatch) => {
  let addToCartProduct = {
    ...productDetail,
    productCount: productDetail?.minQuantityAllowedCart
      ? productDetail?.minQuantityAllowedCart
      : 1,
  };
  let localCart = [];
  if (localStorage.getItem("cartItems")) {
    let cartlist = JSON.parse(localStorage.getItem("cartItems"));

    if (cartlist.length) {
      let idCheck = false;
      cartlist.map((product) => {
        if (product.productId == productDetail.productId) {
          if (product.skuName == productDetail.skuName) {
            idCheck = true;
            product.productCount =
              product.productCount +
              parseInt(productDetail?.minQuantityAllowedCart);
          }
        }
      });
      if (idCheck == true) {
        localStorage.setItem("cartItems", JSON.stringify(cartlist));
        dispatch(viewCartCount(cartlist));
      } else {
        cartlist.push(addToCartProduct);
        localStorage.setItem("cartItems", JSON.stringify(cartlist));
        dispatch(viewCartCount(cartlist));
      }
    } else {
      localCart.push(addToCartProduct);
      localStorage.setItem("cartItems", JSON.stringify(localCart));
      dispatch(viewCartCount(localCart));
    }
  } else {
    localCart.push(addToCartProduct);
    localStorage.setItem("cartItems", JSON.stringify(localCart));
    dispatch(viewCartCount(localCart));
  }
};
