import axiosInstance from "@/api/interceptor";

export const getProductDetailApi = async (slug) => {
  const data = await axiosInstance.get(`product-store/productdetail/${slug}`);
  return data?.data;
};

export const getVariants = async (slug) => {
  try {
    const { data } = await axiosInstance.get(
      `store-product-variant/product/${slug}`,
    );
    return data?.status == 1 ? data : null;
  } catch (error) {
    return null;
  }
};

export const filterListApi = async (categorySlug, setFilterList) => {
  const result = await axiosInstance.get(`site-filter/${categorySlug}`);
  if (result?.data?.data) {
    setFilterList(
      result.data.data?.filter((attribute) => attribute?.sectionType == 1),
    );
  }
};
