"use client";

//React and Next
import { Fragment, useEffect, useState } from "react";
import Link from "next/link";

//3rd Party
import { useRouter } from "nextjs-toploader/app";
import StarRatings from "react-star-ratings";

//Utils
import {
  allowImageSize,
  allowImageType,
  allowVideoSize,
  allowVideoType,
  imageUrl,
  unallowImageType,
  unallowVideoType,
} from "@/utilities/url";

//API
import { OrderProductReviewApi, ReviewSendApi } from "./ratingApi";

//Components
import Sidebar from "@/Components/Myprofile/sidebar";
import Breadcrumb from "@/Components/Breadcrumbs";
import ToastMessage from "@/Components/ToastMessage";
import CustomCurrencyFormatter from "@/Components/CustomCurrencyFormatter";
import { useTranslation } from "react-i18next";

export default function OrderDetailAddReview({ orderId }) {
  const [AddLoader, setAddLoader] = useState(false);
  const [starRating, setStarRating] = useState(0);
  const [starRatingErr, setStarRatingErr] = useState("");
  const [description, setDescription] = useState("");
  const [descriptionErr, setDescriptionErr] = useState("");
  const [validestatus, setValideStatus] = useState(false);
  const [reviewdata, setReviewData] = useState();
  const router = useRouter();
  const { t } = useTranslation();

  //Rating and Review with Image
  const [reviewImage, setReviewImage] = useState([]);
  const [reviewVideo, setReviewVideo] = useState([]);
  const [preReviewImage, setPreReviewImage] = useState([]);
  const [preReviewVideo, setPreReviewVideo] = useState([]);
  const [deletedIds, setDeletedIds] = useState([]);

  let newImage = [];
  let newVideo = [];
  let newArray = [];

  const [media, setMedia] = useState(null);
  const [selectedImageIndex, setSelectedImageIndex] = useState(0);
  const [ratingMedia, setRatingMedia] = useState([]);
  const [reszize, setResize] = useState(false);

  const handleResizeMedia = (value, index) => {
    setResize(true);
    setMedia(value);
    setSelectedImageIndex(index + 1);
  };

  const handleImageChange = (value, index) => {
    setMedia(value);
    setSelectedImageIndex(index + 1);
  };

  const handlePreviousImage = () => {
    newArray?.forEach((imagelist, index) => {
      if (imagelist?.fileName == media?.fileName) {
        if (index == 0) {
          setSelectedImageIndex(newArray?.length);
          const lastImage = newArray?.at(-1);
          setMedia(JSON?.parse(JSON?.stringify(lastImage)));
        } else {
          setSelectedImageIndex(index);
          const previousImage = newArray?.find((img, i) => i == index - 1);
          setMedia(JSON?.parse(JSON?.stringify(previousImage)));
        }
      } else if (imagelist == media) {
        if (index == 0) {
          setSelectedImageIndex(newArray?.length);
          const lastImage = newArray?.at(-1);
          setMedia(lastImage);
        } else {
          setSelectedImageIndex(index);
          const previousImage = newArray?.find((img, i) => i == index - 1);
          setMedia(previousImage);
        }
      }
    });
  };

  const handleNextImage = () => {
    newArray?.forEach((imagelist, index) => {
      if (imagelist == media) {
        if (index + 1 == newArray?.length) {
          setSelectedImageIndex(1);
          const firstImage = newArray?.at(0);
          setMedia(firstImage);
        } else {
          setSelectedImageIndex(index + 2);
          const nextImage = newArray?.find((img, i) => i == index + 1);
          setMedia(nextImage);
        }
      }
    });
  };

  function handleRemoveReviewImage(data, id) {
    let modified = reviewImage.filter((value, ind) => data !== value);
    setReviewImage(modified);
  }

  function handleRemoveReviewVideo(data, id) {
    let modified = reviewVideo.filter((value, ind) => data !== value);
    setReviewVideo(modified);
  }

  function handleRemovePreReviewImage(data, id) {
    setDeletedIds((preDel) => [...preDel, data?.id]);
    let modified = preReviewImage.filter(
      (value, ind) => data?.fileName !== value?.fileName
    );
    setPreReviewImage(modified);
  }

  function handleRemovePreReviewVideo(data, id) {
    setDeletedIds((preDel) => [...preDel, data?.id]);
    let modified = preReviewVideo.filter(
      (value, ind) => data?.fileName !== value?.fileName
    );
    setPreReviewVideo(modified);
  }

  function handleReviewMedia(e) {
    const { files } = e.target;

    // Image
    if (files && files[0] && files[0].name.match(allowImageType)) {
      const fsize = files[0].size;
      const file = Math.round(fsize / 1024);

      if (file <= allowImageSize) {
        let reader = new FileReader();
        reader.readAsDataURL(files[0]);
        reader.onloadend = () => {
          if (reader?.result.startsWith("data:image/")) {
            if (
              reviewImage.length <= 2 &&
              preReviewImage.length + reviewImage.length <= 2
            ) {
              setReviewImage((previousImages) => [
                ...previousImages,
                reader?.result,
              ]);
            } else {
              ToastMessage({
                type: "error",
                message: t("toastMessage.imageUploadLimit"),
              });
            }
          }
        };
      } else {
        ToastMessage({
          type: "error",
          message: t("toastMessage.profileImageSize"),
        });
      }
    }
    // Video
    else if (files && files[0] && files[0].name.match(allowVideoType)) {
      const fsize = files[0].size;
      const file = Math.round(fsize / 1024);

      if (file <= allowVideoSize) {
        let reader = new FileReader();
        reader.readAsDataURL(files[0]);
        reader.onloadend = () => {
          if (reader?.result.startsWith("data:video/")) {
            if (
              reviewVideo.length <= 0 &&
              preReviewVideo.length + reviewVideo.length <= 0
            ) {
              setReviewVideo((previousVideos) => [
                ...previousVideos,
                reader?.result,
              ]);
            } else {
              ToastMessage({
                type: "error",
                message: t("toastMessage.videoUploadLimit"),
              });
            }
          }
        };
      } else {
        ToastMessage({
          type: "error",
          message: t("toastMessage.profileVideoSize"),
        });
      }
    } else if (files && files[0] && files[0].name.match(unallowImageType)) {
      ToastMessage({
        type: "error",
        message: t("toastMessage.profileImageType"),
      });
    } else if (files && files[0] && files[0].name.match(unallowVideoType)) {
      ToastMessage({
        type: "error",
        message: t("toastMessage.profileVideoType"),
      });
    } else {
      ToastMessage({
        type: "error",
        message: t("toastMessage.invalidFileType"),
      });
    }

    e.target.value = "";
  }

  const handleImageVideo = () => {
    preReviewImage.map((value, index) => {
      if (value?.fileType == "image") {
        newImage.push(value);
      }
    });

    reviewImage.map((value, index) => {
      if (value?.startsWith("data:image/")) {
        newImage.push(value);
      }
    });

    preReviewVideo.map((value, index) => {
      if (value?.fileType == "video") {
        newVideo.push(value);
      }
    });

    reviewVideo.map((value, index) => {
      if (value?.startsWith("data:video/")) {
        newVideo.push(value);
      }
    });

    newArray = [...newImage, ...newVideo];

    return newArray.map((value, index) => (
      <Fragment key={index}>
        {value?.fileType == "image" && (
          <div className="relative w-20 h-20 rounded cursor-pointer group-item min-w-20">
            <img
              src={
                imageUrl +
                "?path=" +
                value?.filePath +
                "&name=" +
                value?.fileName +
                "&width=300&height=300"
              }
              alt={value?.fileName}
              className="object-cover w-full h-full"
            />
            <div className="top-0 left-0 absolute flex justify-end items-start space-x-1 bg-[#00000029] p-1 rounded w-full h-full item">
              <a
                role="button"
                onClick={() => handleRemovePreReviewImage(value, index)}
                className="flex justify-center items-center bg-white hover:bg-slate-100 shadow-md rounded-full w-[18px] h-[18px]"
              >
                <img src="/img/close.svg" alt="close" className="w-2.5" />
              </a>
              <a
                role="button"
                onClick={() => handleResizeMedia(value, index)}
                className="flex justify-center items-center bg-white hover:bg-slate-100 shadow-md rounded-full w-[18px] h-[18px]"
              >
                <img src="/img/expand.svg" alt="expand" className="w-2.5" />
              </a>
            </div>
          </div>
        )}
        {String(value)?.startsWith("data:image/") && (
          <div className="relative w-20 h-20 rounded cursor-pointer group-item min-w-20">
            <img
              src={value}
              alt="image"
              className="object-cover w-full h-full"
            />
            <div className="top-0 left-0 absolute flex justify-end items-start space-x-1 bg-[#00000029] p-1 rounded w-full h-full item">
              <a
                role="button"
                onClick={() => handleRemoveReviewImage(value, index)}
                className="flex justify-center items-center bg-white hover:bg-slate-100 shadow-md rounded-full w-[18px] h-[18px]"
              >
                <img src="/img/close.svg" alt="close" className="w-2.5" />
              </a>
              <a
                role="button"
                onClick={() => handleResizeMedia(value, index)}
                className="flex justify-center items-center bg-white hover:bg-slate-100 shadow-md rounded-full w-[18px] h-[18px]"
              >
                <img src="/img/expand.svg" alt="expand" className="w-2.5" />
              </a>
            </div>
          </div>
        )}
        {value?.fileType == "video" && (
          <div className="relative w-20 h-20 rounded cursor-pointer group-item min-w-20">
            <video
              src={`${process.env.NEXT_PUBLIC_SERVER_URL}media/video-preview-s3?name=${value?.fileName}&path=${value?.filePath}`}
              className="object-cover w-full h-full"
            />
            <div className="top-0 left-0 absolute flex justify-end items-start space-x-1 bg-[#00000029] p-1 rounded w-full h-full item">
              <a
                role="button"
                onClick={() => handleRemovePreReviewVideo(value, index)}
                className="flex justify-center items-center bg-white hover:bg-slate-100 shadow-md rounded-full w-[18px] h-[18px]"
              >
                <img src="/img/close.svg" alt="close" className="w-2.5" />
              </a>
              <a
                role="button"
                onClick={() => handleResizeMedia(value, index)}
                className="flex justify-center items-center bg-white hover:bg-slate-100 shadow-md rounded-full w-[18px] h-[18px]"
              >
                <img src="/img/expand.svg" alt="expand" className="w-2.5" />
              </a>
            </div>
          </div>
        )}
        {String(value)?.startsWith("data:video/") && (
          <div className="relative w-20 h-20 rounded cursor-pointer group-item min-w-20">
            <video src={value} className="object-cover w-full h-full" />
            <div className="top-0 left-0 absolute flex justify-end items-start space-x-1 bg-[#00000029] p-1 rounded w-full h-full item">
              <a
                role="button"
                onClick={() => handleRemoveReviewVideo(value, index)}
                className="flex justify-center items-center bg-white hover:bg-slate-100 shadow-md rounded-full w-[18px] h-[18px]"
              >
                <img src="/img/close.svg" alt="close" className="w-2.5" />
              </a>
              <a
                role="button"
                onClick={() => handleResizeMedia(value, index)}
                className="flex justify-center items-center bg-white hover:bg-slate-100 shadow-md rounded-full w-[18px] h-[18px]"
              >
                <img src="/img/expand.svg" alt="expand" className="w-2.5" />
              </a>
            </div>
          </div>
        )}
      </Fragment>
    ));
  };

  const orderBreadCrumbEdit = [
    {
      text: t("Breadcrumb.home"),
      url: "/",
    },
    {
      text: t("Breadcrumb.accountSettings"),
      url: "/account-settings",
    },
    {
      text: t("Breadcrumb.orderHistory"),
      url: "/account-settings/order-history",
    },
    {
      text: t("Breadcrumb.review"),
      url: "javascript:void(0)",
    },
  ];
  useEffect(() => {
    OrderProductReviewApi(
      orderId,
      setReviewData,
      setStarRating,
      setDescription,
      setRatingMedia
    );
  }, []);

  useEffect(() => {
    ratingMedia.map((value) => {
      if (value?.fileType == "image") {
        setPreReviewImage((pre) => [...pre, value]);
      } else if (value?.fileType == "video") {
        setPreReviewVideo((pre) => [...pre, value]);
      }
    });
  }, [ratingMedia]);

  const handleChangeStar = (rating) => {
    setStarRating(rating);
  };

  const handlesubmit = () => {
    setValideStatus(true);
    if (starRating == 0) {
      ToastMessage({
        type: "error",
        message: t("validationMessage.ratingStar"),
      });
    }
    if (validate()) {
      setAddLoader(true);
      ReviewSendApi(
        orderId,
        deletedIds,
        starRating,
        description,
        reviewdata,
        reviewImage,
        reviewVideo,
        router,
        setAddLoader
      );
    }
  };

  const validate = () => {
    let ValidateObj = {
      starRating: false,
      description: false,
    };
    if (starRating !== 0) {
      setStarRatingErr("");
      ValidateObj.starRating = true;
    } else {
      setStarRatingErr(t("validationMessage.ratingStar"));
      ValidateObj.starRating = false;
    }

    if (description == "") {
      setDescriptionErr(t("validationMessage.reviewEmpty"));
      ValidateObj.description = false;
    } else {
      setDescriptionErr("");
      ValidateObj.description = true;
    }
    if (ValidateObj.starRating == true && ValidateObj.description == true) {
      return true;
    } else {
      return false;
    }
  };

  useEffect(() => {
    if (validestatus) {
      validate();
    }
  }, [starRating, description]);

  return (
    <div className="mx-auto px-4 py-2.5 max-w-screen-2xl">
      <Breadcrumb breadCrumb={orderBreadCrumbEdit} />

      <div className="flex gap-8 mx-auto mt-4 mb-7">
        <Sidebar />
        {/* New Design */}
        <div className="w-full">
          <div className="relative w-full max-h-full">
            <div className="relative bg-white dark:bg-gray-700">
              <div>
                <h3 className="mb-1.5 font-bold text-black-300 text-xl">
                  {t("productcontent.WrtRev")}
                </h3>
                {/* <p className="text-sm font-normal leading-relaxed text-black-300">
                                        {t("productcontent.PstQCont")}
                                    </p> */}
                <div className="flex space-x-4 rtl:space-x-[unset] bg-lightgrey my-6 p-4">
                  {reviewdata !== undefined && (
                    <div className="rtl:ml-4 rounded-md w-[120px] h-[116px]">
                      <img
                        src={
                          imageUrl +
                          "?path=" +
                          reviewdata?.containerName +
                          "&name=" +
                          reviewdata?.productImage +
                          "&width=200&height=200"
                        }
                        alt={reviewdata?.containerName}
                        className="object-contain w-full h-full max-w-full m-auto rounded-md"
                      />
                    </div>
                  )}
                  <div className="flex flex-col justify-between">
                    {reviewdata !== undefined && (
                      <div className="flex flex-col space-y-2.5">
                        <h3 className="text-base font-semibold text-boldblack">
                          {reviewdata?.productNameTrans
                            ? reviewdata?.productNameTrans
                            : reviewdata?.productName}
                        </h3>
                        <p className="mb-1 text-lg font-black text-boldblack">
                          <CustomCurrencyFormatter
                            price={reviewdata?.basePrice}
                          />
                        </p>
                      </div>
                    )}
                    <div className="flex items-center space-x-1.5">
                      <StarRatings
                        rating={starRating}
                        starEmptyColor="#d8d8eb"
                        starRatedColor="black"
                        starHoverColor="#333"
                        starDimension="16px"
                        starSpacing="3px"
                        numberOfStars={5}
                        changeRating={handleChangeStar}
                        name="rating"
                        svgIconPath="M12.489,5.03a.665.665,0,0,0-.572-.457L8.3,4.245,6.873.9A.666.666,0,0,0,5.649.9L4.22,4.245.6,4.573A.666.666,0,0,0,.227,5.738l2.732,2.4-.806,3.548a.665.665,0,0,0,.99.72l3.117-1.864L9.377,12.4a.666.666,0,0,0,.991-.72L9.562,8.134l2.732-2.4A.666.666,0,0,0,12.489,5.03ZM6.321,10.5"
                        svgIconViewBox="0 0 12.522 12"
                      />
                    </div>
                  </div>
                </div>
                <div className="mb-3">
                  <textarea
                    onChange={(e) => setDescription(e.target.value.trimStart())}
                    value={description}
                    className={
                      descriptionErr !== ""
                        ? "w-full  px-4 py-3.5 text-black-300 h-148 placeholder: text-gray-500 text-sm font-normal  rounded-md focus:border-red border border-red"
                        : "w-full  px-4 py-3.5 text-black-300 h-148 placeholder: text-gray-500 text-sm font-normal  rounded-md focus:ring-0 border border-gray-25"
                    }
                    placeholder={t("myprofile.MentDetails")}
                  ></textarea>
                </div>
                <div className="mb-5">
                  <p className="text-sm font-medium text-bold-black">
                    {t("productcontent.AddRtg")}
                    <Link
                      prefetch={false}
                      href="/terms-conditions"
                      className="ml-1 text-sm font-medium text-link"
                    >
                      {t("signin.TandC")}
                    </Link>
                  </p>
                </div>
                <div className="flex flex-wrap items-center gap-2">
                  <input
                    type="file"
                    id="file"
                    onChange={(e) => handleReviewMedia(e)}
                    className="hidden"
                  />
                  <label
                    htmlFor="file"
                    className="flex justify-center items-center bg-[#F9F9FC] rounded w-20 min-w-20 h-20 cursor-pointer"
                  >
                    <img
                      src="/img/img-upload-review.svg"
                      alt="img-upload-review"
                    />
                  </label>

                  {handleImageVideo()}

                  {/* Resize Modal */}
                  <div
                    className={`${
                      reszize
                        ? "fixed top-0 left-0 w-full h-full bg-light-modal z-150 flex justify-center items-center lg:items-start"
                        : "hidden"
                    }`}
                  >
                    <div className="w-full h-full pb-4 bg-white">
                      <div className="flex items-center m-4">
                        <div className="flex items-center justify-center w-full px-6 space-x-3">
                          <h2 className="flex space-x-1 text-xl font-bold text-black">
                            {selectedImageIndex}
                            <span className="text-black mx-1">
                              {t("productlist.of")}
                            </span>
                            {newArray && newArray?.length}
                          </h2>
                        </div>
                        <button
                          className="flex justify-center items-center bg-white hover:bg-[#e8e8e8] hover:shadow-lg rounded-full w-8 h-8"
                          onClick={() => {
                            setResize(false), setMedia({});
                          }}
                        >
                          {" "}
                          <img
                            className="w-4"
                            src="/img/close.svg"
                            alt="close"
                          />
                        </button>
                      </div>
                      <div className="flex space-x-6 rtl:space-x-[unset] px-4 h-calc">
                        <div className="relative flex-[13] bg-gray-100 rtl:ml-6 rounded-2xl w-full min-w-full md:min-w-[75vw] h-full aspect-square">
                          {media?.fileType == "image" && (
                            <img
                              src={
                                imageUrl +
                                "?path=" +
                                media?.filePath +
                                "&name=" +
                                media?.fileName +
                                "&width=600&height=500"
                              }
                              alt={media?.fileName}
                              onError={({ currentTarget }) => {
                                currentTarget.onerror = null;
                                currentTarget.src = "/img/no-image.png";
                                currentTarget.alt = "no-image";
                              }}
                              className="object-contain w-full h-full m-auto rounded-2xl"
                            />
                          )}
                          {String(media)?.startsWith("data:image/") && (
                            <img
                              src={media}
                              alt="image"
                              onError={({ currentTarget }) => {
                                currentTarget.onerror = null;
                                currentTarget.src = "/img/no-image.png";
                                currentTarget.alt = "no-image";
                              }}
                              className="object-contain w-full h-full m-auto rounded-2xl"
                            />
                          )}
                          {media?.fileType == "video" && (
                            <video
                              src={`${process.env.NEXT_PUBLIC_SERVER_URL}media/video-preview-s3?name=${media?.fileName}&path=${media?.filePath}`}
                              className="object-contain w-full h-full m-auto"
                              autoPlay
                              controls
                              controlsList="nodownload"
                            />
                          )}
                          {String(media)?.startsWith("data:video/") && (
                            <video
                              src={media}
                              className="object-contain w-full h-full m-auto"
                              autoPlay
                              controls
                              controlsList="nodownload"
                            />
                          )}
                          <button
                            onClick={(e) => handlePreviousImage()}
                            className="top-[45%] left-2 absolute flex justify-center items-center bg-white hover:bg-gray-100 shadow-lg rounded-full w-12 h-12"
                          >
                            <img
                              src="/img/leftgray- arrow.svg"
                              alt="leftgray-arrow"
                            />
                          </button>
                          <button
                            onClick={(e) => handleNextImage()}
                            className="top-[45%] right-2 absolute flex justify-center items-center bg-white hover:bg-gray-100 shadow-lg rounded-full w-12 h-12"
                          >
                            <img
                              src="/img/rightgray-arrow.svg"
                              alt="rightgray-arrow"
                            />
                          </button>
                        </div>

                        <div className="hidden flex-[3] gap-3 three-grid md:grid h-auto lg:h-50vh overflow-y-auto scroll-hide">
                          {newArray &&
                            newArray?.map(
                              (imagelist, index) =>
                                imagelist?.fileType == "image" && (
                                  <a
                                    role="button"
                                    key={`image${index}`}
                                    onClick={(e) =>
                                      handleImageChange(imagelist, index)
                                    }
                                    className={`w-full bg-gray-100 max-h-[236px] cursor-pointer hover:border-2 border-black  aspect-square hover:p-0.5 rounded-2xl ${
                                      media?.fileName == imagelist?.fileName &&
                                      "border-2 p-0.5"
                                    } `}
                                  >
                                    <img
                                      src={
                                        imageUrl +
                                        "?path=" +
                                        imagelist?.filePath +
                                        "&name=" +
                                        imagelist?.fileName +
                                        "&width=278&height=236"
                                      }
                                      alt={imagelist?.fileName}
                                      className="object-contain w-full h-full m-auto rounded-2xl"
                                      onError={({ currentTarget }) => {
                                        currentTarget.onerror = null;
                                        currentTarget.src = "/img/no-image.png";
                                        currentTarget.alt = "no-image";
                                      }}
                                    />
                                  </a>
                                )
                            )}
                          {newArray &&
                            newArray?.map(
                              (imagelist, index) =>
                                String(imagelist)?.startsWith(
                                  "data:image/"
                                ) && (
                                  <a
                                    role="button"
                                    key={`image${index}`}
                                    onClick={(e) =>
                                      handleImageChange(imagelist, index)
                                    }
                                    className={`w-full bg-gray-100 max-h-[236px] cursor-pointer hover:border-2 border-black  aspect-square hover:p-0.5 rounded-2xl ${
                                      media == imagelist && "border-2 p-0.5"
                                    } `}
                                  >
                                    <img
                                      src={imagelist}
                                      alt="image"
                                      className="object-contain w-full h-full m-auto rounded-2xl"
                                      onError={({ currentTarget }) => {
                                        currentTarget.onerror = null;
                                        currentTarget.src = "/img/no-image.png";
                                        currentTarget.alt = "no-image";
                                      }}
                                    />
                                  </a>
                                )
                            )}
                          {newArray &&
                            newArray?.map(
                              (imagelist, index) =>
                                imagelist?.fileType == "video" && (
                                  <a
                                    role="button"
                                    key={`video${index}`}
                                    onClick={(e) =>
                                      handleImageChange(imagelist, index)
                                    }
                                    className={`w-full bg-gray-100 max-h-[236px] cursor-pointer hover:border-2 border-black  aspect-square hover:p-0.5 rounded-2xl ${
                                      media?.fileName == imagelist?.fileName &&
                                      "border-2 p-0.5"
                                    }`}
                                  >
                                    <video
                                      src={`${process.env.NEXT_PUBLIC_SERVER_URL}media/video-preview-s3?name=${imagelist?.fileName}&path=${imagelist?.filePath}`}
                                      className="object-contain w-full h-full m-auto"
                                    ></video>
                                  </a>
                                )
                            )}
                          {newArray &&
                            newArray?.map(
                              (imagelist, index) =>
                                String(imagelist)?.startsWith(
                                  "data:video/"
                                ) && (
                                  <a
                                    role="button"
                                    key={`video${index}`}
                                    onClick={(e) =>
                                      handleImageChange(imagelist, index)
                                    }
                                    className={`w-full bg-gray-100 max-h-[236px] cursor-pointer hover:border-2 border-black  aspect-square hover:p-0.5 rounded-2xl ${
                                      media == imagelist && "border-2 p-0.5"
                                    }`}
                                  >
                                    <video
                                      src={imagelist}
                                      className="object-contain w-full h-full m-auto"
                                    ></video>
                                  </a>
                                )
                            )}
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              {/* <!-- Modal footer --> */}
              <div className="flex items-center justify-end p-4 bg-gray-slate-100 md:px-8 md:py-4 dark:border-gray-600">
                {AddLoader == true ? (
                  <button className="bg-black hover:bg-zinc-800 px-8 rounded h-[49px] font-semibold text-white text-sm uppercase">
                    <svg
                      aria-hidden="true"
                      role="status"
                      className="inline w-24 h-5 text-white me-3 animate-spin"
                      viewBox="0 0 100 101"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        d="M100 50.5908C100 78.2051 77.6142 100.591 50 100.591C22.3858 100.591 0 78.2051 0 50.5908C0 22.9766 22.3858 0.59082 50 0.59082C77.6142 0.59082 100 22.9766 100 50.5908ZM9.08144 50.5908C9.08144 73.1895 27.4013 91.5094 50 91.5094C72.5987 91.5094 90.9186 73.1895 90.9186 50.5908C90.9186 27.9921 72.5987 9.67226 50 9.67226C27.4013 9.67226 9.08144 27.9921 9.08144 50.5908Z"
                        fill="#E5E7EB"
                      />
                      <path
                        d="M93.9676 39.0409C96.393 38.4038 97.8624 35.9116 97.0079 33.5539C95.2932 28.8227 92.871 24.3692 89.8167 20.348C85.8452 15.1192 80.8826 10.7238 75.2124 7.41289C69.5422 4.10194 63.2754 1.94025 56.7698 1.05124C51.7666 0.367541 46.6976 0.446843 41.7345 1.27873C39.2613 1.69328 37.813 4.19778 38.4501 6.62326C39.0873 9.04874 41.5694 10.4717 44.0505 10.1071C47.8511 9.54855 51.7191 9.52689 55.5402 10.0491C60.8642 10.7766 65.9928 12.5457 70.6331 15.2552C75.2735 17.9648 79.3347 21.5619 82.5849 25.841C84.9175 28.9121 86.7997 32.2913 88.1811 35.8758C89.083 38.2158 91.5421 39.6781 93.9676 39.0409Z"
                        fill="currentColor"
                      />
                    </svg>
                  </button>
                ) : (
                  <button
                    onClick={() => handlesubmit()}
                    className="bg-black hover:bg-zinc-800 px-8 rounded h-[49px] font-semibold text-white text-sm uppercase"
                  >
                    {t("productcontent.SubRev")}
                  </button>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
