"use client";

//React and Next
import { useEffect, useState } from "react";

//API
import {
  getQuotationlist,
  getQuotationlistapi,
  getQuotationlistapiCount,
} from "../QuotationApi";

// 3d Party
import { useRouter } from "nextjs-toploader/app";
//Redux
import { useDispatch, useSelector } from "react-redux";
import { offsetCount } from "@/redux/slices/userSlice";

//Utils
import { GetAddonList } from "@/utilities/localStorage";

//Components
import Breadcrumb from "@/Components/Breadcrumbs";
import QuotationCard from "./QuotationProductCard";
import Sidebar from "../SideBar";
import Contactus from "@/Components/Contactus";
import { useTranslation } from "react-i18next";

export default function QuotationList({ token }) {
  const offset = useSelector((state) => state.userReducer.offsetVal);
  const dispatch = useDispatch();
  const [quatationList, setQuatationList] = useState([]);
  const [loader, setLoader] = useState(false);
  const addonList = GetAddonList();
  const { t } = useTranslation();
  const router = useRouter();
  const [status, setStatus] = useState(-1);

  //new
  const [currentPage, setCurrentPage] = useState(1);
  // const off = searchparams.get("offset") ? searchparams.get("offset") : 0;
  // const cleanedStr =
  //     off && off.replace(/[a-zA-Z]+/g, "") ? off.replace(/[a-zA-Z]+/g, "") : 0;
  // const [offset, setOffset] = useState(parseInt(cleanedStr));

  const [getQuotationlistApidata, setgetQuotationlistApidata] = useState([]);
  const [getQuotationlistApiCount, setgetQuotationlistApiCount] = useState(0);
  const pagenationapi = async () => {
    getQuotationlistapi(status, setQuatationList, offset, setLoader);
    getQuotationlistapiCount(status, setgetQuotationlistApiCount);
  };

  useEffect(() => {
    if (offset != 0) {
      pagenationapi();
    }
  }, [offset]);

  const onPageChange = (data) => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });

    let limit = 5;
    let offset = Math.ceil((data - 1) * limit);
    // setOffset(offset);
    dispatch(offsetCount(offset));
    setCurrentPage(data);
    // router.push(`/account-settings/quotation-list?offset=${offset}`);
  };

  const nPages1 = Math.ceil(
    getQuotationlistApidata != undefined && getQuotationlistApiCount / 5
  );
  useEffect(() => {
    setCurrentPage(offset / 5 + 1);
  }, []);

  const hanldeQuotationList = async (status) => {
    const quotationdata = await getQuotationlist(status);
    if (quotationdata?.status == 1) {
      setQuatationList(quotationdata?.data);
      getQuotationlistapiCount(status, setgetQuotationlistApiCount);
      setLoader(true);
    }
  };
  const handleStatusFilter = (data) => {
    setLoader(false);
    setStatus(data);
    hanldeQuotationList(data);
  };

  useEffect(() => {
    if (offset == 0) {
      hanldeQuotationList(status);
    }
  }, [offset]);

  const breadCrumb = [
    {
      text: t("Breadcrumb.home"),
      url: "/",
    },
    {
      text: t("Breadcrumb.accountSettings"),
      url: "/account-settings",
    },
    {
      text: t("Breadcrumb.quotationList"),
      url: "javascript:void(0)",
    },
  ];
  useEffect(() => {
    if (addonList?.["product-quotation"]?.status == 0) {
      router.push("/");
    }
  }, []);
  return (
    addonList?.["product-quotation"]?.status == 1 && (
      <>
        <div className="mx-auto px-4 py-2.5 max-w-screen-2xl">
          <Breadcrumb breadCrumb={breadCrumb} />
          <main className="flex min-h-screen mx-auto mt-1 max-w-screen-2xl">
            <Sidebar />
            <QuotationCard
              quatationList={quatationList}
              loader={loader}
              handleStatusFilter={handleStatusFilter}
              getQuotationlistApiCount={getQuotationlistApiCount}
              nPages1={nPages1}
              currentPage={currentPage}
              onPageChange={onPageChange}
            />
          </main>
        </div>

        <Contactus token={token} />
      </>
    )
  );
}
