//API
import axiosInstance from "@/api/interceptor";
//Component
import ToastMessage from "@/Components/ToastMessage";

export const getProductSpecification = async (
  slug,
  setProductSpecification
) => {
  const result = await axiosInstance.get(
    `store-product-attributes/product/${slug}`
  );
  if (result?.data?.status === 1) {
    setProductSpecification(result?.data?.data);
  } else {
    ToastMessage({ type: "error", message: result?.data?.message });
  }
};

export const getSpecificationList = async (
  id,
  setAttributes,
  setSpecificationList
) => {
  const result = await axiosInstance.get(
    `store-product-attributes?categoryIds=${id}`
  );
  if (result?.data?.status === 1) {
    let attributes = [];
    result?.data?.data?.map((specification) => {
      specification?.attributeGroups?.map((attributeGroup) => {
        let obj = {
          specificationId: specification?.id,
          attributes: attributeGroup?.attributes,
        };
        attributes.push(obj);
        setAttributes(attributes);
      });
    });
    setSpecificationList(result?.data?.data);
  } else {
    ToastMessage({ type: "error", message: result?.data?.message });
  }
};

export const filterListApi = async (categorySlug, setFilterList) => {
  const result = await axiosInstance.get(`site-filter/${categorySlug}`);
  if (result?.data?.data) {
    setFilterList(
      result.data.data?.filter((attribute) => attribute?.sectionType == 2)
    );
  }
};
