"use client";

// React and Next
import Link from "next/link";

// API
import { addToCartApi } from "./CommonProductApi";
import { AddToCartLocal } from "./AddToCart";

// Utils
import { imageUrl } from "@/utilities/url";

// 3rd Party
import { useRouter } from "nextjs-toploader/app";
// Redux
import { useDispatch } from "react-redux";

// Components
import ToastMessage from "@/Components/ToastMessage";
import CustomCurrencyFormatter from "@/Components/CustomCurrencyFormatter";
import { useTranslation } from "react-i18next";

export default function CommonProductVendorList({
  selleroptionModal,
  setSellerOptionModal,
  productDetail,
  vendorList,
  defaultImage,
  token,
}) {
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const router = useRouter();

  //  add to cart funtion for common product
  const handleAddToCart = (vendor) => {
    const product = { ...productDetail };
    product.skuName = vendor?.skuName;
    product.price = vendor?.price;
    product.skuId = vendor?.skuId;
    product.companyLogo = vendor?.companyLogo;
    product.companyLogoPath = vendor?.companyLogoPath;
    product.vendorCompanyName = vendor?.companyName;
    product.vendorDisplayNameUrl = vendor?.displayNameUrl;
    product.vendorName = vendor?.firstName + vendor?.lastName;
    product.vendorId = vendor?.vendorId;
    if (token) {
      addToCartApi("new", product, dispatch, setSellerOptionModal);
    } else {
      // if (addonlist?.["abandoned-cart"]?.status == 1) {
      //   addToGuestApi("new", product, dispatch, setSellerOptionModal);
      // } else {
      AddToCartLocal(product, dispatch);
      ToastMessage({
        type: "success",
        message: t("toastMessage.addToCart"),
      });
      // }
    }
  };

  const handleVendorRoute = (vendor) => {
    router.push(`/shop/${vendor?.displayNameUrl}`);
  };

  return (
    <>
      <div
        className={`${
          selleroptionModal
            ? "overflow-y-auto translate-x-0 transition-opacity  overflow-x-hidden fixed top-0 right-0 left-0 z-50  bg-light-modal  w-full md:inset-0 h-full max-h-full flex  "
            : "hidden"
        }`}
      ></div>
      <div
        aria-modal="true"
        id="rtl-modal"
        tabIndex="-1"
        aria-hidden="true"
        className={`${
          selleroptionModal
            ? "z-150 w-full max-w-[480px] max-h-full fixed top-0 right-0  h-full show  overflow-auto"
            : "closing z-50 w-full max-w-[480px] max-h-full fixed top-0 right-0  h-full"
        }`}
      >
        {/* <!-- Modal content --> */}
        <div className="relative h-full bg-white shadow dark:bg-gray-700">
          {/* <!-- Modal header --> */}
          <div className="sticky top-0 flex items-center justify-between px-4 py-6 bg-white border-b border-grey200">
            <h3 className="text-xl font-bold text-black-300">
              {t("productdetails.MoreSellOpts")}
            </h3>
            <button
              type="button"
              data-modal-target="rtl-modal"
              data-modal-hide="rtl-modal"
              onClick={() => setSellerOptionModal(false)}
            >
              <img src="/img/modal-x.svg" alt="modal-x" />
            </button>
          </div>
          <div className="sticky p-4 pb-0 bg-white top-61">
            <div className="flex justify-start items-center space-x-3 rtl:space-x-[unset] pb-4 border-grey200 border-b">
              <div className="h-16 rtl:ml-3 min-w-20">
                <img
                  src={
                    imageUrl +
                    "?path=" +
                    defaultImage?.containerName +
                    "&name=" +
                    defaultImage?.image +
                    "&width=600&height=500"
                  }
                  alt={defaultImage?.image}
                  onError={({ currentTarget }) => {
                    currentTarget.onerror = null;
                    currentTarget.src = "/img/no-image.png";
                    currentTarget.alt = "no-image";
                  }}
                  className="w-auto h-full max-w-full mx-auto rounded"
                />
              </div>
              <h3 className="font-normal text-black-300 text-base line-clamp-2 leading-[21px]">
                {productDetail?.productNameTrans
                  ? productDetail?.productNameTrans
                  : productDetail?.name}
              </h3>
            </div>
          </div>
          {/* <!-- Modal body --> */}
          {vendorList?.map((vendor, index) => (
            <div className="p-4 overflow-auto bg-white" key={`vendor${index}`}>
              <div className="flex flex-col pb-4 mb-4 space-y-3 border-b border-grey200">
                <>
                  <div className="flex items-center justify-between">
                    <h5 className="text-lg font-bold leading-8 text-black-300">
                      <CustomCurrencyFormatter price={vendor.price} />
                    </h5>
                    <button
                      disabled={vendor?.quantity > 0 ? false : true}
                      onClick={(e) => handleAddToCart(vendor)}
                      className="flex justify-center items-center bg-orange-500 px-6 rounded h-[41px] font-semibold text-white text-sm btn"
                    >
                      {t("wishlist.addtocart")}
                    </button>
                  </div>
                  <div className="flex items-center space-x-2.5 rtl:space-x-[unset]">
                    <div className="rtl:ml-2.5 w-38 h-38">
                      <img
                        src={
                          imageUrl +
                          "?path=" +
                          vendor?.companyLogoPath +
                          "&name=" +
                          vendor?.companyLogo +
                          "&width=60&height=60"
                        }
                        alt={vendor?.companyName}
                        onError={({ currentTarget }) => {
                          currentTarget.onerror = null;
                          currentTarget.src = "/img/user1.jpg";
                          currentTarget.alt = "user";
                        }}
                        className="w-full h-full"
                      />
                    </div>
                    <div className="flex flex-col space-y-1.5">
                      <Link
                        prefetch={false}
                        href={`/shop/${vendor?.displayNameUrl}`}
                        className="text-sm font-semibold text-black-300 leading-17"
                      >
                        {vendor?.companyName}
                      </Link>
                      <p className="flex space-x-2 rtl:space-x-[unset] font-normal text-black-300 text-xs leading-15">
                        <span className="rtl:ml-2">
                          {vendor?.companyCountryName},
                        </span>
                        <span className="text-xs font-normal text-black-300 leading-15">
                          {t("myprofile.GST")} {vendor.companyTaxNumber}
                        </span>
                      </p>
                    </div>
                  </div>
                </>
              </div>
            </div>
          ))}
        </div>
      </div>
    </>
  );
}
