"use client";

//React and Next
import { useEffect, useState } from "react";

//API
import { getVariants } from "./VariantApi";

//Component
import { useTranslation } from "react-i18next";

export default function ProductVariant({
  rawProduct,
  slug,
  setIsProductDetailLoading,
  productDetail,
  setProductDetail,
}) {
  const { t } = useTranslation();

  const [variantData, setVariantData] = useState([]);
  const [selectedVariant, setSelectedVariant] = useState([]);
  const [combinationVariant, setCombinationVariant] = useState([]);

  useEffect(() => {
    if (rawProduct?.isSimplified == 0) {
      productVariantApi();
    }
  }, [slug]);

  // Product variant detail API
  const productVariantApi = async () => {
    setIsProductDetailLoading(true);
    const result = await getVariants(slug);
    if (result?.status == 1) {
      handlingVariantProduct(result?.data);
    } else {
      handlingVariantProduct({});
    }
    setIsProductDetailLoading(false);
  };

  const handlingVariantProduct = (productData) => {
    setVariantData(productData);
    let selectedvariant = [];
    if (productData?.productvarientList.length) {
      Object?.values(
        productData?.productvarientList[0]?.productVarientOption,
      ).map((key) => selectedvariant.push(key));
    }

    priceAndImageChange(productData?.productvarientList, selectedvariant);
    disableCombination(
      productData?.productvarientList,
      productData?.productVarient,
      selectedvariant,
    );
    setSelectedVariant(selectedvariant);
  };

  const handleVariantChange = (variant, variantData, index) => {
    const select = [...selectedVariant];
    select[index] = variant?.id;
    setSelectedVariant(select);
    priceAndImageChange(variantData.productvarientList, select);
    disableCombination(
      variantData.productvarientList,
      variantData.productVarient,
      select,
    );
  };

  const priceAndImageChange = (productvarientList, selectedVariant) => {
    const selectedCombination = productvarientList.find((data) => {
      if (
        selectedVariant.every((value) =>
          data.productVarientOption.includes(value),
        )
      ) {
        return data;
      }
    });

    if (selectedCombination) {
      let variantPrice = {
        ...rawProduct,
        orderProductId: selectedCombination.orderProductId,
        buyed: selectedCombination.buyed,
        price: selectedCombination?.price,
        skuName: selectedCombination?.skuName,
        skuId: selectedCombination?.skuId,
        variantName: selectedCombination?.varientName,
        quantity: selectedCombination?.quantity,
        minQuantityAllowedCart: selectedCombination?.minQuantityAllowedCart,
        maxQuantityAllowedCart: selectedCombination?.maxQuantityAllowedCart,
        enableBackOrders: selectedCombination?.enableBackOrders,
        availableBackOrderStock: selectedCombination?.availableBackOrderStock,
        outOfStockThreshold: selectedCombination?.outOfStockThreshold,
        pricerefer: "",
        flag: "",
        wishListStatus: productDetail?.wishListStatus,
      };

      if (
        (selectedCombination?.specialPrice?.length > 0 &&
          selectedCombination?.discountPrice?.length > 0) ||
        selectedCombination?.specialPrice?.length > 0
      ) {
        variantPrice.pricerefer = selectedCombination?.specialPrice[0]?.price;
        variantPrice.flag = 1;
      } else if (selectedCombination?.discountPrice?.length > 0) {
        variantPrice.pricerefer = selectedCombination?.discountPrice[0]?.price;
        variantPrice.flag = 0;
      }

      if (selectedCombination?.optionImage?.length > 0) {
        const img = selectedCombination?.optionImage?.[0];
        const variantImage = { ...img, defaultImage: 1, mediaType: 1 };

        const filtered = rawProduct.productOriginalImage.filter(
          (img) => img?.image != variantImage?.image,
        );
        const images = filtered.map((product) => {
          return {
            ...product,
            defaultImage: 0,
          };
        });

        const imageList = [variantImage, ...images];

        variantPrice.productImage = imageList;
        variantPrice.productOriginalImage = imageList;
      }

      setProductDetail(variantPrice);
    }
  };

  const disableCombination = (
    productvarientList,
    productVarient,
    selectedvariant,
  ) => {
    let combinationvariant = [];

    if (productVarient?.length == 1) {
      productvarientList.map((data) => {
        Object.values(data.productVarientOption).map((key) =>
          combinationvariant.push(key),
        );
      });
    } else if (productVarient?.length == 2) {
      productvarientList.map((data) => {
        data.productVarientOption.some((id) => selectedvariant.includes(id)) &&
          Object.values(data.productVarientOption).map((key) =>
            combinationvariant.push(key),
          );
      });
    } else {
      const generateCombinations = (arr, k) => {
        const results = [];

        const combine = (arr, temp, start, k) => {
          if (k === 0) {
            results.push([...temp]);
            return;
          }

          for (let i = start; i < arr.length; i++) {
            temp.push(arr[i]);
            combine(arr, temp, i + 1, k - 1);
            temp.pop();
          }
        };

        combine(arr, [], 0, k);
        return results;
      };

      const newCombinations = generateCombinations(selectedvariant, 2);
      for (let data3 = 0; data3 <= productvarientList.length; data3++) {
        for (let index = 0; index < newCombinations.length; index++) {
          newCombinations[index].every((value) =>
            productvarientList[data3]?.productVarientOption.includes(value),
          ) &&
            Object.values(productvarientList[data3]?.productVarientOption).map(
              (key) => combinationvariant.push(key),
            );
        }
      }
    }

    setCombinationVariant(combinationvariant);
  };

  return (
    variantData?.productVarient &&
    variantData?.productvarientList.length > 0 && (
      <div className="mb-8 flex flex-col space-y-4">
        <h4 className="text-black-100 text-base font-black leading-5">
          {t("productdetails.Options")}
        </h4>
        {variantData?.productVarient?.map((variantTitle, index) => (
          <div className="flex flex-col space-y-3" key={`variant${index}`}>
            <h5 className="text-black-300 text-sm font-bold capitalize">
              {variantTitle?.variantNameTrans
                ? variantTitle?.variantNameTrans
                : variantTitle?.name}
            </h5>
            <div className="/* mobile: 2 columns */ /* small screen: 3 columns */ /* large screen: 4 columns */ grid grid-cols-2 gap-4 sm:grid-cols-3 md:gap-6 lg:grid-cols-4">
              {variantTitle?.variantValue?.map((variant, i) => (
                <div
                  className="flex items-center space-x-2 rtl:space-x-[unset]"
                  key={`variantValue${i}`}
                >
                  <input
                    id={variant?.id}
                    defaultChecked={selectedVariant?.find(
                      (item) => item === variant?.id,
                    )}
                    name={index}
                    type="radio"
                    disabled={
                      combinationVariant.indexOf(variant?.id) !== -1
                        ? false
                        : true
                    }
                    onChange={(e) =>
                      handleVariantChange(variant, variantData, index)
                    }
                    className={`h-20px w-20px radio-black rounded-full text-black focus:ring-1 focus:ring-transparent rtl:ml-2 ${
                      combinationVariant.indexOf(variant?.id) !== -1
                        ? "cursor-pointer"
                        : "cursor-not-allowed opacity-50"
                    }`}
                  />
                  <label
                    htmlFor={variant?.id}
                    className={`text-black-300 text-sm font-normal capitalize ${
                      combinationVariant.indexOf(variant?.id) !== -1
                        ? "cursor-pointer"
                        : "cursor-not-allowed opacity-50"
                    }`}
                  >
                    {variant?.valueTrans ? variant?.valueTrans : variant?.value}
                  </label>
                </div>
              ))}
            </div>
          </div>
        ))}
      </div>
    )
  );
}
