"use client";

// React and Next
import { useEffect, useState } from "react";
import { usePathname } from "next/navigation";

// API
import {
  CommonProductVendorCountApi,
  CommonProductVendorListApi,
} from "./CommonProductApi";

// Components
import CommonProductVendorList from "./CommonProductVendorList";
import CustomCurrencyFormatter from "@/Components/CustomCurrencyFormatter";
import { useTranslation } from "react-i18next";

export default function CommonProductVendorCount({ token, productDetail }) {
  const pathname = usePathname();
  const slug = pathname.split("/").at(-1);

  const [vendorCount, setVendorCount] = useState(0);
  const [vendorList, setVendorList] = useState([]);
  const [defaultImage, setDefaultImage] = useState();
  const { t } = useTranslation();
  const [selleroptionModal, setSellerOptionModal] = useState(false);

  // get common product vendor count api call function
  useEffect(() => {
    if (productDetail?.productId) {
      CommonProductVendorCountApi(productDetail?.productId, setVendorCount);
    }
  }, [productDetail?.productId]);

  // get common product vendor list api call function
  useEffect(() => {
    if (slug) {
      CommonProductVendorListApi(slug, setVendorList);
    }
  }, [slug]);

  // product image bind
  useEffect(() => {
    if (productDetail) {
      const defaultImg = productDetail?.productOriginalImage?.find(
        (image) => image.defaultImage == 1,
      );
      setDefaultImage(defaultImg);
    }
  }, [productDetail]);

  useEffect(() => {
    if (selleroptionModal) {
      if (typeof window !== "undefined") {
        document.body.classList.add("overflow-hidden");
      }
    } else {
      if (typeof window !== "undefined") {
        document.body.classList.remove("overflow-hidden");
      }
    }
  }, [selleroptionModal]);

  return (
    <>
      {/* vendor count  */}
      {vendorCount?.vendorCount && vendorCount?.minimumPrice && (
        <>
          <h3 className="mb-3 text-base font-black text-black">
            {t("productdetails.MoreSellOpts")} ({vendorCount.vendorCount})
          </h3>
          <div className="mb-6 flex flex-row items-center gap-4">
            <p className="text-black-300 text-sm font-normal">
              {" "}
              {t("productdetails.StartFrm")}{" "}
              <CustomCurrencyFormatter price={vendorCount.minimumPrice} />
            </p>{" "}
            <a
              role="button"
              data-modal-target="rtl-modal"
              data-modal-toggle="rtl-modal"
              className="text-link relative cursor-pointer text-sm font-normal underline before:bottom-0"
              onClick={() => setSellerOptionModal(true)}
            >
              {t("productdetails.CmpAllSls")}
            </a>
          </div>
        </>
      )}

      {/* <!-- Main modal --> */}
      <CommonProductVendorList
        selleroptionModal={selleroptionModal}
        setSellerOptionModal={setSellerOptionModal}
        productDetail={productDetail}
        vendorList={vendorList}
        defaultImage={defaultImage}
        token={token}
      />
    </>
  );
}
