"use client";

// React and Next
import { useEffect, useRef, useState } from "react";

//3rd Party
import {
  GoogleMap,
  StandaloneSearchBox,
  LoadScript,
  MarkerF,
} from "@react-google-maps/api";
import Geocode from "react-geocode";

//API
import {
  CountryIdgetApi,
  CurrentGoogleMapApi,
  GoogleMapApi,
  GoogleMapKey,
} from "./googleMapApi";

//Components
import { useTranslation } from "react-i18next";

// Declare the libraries array outside the component to avoid reloading
const libraries = ["places"];

export default function GoogleMaps(props) {
  const { t } = useTranslation();
  const [currentPosition, setCurrentPosition] = useState({});
  const [citypin, setCitypin] = useState();
  const [check, setCheck] = useState(false);
  const [svalue, setSvalue] = useState("");
  const [current, setCurrent] = useState("");
  const [drag, setDrag] = useState(0);
  const [mapShow, setMapShow] = useState(false);

  ///----> current location
  const [currsta, setCurrSta] = useState("");
  const [currcity, setCurrCity] = useState("");
  const [curradd, setCurrAdd] = useState("");
  const [currpost, setCurrPost] = useState("");
  const [stateval, setStateval] = useState("");

  ///----> search location
  const [state, setState] = useState("");
  const [cityval, setCityval] = useState("");
  const [address, setAddress] = useState("");
  const [postcode, setPostcode] = useState("");
  const [statevalue, setStatevalue] = useState("");

  ///----> key
  const [key, setKey] = useState("");
  const [keyval, setKeyval] = useState(false);

  ///----> country id
  const [country, setCountry] = useState("");
  const [countryId, setCounrtyId] = useState("");
  const token = "";

  useEffect(() => {
    GoogleMapKey(setKey, setKeyval);
  }, []);

  useEffect(() => {
    if (country) {
      CountryIdgetApi(country, setCounrtyId);
    }
  }, [country]);

  const onPlacesChanged = () => {
    if (countryId !== "" && countryId !== undefined && countryId !== null) {
      if (svalue === "") {
        CurrentGoogleMapApi(
          setCitypin,
          setCheck,
          stateval,
          currcity,
          curradd,
          currpost,
          token,
          countryId
        );
      } else {
        GoogleMapApi(
          setCitypin,
          setCheck,
          statevalue,
          cityval,
          address,
          postcode,
          token,
          countryId
        );
      }
    } else {
      setMapShow(!mapShow);
    }
  };

  Geocode.setApiKey(key);

  const searchchange = (e) => {
    setStatevalue("");
    setCityval("");
    setAddress("");
    setPostcode("");
    setStateval("");
    setCurrAdd("");
    setCurrCity("");
    setCurrPost("");
    setCurrent("");
    setCountry("");
    setCounrtyId("");
  };

  const onMarkerDragEnd = (e) => {
    const lat = e.latLng.lat();
    const lng = e.latLng.lng();
    setCurrentPosition({ lat, lng });
    setSvalue("");
    setDrag(drag + 1);
  };

  const success = (position) => {
    const currentPosition = {
      lat: position.coords.latitude,
      lng: position.coords.longitude,
    };

    fetch(
      `https://maps.googleapis.com/maps/api/geocode/json?latlng=${currentPosition.lat},${currentPosition.lng}&key=${key}`
    )
      .then((res) => res.json())
      .then((data) => {});

    setCurrentPosition(currentPosition);
  };

  useEffect(() => {
    navigator.geolocation.getCurrentPosition(success);
  }, []);

  const handlePlaceChanged = () => {
    const [place] = inputRef.current.getPlaces();
    if (place) {
      setSvalue(place.formatted_address);
      let lat = place.geometry.location.lat();
      let lng = place.geometry.location.lng();
      setCurrentPosition({ lat, lng });
    }
  };

  const inputRef = useRef();

  useEffect(() => {
    if (svalue) {
      let value = svalue.split(",");
      let address = value.reverse();
      let country = address[0].trimStart();
      let statevalue = address[1];
      let cityvalue = address[2];

      const arr1 = [];
      address.forEach((value, index) => {
        if (index >= 3) {
          arr1.push(value);
        }
      });
      const address1 = arr1.reverse().join(",");
      let addressone = address[3] === undefined ? address[2] : address1;
      setCountry(country);
      setState(statevalue);
      setCityval(cityvalue);
      setAddress(addressone);
    }
  }, [svalue]);

  useEffect(() => {
    if (state) {
      var str = state;
      var patt1 = /[0-9]/g;
      var patt2 = /[a-zA-Z]/g;
      var letters = str.match(patt2);
      var digits = str.match(patt1);
      if (digits == null) {
      } else {
        let pin = digits.join("");
      }

      let sts = letters.join("");
      setStatevalue(sts);
      setPostcode(pin);
      if (pin == undefined) {
        setPostcode("");
      } else {
        setPostcode(pin);
      }
    }
  }, [state]);

  useEffect(() => {
    if (currentPosition.lat && currentPosition.lng) {
      Geocode.fromLatLng(currentPosition.lat, currentPosition.lng).then(
        (response) => {
          const addressss = response.results[0].formatted_address;
          setCurrent(response.results[0].formatted_address);
        },
        (error) => {
          console.error(error);
        }
      );
    }
  }, [drag]);

  useEffect(() => {
    if (current) {
      let arr = [];
      let curvalue = current.split(",");
      let currev = curvalue.reverse();
      currev.forEach((val, i) => {
        if (i >= 3) {
          arr.push(val);
        }
      });

      let curcountry = currev[0].trimStart();
      let curstate = currev[1];
      let curdist = currev[2];
      let add = arr.reverse().join(",");
      let add1 = currev[3] === undefined ? currev[2] : add;
      setCountry(curcountry);
      setCurrSta(curstate);
      setCurrCity(curdist);
      setCurrAdd(add1);
    }
  }, [current]);

  useEffect(() => {
    if (currsta) {
      var str = currsta;
      var patt1 = /[0-9]/g;
      var patt2 = /[a-zA-Z]/g;
      var letters = str.match(patt2);
      var digits = str.match(patt1);
      if (digits == null) {
      } else {
        let pin = digits.join("");
      }

      let sts = letters.join("");
      setStateval(sts);
      if (pin == undefined) {
        setCurrPost("");
      } else {
        setCurrPost(pin);
      }
    }
  }, [currsta]);

  const closeOpenMap = () => {
    if (keyval) {
      setMapShow(!mapShow);
      document.body.classList.toggle("overflow-hidden", !mapShow);
    }
  };

  return (
    <>
      <div>
        <div
          className="flex cursor-pointer whitespace-nowrap"
          onClick={closeOpenMap}
        >
          <img src="/img/pin.svg" alt="pin" className="min-w-[18px]" />
          <p className="hidden text-sm font-semibold leading-6 max-sm:hidden sm:block text-black-300 sm:whitespace-nowrap">
            {t("homepage.Delivery")}
          </p>
        </div>
        {keyval ? (
          check !== true ? (
            <p className="text-sm font-semibold leading-6 max-sm:hidden text-black-300 sm:whitespace-nowrap">
              <a
                onClick={closeOpenMap}
                role="button"
                className="cursor-pointer"
              >
                {t("homepage.ChLocation")}
              </a>
            </p>
          ) : (
            <p className="text-sm font-semibold leading-6 max-sm:hidden text-black-300 sm:whitespace-nowrap">
              <a
                onClick={closeOpenMap}
                role="button"
                className="cursor-pointer"
              >
                {citypin.city} {citypin.postcode}
              </a>
            </p>
          )
        ) : (
          <p className="text-sm font-semibold leading-6 max-sm:hidden text-black-300 sm:whitespace-nowrap">
            <a className="cursor-default" role="button">
              {t("homepage.ChLocation")}
            </a>
          </p>
        )}
      </div>
      {keyval && (
        <div
          id="default-modal"
          tabIndex="-1"
          aria-hidden="true"
          className={`${
            mapShow ? "flex z-[55] bg-light-modal" : "hidden"
          } overflow-y-auto overflow-x-hidden px-4 fixed top-0 right-0 m-0 left-0 z-50 justify-center h-full items-center w-full md:inset-0 max-h-full`}
        >
          <div className="relative p-[16px_32px] max-lg:p-[32px_0_0_0] w-full max-w-[962px] max-h-full">
            <button
              onClick={closeOpenMap}
              className="absolute top-0 right-0 lg:top-6"
            >
              <img src="/img/input-close-white.svg" alt="input-close-white" />
            </button>
            <div className="relative bg-white rounded-lg shadow dark:bg-gray-700">
              <div className="p-4 space-y-4 md:p-5">
                {keyval ? (
                  <LoadScript googleMapsApiKey={key} libraries={libraries}>
                    <GoogleMap
                      google={props.google}
                      mapContainerStyle={{ maxWidth: "100%", height: "500px" }}
                      center={currentPosition}
                      zoom={12}
                    >
                      <MarkerF
                        style={{ backgroundColor: "red", color: "green" }}
                        position={currentPosition}
                        onDragEnd={(e) => onMarkerDragEnd(e)}
                        draggable={true}
                      />
                      <StandaloneSearchBox
                        onLoad={(ref) => (inputRef.current = ref)}
                        onPlacesChanged={handlePlaceChanged}
                      >
                        <div className="absolute grid gap-4 p-6 grid-cols-1fr-240">
                          <input
                            type="text"
                            value={svalue}
                            placeholder={t("homepage.SLocation")}
                            onChange={(e) => searchchange(e)}
                          />
                          <button
                            onClick={onPlacesChanged}
                            className="bg-[var(--color)] rounded-md h-12 font-medium text-white text-base"
                          >
                            {t("homepage.CoLocation")}
                          </button>
                        </div>
                      </StandaloneSearchBox>
                    </GoogleMap>
                  </LoadScript>
                ) : (
                  <div></div>
                )}
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
}
