import { facebookAppId } from "@/utilities/url";

// Initialize the Facebook SDK
export const initFacebookSdk = () => {
  return new Promise((resolve, reject) => {
    // Load the Facebook SDK asynchronously
    (function (d, s, id) {
      var js,
        fjs = d.getElementsByTagName(s)[0];
      if (d.getElementById(id)) return;
      js = d.createElement(s);
      js.id = id;
      js.src = "https://connect.facebook.net/en_US/sdk.js";
      fjs.parentNode.insertBefore(js, fjs);
    })(document, "script", "facebook-jssdk");

    // Initialize the Facebook SDK
    window.fbAsyncInit = () => {
      window.FB.init({
        appId: facebookAppId,
        cookie: true,
        xfbml: true,
        version: "v16.0",
      });
      resolve();
    };
  });
};

// Get the Facebook login status
export const getFacebookLoginStatus = () => {
  return new Promise((resolve, reject) => {
    window.FB.getLoginStatus((response) => {
      if (response.status === "connected") {
        // User is logged in and has authorized the app
        resolve(response);
      } else if (response.status === "not_authorized") {
        // User is logged in but has not authorized the app
        reject(new Error("User has not authorized the app"));
      } else {
        // User is not logged in
        reject(new Error("User is not logged in"));
      }
    });
  });
};

// Perform a Facebook login
export const fbLogin = () => {
  return new Promise((resolve, reject) => {
    window.FB.login(
      (response) => {
        if (response.status === "connected") {
          // User is logged in and has authorized the app
          resolve(response);
        } else {
          // User cancelled the login or did not authorize the app
          reject(
            new Error("User cancelled the login or did not authorize the app")
          );
        }
      },
      { scope: "email" }
    ); // Request the email permission
  });
};
