"use client";

// React and Next
import { useEffect, useState } from "react";
import Link from "next/link";

// API
import { blogslist, getBlogCount } from "./blogs";

// Utils
import { imageUrl } from "@/utilities/url";

// 3rd Party
import moment from "moment";

// Components
import { useRouter, useSearchParams } from "next/navigation";
import Pagination from "./Pagination";
import { useTranslation } from "react-i18next";

const Blogslist = () => {
  const router = useRouter();
  const searchParams = useSearchParams();
  const { t } = useTranslation();
  const [bloglist, setBloglist] = useState();
  const [loader, setLoader] = useState(false);
  const [totalPages, setTotalPages] = useState(0);
  const [blogCount, setBlogCount] = useState(0);
  // Get pagination params from query string
  const offset = parseInt(searchParams.get("offset") || "0");
  const limit = parseInt(searchParams.get("limit") || "9");

  //blog count api call functionality
  const handleBlogCount = async () => {
    try {
      const res = await getBlogCount();
      if (res?.status === 1) {
        setBlogCount(res.data || 0);
        setTotalPages(Math.ceil((res.data || 0) / limit));
      }
    } catch (error) {
      console.error("Error fetching blog count:", error);
    }
  };
  //bloglist api call functionality
  const handleBlogList = async () => {
    try {
      setLoader(false);

      const res = await blogslist(limit, offset);
      if (res?.status === 1) {
        setBloglist(res.data);
      }
    } catch (error) {
      console.error("Error fetching blog list:", error);
    } finally {
      setLoader(true);
    }
  };

  // Fetch blog count only on initial mount
  useEffect(() => {
    handleBlogCount();
  }, []);

  // Fetch bloglist when pagination changes
  useEffect(() => {
    handleBlogList();
  }, [offset, limit]);

  // Handle pagination click
  const handlePaginationClick = (event) => {
    const selectedPage = event.selected;
    const newOffset = selectedPage * limit;

    // Update URL with new offset
    router.push(`?offset=${newOffset}&limit=${limit}`);
  };

  return (
    <>
      {/* skeleton loader */}
      {loader == false && (
        <div role="status" className="animate-pulse">
          <main className="m-auto flex min-h-screen max-w-screen-2xl">
            <div className="w-full p-4 sm:p-10">
              {/* Featured Blog Skeleton */}
              <div className="border-gray grid grid-cols-1 items-center gap-10 border-b pb-10 sm:grid-cols-2 sm:gap-24">
                <div className="space-y-6">
                  <div className="h-10 w-3/4 animate-pulse rounded bg-gray-200"></div>
                  <div className="space-y-3">
                    <div className="h-4 w-full animate-pulse rounded bg-gray-200"></div>
                    <div className="h-4 w-5/6 animate-pulse rounded bg-gray-200"></div>
                    <div className="h-4 w-2/3 animate-pulse rounded bg-gray-200"></div>
                  </div>
                </div>

                <div className="h-auto w-full">
                  <div className="h-[382px] w-full animate-pulse rounded-lg bg-gray-200"></div>
                </div>
              </div>

              {/* Blog Cards Grid Skeleton */}
              <div className="mt-12 grid grid-cols-1 gap-6 gap-y-12 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4">
                {[...Array(8)].map((_, index) => (
                  <div key={index} className="flex flex-col space-y-3">
                    <div className="h-60 w-full animate-pulse rounded-lg bg-gray-200"></div>

                    <div className="space-y-3">
                      <div className="h-3 w-20 animate-pulse rounded bg-gray-200"></div>
                      <div className="h-6 w-full animate-pulse rounded bg-gray-200"></div>
                      <div className="flex space-x-2.5">
                        <div className="h-3 w-16 animate-pulse rounded bg-gray-200"></div>
                        <div className="h-3 w-24 animate-pulse rounded bg-gray-200"></div>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </main>
        </div>
      )}

      {/* data binding */}
      {loader == true && (
        <main className="m-auto flex min-h-screen max-w-screen-2xl">
          {bloglist?.featuredPost?.title ? (
            <div className="p-4 sm:p-10">
              <div className="border-gray grid grid-cols-1 items-center gap-10 border-b pb-10 sm:grid-cols-2 sm:gap-24">
                <Link
                  prefetch={false}
                  href={`/blog/${bloglist?.featuredPost?.blogSlug}`}
                >
                  <div>
                    <h3 className="text-black-300 mb-5 text-5xl font-black">
                      {bloglist?.featuredPost?.blogTranslation?.title
                        ? bloglist?.featuredPost?.blogTranslation?.title
                        : bloglist?.featuredPost?.title}
                    </h3>
                    {/* Backup Style */}
                    {/* className="text-xl font-normal text-black-300 list-default bullet line" */}
                    <div
                      className="text-black-300 list-default bullet line editor-style"
                      dangerouslySetInnerHTML={{
                        __html: bloglist?.featuredPost?.blogTranslation
                          ?.description
                          ? bloglist?.featuredPost?.blogTranslation?.description
                          : bloglist?.featuredPost?.description
                              .replaceAll("&amp;", "&")
                              .replaceAll("&lt;", "<")
                              .replaceAll("&gt;", ">")
                              .replaceAll("&quot;", '"')
                              .replaceAll("&#39;", "'")
                              .replaceAll("&sbquo;", "")
                              .replaceAll("&#61;", "=")
                              .replaceAll("&#45;", "-")
                              .replaceAll("&hellip;", "")
                              .replaceAll("&commat;", "@")
                              .replaceAll("&copy;", "")
                              .replaceAll("&#35;", "#")
                              .replaceAll("&ldquo;", "")
                              .replaceAll("&rsquo;", "")
                              .replaceAll("&lsquo;", "")
                              .replaceAll("&trade;", "")
                              .replaceAll("&reg;", "")
                              .replaceAll("&ndash;", "")
                              .replaceAll("&eacute;", "")
                              .replaceAll("&euro;", "")
                              .replaceAll("&pound;", "")
                              .replace(
                                "&lt;/p&gt;&amp;&#35;10;&amp;&#35;10;&lt;p&gt;",
                                "",
                              ),
                      }}
                    />
                  </div>
                </Link>
                <Link
                  prefetch={false}
                  href={`/blog/${bloglist?.featuredPost?.blogSlug}`}
                >
                  <div className="h-auto w-full">
                    <img
                      src={
                        imageUrl +
                        "?path=" +
                        bloglist?.featuredPost?.imagePath +
                        "&name=" +
                        bloglist?.featuredPost?.image +
                        "&width=680&height=382"
                      }
                      alt={bloglist?.featuredPost?.title}
                      className="imgas rounded-lg"
                    />
                  </div>
                </Link>
              </div>
              <div className="mt-12 grid grid-cols-1 gap-6 gap-y-12 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4">
                {bloglist?.results?.map((data, index) => (
                  <Link
                    prefetch={false}
                    key={index}
                    href={`/blog/${data?.blogSlug}`}
                    className="flex flex-col space-y-3"
                  >
                    <div>
                      <img
                        src={
                          imageUrl +
                          "?path=" +
                          data?.imagePath +
                          "&name=" +
                          data?.image +
                          "&width=600&height=600"
                        }
                        alt={data?.title}
                        className="imgas2 rounded-lg"
                      />
                    </div>
                    <div>
                      <div className="mb-2 flex items-center space-x-1.5 rtl:space-x-[unset]">
                        <img
                          src="/img/right-arrow.svg"
                          alt="right-arrow"
                          className="rtl:ml-1.5"
                        />
                        <p className="text-black-300 text-xs font-medium">
                          {data?.categoryName}
                        </p>
                      </div>
                      <span
                        role="button"
                        className="text-black-300 hei-96 mb-2 line-clamp-3 cursor-pointer text-2xl font-black"
                      >
                        {data?.blogTranslation?.title
                          ? data?.blogTranslation?.title
                          : data?.title}
                      </span>
                      <div className="flex space-x-2.5 rtl:space-x-[unset]">
                        <p className="text-black-300 rtl:spave-x-[unset] flex space-x-0.5 text-xs font-medium rtl:ml-2.5">
                          <span className="rtl:ml-0.5">
                            {t("myprofile.by")}&nbsp;
                          </span>
                          <span className="text-black-300 cursor-pointer text-xs font-medium underline hover:text-blue-500 hover:no-underline">
                            {data?.createdByName}
                          </span>
                        </p>
                        <span className="text-black-300 text-xs font-normal italic">
                          {moment(data?.createdDate).format("MMMM DD, YYYY")}
                        </span>
                      </div>
                    </div>
                  </Link>
                ))}
              </div>
            </div>
          ) : (
            <div className="mx-auto mt-32 min-h-[400vh]">
              <div className="flex flex-col items-center justify-center gap-8 p-4 text-center sm:flex-row">
                <img src="/img/no-data.svg" alt="no-data" />
                <div>
                  <h5 className="text-black-300 mb-1 text-xl font-black">
                    {t("myprofile.NoBlogs")}
                  </h5>
                </div>
              </div>
            </div>
          )}
        </main>
      )}

      {loader && totalPages > 1 && (
        <div className="mb-8 mt-12 flex justify-center p-4">
          <Pagination
            nPages1={totalPages}
            currentPage={Math.floor(offset / limit)}
            onPageChange={handlePaginationClick}
          />
        </div>
      )}
    </>
  );
};
export default Blogslist;
