"use client";

// React and Next
import { useEffect, useState } from "react";
import { usePathname } from "next/navigation";

// API
import { ApplyCouponApi } from "./CouponApi";

// Utils
import { PriceCalculate, PriceTaxCalculate } from "@/utilities/PriceCalculate";
import { GetCustomerProfile } from "@/utilities/localStorage";

// 3rd Party
// Redux
import { useDispatch, useSelector } from "react-redux";
import {
  checkoutCouponData,
  couponApplied,
  couponCode,
  couponDiscountValue,
  enteredCouponCode,
} from "@/redux/slices/checkout";

// Components
import ToastMessage from "@/Components/ToastMessage";
import CustomCurrencyFormatter from "@/Components/CustomCurrencyFormatter";
import { useTranslation } from "react-i18next";

export default function CouponApply({ token, valid }) {
  const pathname = usePathname();
  const [couponError, setCouponError] = useState("");

  const profile = GetCustomerProfile();
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const enterCouponCode = useSelector(
    (state) => state?.CheckoutRedux?.enteredCouponCode
  );
  const couponApplyed = useSelector(
    (state) => state?.CheckoutRedux?.couponApplied
  );
  const couponcode = useSelector((state) => state?.CheckoutRedux?.couponCode);

  const couponOfferValue = useSelector(
    (state) => state?.CheckoutRedux?.couponDiscountValue
  );

  useEffect(() => {
    if (pathname == "/checkout" || pathname == "/stock-checkout") {
      CartProduct();
      dispatch(checkoutCouponData(""));
      dispatch(couponDiscountValue(0));
      dispatch(couponCode(""));
      dispatch(enteredCouponCode(""));
      dispatch(couponApplied(false));
    }
  }, []);

  //  get product in cart function
  const CartProduct = async () => {
    let Product = (await JSON.parse(localStorage.getItem("cartItems"))) || [];
    let productObj = {};
    let CouponProduts = [];
    Product?.forEach((products) => {
      const price =
        products?.priceGroup == 0
          ? PriceCalculate(
              products.price,
              products.pricerefer,
              products.flag,
              products.taxType,
              products.taxValue
            )
          : PriceTaxCalculate(
              products?.price,
              products?.taxType,
              products?.taxValue
            );
      productObj = {
        productId: products.productId,
        productPrice: price,
        quantity: products.productCount,
        skuName: products.skuName,
        total: price * products.productCount,
      };
      CouponProduts.push(productObj);
    });
    return CouponProduts;
  };

  // apply coupon function
  const handleApplyCoupon = async () => {
    if (enterCouponCode !== "") {
      const productList = await CartProduct();
      if (!couponApplyed) {
        const applycoupon = await ApplyCouponApi(
          enterCouponCode,
          profile?.email,
          productList
        );
        if (applycoupon?.status == 1) {
          dispatch(checkoutCouponData(applycoupon?.couponData));
          dispatch(couponDiscountValue(applycoupon?.data?.grandDiscountAmount));
          dispatch(couponCode(applycoupon?.data?.couponCode));
          dispatch(couponApplied(true));
          ToastMessage({ type: "success", message: applycoupon?.message });
        } else {
          ToastMessage({ type: "error", message: applycoupon?.message });
        }
      }
      setCouponError("");
    } else {
      setCouponError("Please enter coupon code");
    }
  };
  //  get coupon code
  const handleCouponChange = (value) => {
    if (value !== "") {
      setCouponError("");
    } else {
      setCouponError("Please enter coupon code");
    }
    // setCoupon(value)
    dispatch(enteredCouponCode(value));
  };

  const handleCouponRemve = () => {
    dispatch(checkoutCouponData(""));
    dispatch(couponDiscountValue(0));
    dispatch(couponCode(""));
    dispatch(couponApplied(false));
    dispatch(enteredCouponCode(""));
  };

  return couponApplyed == true ? (
    <div className="relative bg-[#F9FDF8] mt-[30px] p-[30px] border border-[#258113] border-solid rounded-[8px]">
      <button
        disabled={!valid}
        className={`top-[30px] right-[30px] absolute font-medium text-[#C40047] text-[14px] underline-offset-2 leading-[17px] ${
          valid && "hover:underline"
        }`}
        onClick={(e) => handleCouponRemve()}
      >
        {t("checkout.remove")}
      </button>
      <img
        src="/img/coupon.svg"
        alt="coupon"
        className="mb-[10px] w-[54px] h-[54px]"
      />
      <div className="flex items-center gap-2 mb-[8px]">
        <h3 className="font-semibold text-[#202124] text-[18px] leading-[22px]">
          {t("checkout.CouponApp")}
        </h3>
        <img src="img/coupon-applied.svg" alt="" />
      </div>
      <p className="mb-[16px] font-medium text-[#202124] text-[14px] leading-[20px]">
        {t("checkout.Code")} : {couponcode}
      </p>
      <p className="font-normal text-[#202124] text-[14px] leading-[20px]">
        {t("checkout.CouponSuccess")}{" "}
        <CustomCurrencyFormatter price={couponOfferValue} />{" "}
        {t("checkout.urOrd")}
      </p>
    </div>
  ) : (
    <div className="flex flex-col space-y-4 2xl:space-y-5 mt-[30px]">
      <div className="flex items-center space-x-2 rtl:space-x-[unset]">
        <div className="flex items-center justify-center w-8 h-8 rtl:ml-2 rounded-3xl">
          <img src="/img/coupon.svg" alt="stroke-rounded" />
        </div>
        <p className="font-medium text-[#212121] text-sm leading-[17px]">
          {t("productcontent.CpnCd")}
        </p>
      </div>
      <div className="relative flex flex-nowrap justify-start space-x-2 rtl:space-x-[unset]">
        <div
          className="relative w-auto rtl:ml-2 grow"
          data-twe-input-wrapper-init
        >
          <input
            disabled={!valid}
            type="text"
            className={`peer block bg-transparent p-[14px] border border-[#939190] border-solid rounded outline-none w-full h-11 font-normal text-[#212121] placeholder:text-[#939190] text-sm leading-[1.6] transition-all duration-200 ease-linear ${
              couponError != "" && "border-red-500"
            }`}
            value={enterCouponCode}
            onChange={(e) => handleCouponChange(e.target.value?.trimStart())}
            id="exampleFormControlInput1"
            placeholder={t("checkout.EntCode")}
          />
          {couponError != "" && (
            <p className="mt-1 text-xs font-normal text-red-invalid">
              {couponError}
            </p>
          )}

          {/* {couponApplyed == true &&
                            <button className='top-3.5 right-4 absolute w-4' onClick={(e) => handleCouponRemve()} >
                                <img
                                    src='/img/close-input.svg'
                                    alt='close-input'
                                />
                            </button>} */}
          {/* <label
                            htmlFor="exampleFormControlInput1"
                            className={
                                enterCouponCode == ""
                                    ? "pointer-events-none absolute left-3 top-2  mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-[#939190] text-sm font-normal transition-all duration-200 ease-out peer-focus:-translate-y-[1.01rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.8rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none  bg-white"
                                    : `translate-y-[-0.8rem] scale-[0.8] pointer-events-none absolute left-3 top-1 mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-[#939190] text-sm font-normal transition-all duration-200 ease-out peer-focus:-translate-y-[0.8rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.9rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-white text-red-valid `
                            }
                        >
                            {t("productcontent.EntCode")}
                        </label> */}
        </div>
        <button
          className={`bg-black px-3 cursor-default rounded-md h-11 font-medium text-white text-sm leading-4 grow-0 btn ${
            valid && "hover:bg-zinc-800 cursor-pointer"
          }`}
          disabled={
            token !== "" ? (couponApplyed === false ? false : true) : true
          }
          onClick={(e) => handleApplyCoupon()}
        >
          {/* {t("productcontent.Apply")} */}
          {t("checkout.discount")}
        </button>
      </div>
    </div>
  );
}
