"use client";
import axiosInstance from "@/api/interceptor"

// QuestionList api
export const getQuestionList = async (skuId, search) => {
  const data = await axiosInstance.get(
    `store-question-answer/question-list/${skuId}?keyword=${search}`
  );
  return data?.data;
};

// get Product Detail api
export const getProductDetailApi = async (slug) => {
  const data = await axiosInstance.get(`product-store/productdetail/${slug}`);
  return data?.data;
};

// report abuse reason list api
export async function getReasonApi() {
  const data = await axiosInstance.get(
    `store-question-answer/abuse-reason-list`
  );
  return data?.data;
}

// post your question api
export async function PostQuestionApi(question, productId, skuId) {
  const data = {
    question: question,
    productId: productId,
    skuId: skuId,
  };
  const result = await axiosInstance.post(
    `store-question-answer/add-question`,
    data
  );
  return result?.data;
}

/// answer like and dislike api
export async function LikeAndDisLikeAPi(answerId, type) {
  const data = {
    answerId: answerId,
    type: type,
  };
  const result = await axiosInstance.post(
    `store-question-answer/update-like-status`,
    data
  );
  return result?.data;
}

export async function ReportAbuseAPi(answerId, remark, reasonId) {
  const data = {
    answerId: answerId,
    remark: remark,
    reasonId: reasonId,
  };
  const result = await axiosInstance.post(
    `store-question-answer/add-report-abuse`,
    data
  );
  return result?.data;
}
