"use client"; // If using App Router
import { imageUrl } from "@/utilities/url";
import { useTranslation } from "react-i18next";

export default function SupportDetail_ImageAttachment_Modal({
  isOpen,
  setIsOpen,
  selected_Attachment,
}) {
  const closeModal = () => setIsOpen(false);
  const { t } = useTranslation();

  return (
    <div>
      {isOpen && (
        <div className="z-[999] fixed inset-0 flex flex-col justify-center items-center bg-white bg-opacity-60">
          <div className="flex justify-between items-center p-[20px_16px] border-b w-full">
            <p className="inline-flex flex-grow m-0 overflow-hidden font-bold text-[#202123] text-xl text-ellipsis whitespace-nowrap">
              {t("support.previewImage")}
            </p>

            <a
              role="button"
              onClick={closeModal}
              className="w-[18px] cursor-pointer"
            >
              <img src="/img/close.svg" alt="" />
            </a>
          </div>

          <div className="relative flex justify-center items-center bg-white p-4 w-full grow">
            <img
              src={
                imageUrl +
                "?path=" +
                selected_Attachment.filePath +
                "&name=" +
                selected_Attachment.fileName +
                "&width=500&height=500"
              }
              alt={selected_Attachment.fileName}
              className="max-w-full max-h-full object-contain"
            />
          </div>
        </div>
      )}
    </div>
  );
}
