"use client";

//React and Next
import { useEffect } from "react";

//Utils
import { GetAddonList } from "@/utilities/localStorage";


// 3rd Party
import { useRouter } from "nextjs-toploader/app";

//Components
import Breadcrumb from "@/Components/Breadcrumbs";
import Sidebar from "../SideBar";
import QuotationCardList from "./QuotationCardList";
import { useTranslation } from "react-i18next";
import Contactus from "@/Components/Contactus";

export default function QuotationDetail({ token }) {
  const addonList = GetAddonList();
  const { t } = useTranslation();
  const router = useRouter();

  useEffect(() => {
    if (addonList?.["product-quotation"]?.status == 0) {
      router.push("/");
    }
  }, []);

  const breadCrumb = [
    {
      text: t("Breadcrumb.home"),
      url: "/",
    },
    {
      text: t("Breadcrumb.accountSettings"),
      url: "/account-settings",
    },
    {
      text: t("Breadcrumb.quotationList"),
      url: "/account-settings/quotation-list",
    },
    {
      text: t("Breadcrumb.quotationDetails"),
      url: "javascript:void(0)",
    },
  ];
  
  return (
    addonList?.["product-quotation"]?.status == 1 && (
      <>
        <div className="mx-auto px-4 py-2.5 max-w-screen-2xl">
          <Breadcrumb breadCrumb={breadCrumb} />
          <main className="flex min-h-screen mx-auto mt-4">
            <Sidebar />
            <QuotationCardList />
          </main>
        </div>

        <Contactus token={token} />
      </>
    )
  );
}
