"use client";
//React and Next
import { useEffect, useState } from "react";
//Redux
import { useDispatch } from "react-redux";
//API
import { PriceGroupListApi } from "./PriceGroupApi";
//Utils
import { PriceTaxCalculate } from "@/utilities/PriceCalculate";
//Components
import { PriceCalculate, PricingGroupPriceCalculate } from "./priceCalculate";
import CustomCurrencyFormatter from "@/Components/CustomCurrencyFormatter";
import { useTranslation } from "react-i18next";

export default function ProductPriceGroup({ productDetail }) {
  const [priceGroupList, setPriceGroupList] = useState();
  const { t } = useTranslation();
  const dispatch = useDispatch();

  useEffect(() => {
    if (productDetail?.skuId) {
      PriceGroupListApi(productDetail?.skuId, setPriceGroupList, dispatch);
    }
  }, [productDetail?.skuId]);

  return priceGroupList?.length > 0 ? (
    <div className="border-grey-150 mb-4 border-b-2 pb-2">
      <h3 className="mb-0 text-[28px] font-black text-black 2xl:mb-2.5">
        <CustomCurrencyFormatter
          price={PricingGroupPriceCalculate(
            priceGroupList[0]?.price,
            productDetail?.taxType,
            productDetail?.taxValue,
          )}
        />
      </h3>

      <div className="flex items-center space-x-2 rtl:space-x-[unset]">
        <p className="text-black-300 text-sm font-normal rtl:ml-2">
          {t("productdetails.Mrp")}{" "}
          <del>
            <CustomCurrencyFormatter
              price={PricingGroupPriceCalculate(
                productDetail?.price,
                productDetail?.taxType,
                productDetail?.taxValue,
              )}
            />
          </del>
        </p>
        <span className="text-activegreen text-sm font-normal">
          {Math.round(
            ((PricingGroupPriceCalculate(
              productDetail?.price,
              productDetail?.taxType,
              productDetail?.taxValue,
            ) -
              PricingGroupPriceCalculate(
                priceGroupList[0]?.price,
                productDetail?.taxType,
                productDetail?.taxValue,
              )) /
              PricingGroupPriceCalculate(
                productDetail?.price,
                productDetail?.taxType,
                productDetail?.taxValue,
              )) *
              100,
          )}
          {t("productdetails.OFF")} {t("productdetails.ProdPrg")}{" "}
          {priceGroupList[0]?.maxQuantity} {t("productdetails.ProdPrgQty")}
        </span>
      </div>
    </div>
  ) : (
    <div className="border-grey-150 mb-4 border-b-2 pb-2">
      <h3 className="mb-0 text-[28px] font-black text-black 2xl:mb-2.5">
        <CustomCurrencyFormatter
          price={PriceCalculate(
            productDetail?.price,
            productDetail?.pricerefer,
            productDetail?.flag,
            productDetail?.taxType,
            productDetail?.taxValue,
          )}
        />
      </h3>
      {productDetail?.flag == 1 && (
        <div className="flex items-center space-x-2">
          <p className="text-black-300 text-sm font-normal">
            {t("productdetails.Mrp")}{" "}
            <del>
              <CustomCurrencyFormatter
                price={PriceTaxCalculate(
                  productDetail?.price,
                  productDetail?.taxType,
                  productDetail?.taxValue,
                )}
              />
            </del>
          </p>
          <span className="text-activegreen text-sm font-normal">
            {Math.round(
              ((PriceTaxCalculate(
                productDetail?.price,
                productDetail?.taxType,
                productDetail?.taxValue,
              ) -
                PriceTaxCalculate(
                  productDetail?.pricerefer,
                  productDetail?.taxType,
                  productDetail?.taxValue,
                )) /
                PriceTaxCalculate(
                  productDetail?.price,
                  productDetail?.taxType,
                  productDetail?.taxValue,
                )) *
                100,
            )}
            {t("productdetails.OFF")}
          </span>
        </div>
      )}
    </div>
  );
}
