//API
import axiosInstance from "@/api/interceptor";
//Components
import ToastMessage from "@/Components/ToastMessage";

export const getReviewList = async (
  skuId,
  limit,
  offset,
  count,
  setReviewList,
  setLoader,
) => {
  setLoader(true)
  const data = {
    skuId: skuId,
    count: count,
  };
  const result = await axiosInstance.get(
    `product-store/rating/${skuId}?limit=${limit}&offset=${offset}`,
    data,
  );
  if (result?.data?.status === 1) {
    setReviewList(result?.data?.data?.productRating);
  } else {
    ToastMessage({ type: "error", message: result?.data?.message });
  }
  setLoader(false);
};

export const getReviewListCount = async (skuId, setCount) => {
  const result = await axiosInstance.get(
    `product-store/rating/${skuId}?count=1`,
  );
  if (result?.data?.status === 1) {
    setCount(result?.data?.data);
  } else {
    ToastMessage({ type: "error", message: result?.data?.message });
  }
};

export const getProductDetailApi = async (slug) => {
  const data = await axiosInstance.get(`product-store/productdetail/${slug}`);
  return data?.data;
};

export async function AddReviewApi(
  productdetail,
  deletedIds,
  reviews,
  rating,
  reviewImage,
  reviewVideo,
) {
  const data = {
    deleteIds: deletedIds,
    productId: productdetail.productId,
    orderProductId: productdetail.orderProductId,
    skuName: productdetail.skuName,
    rating: rating,
    reviews: reviews,
    ratingImages: reviewImage,
    ratingVideo: reviewVideo,
  };
  const result = await axiosInstance.post(`product-store/rating`, data);
  return result?.data;
}

export async function ReviewSendApi(
  orderId,
  deletedIds,
  starRating,
  description,
  reviewData,
  reviewImage,
  reviewVideo,
  router,
  setAddLoader,
) {
  const data = {
    deleteIds: deletedIds,
    orderProductId: orderId,
    skuName: reviewData.skuName,
    productId: reviewData.productId,
    reviews: description,
    rating: starRating,
    ratingImages: reviewImage,
    ratingVideo: reviewVideo,
  };
  const result = await axiosInstance.post(`product-store/rating`, data);
  if (result?.data?.status == 1) {
    ToastMessage({ type: "success", message: result?.data?.message });
    router.push("/account-settings/order-history");
    setAddLoader(false);
  } else {
    ToastMessage({ type: "error", message: result?.data?.message });
    setAddLoader(false);
  }
}

export const OrderProductReviewApi = async (
  orderId,
  setReviewData,
  setStarRating,
  setDescription,
  setRatingMedia,
) => {
  const result = await axiosInstance.get(
    `product-store/rating-by-order?orderProductId=${orderId}`,
  );
  if (result?.data?.status == 1) {
    setReviewData(result?.data?.data);
    setStarRating(result?.data?.data?.rating);
    setDescription(result?.data?.data?.review);
    setRatingMedia(result?.data?.data?.ratingImages);
  }
};

