"use client";
//React and Next Import
import { Fragment, useEffect, useState } from "react";
//3rd Party
import {
  Accordion,
  AccordionContent,
  AccordionPanel,
  AccordionTitle,
} from "flowbite-react";
//API
import { filterListApi } from "./VariantApi";
//Redux
import { setSelectedFilterIds, setVariantFilter } from "@/redux/slices/variant";
import { useDispatch, useSelector } from "react-redux";
//Component
import { useTranslation } from "react-i18next";

export default function VariantFilter({ params }) {
  const [filterList, setFilterList] = useState([]);

  const selectedval = useSelector((s) => s.VariantRedux.selectedFilterIds);
  const variantfilter = useSelector((s) => s.VariantRedux.variantFilter);
  const dispatch = useDispatch();
  const { t } = useTranslation();
  useEffect(() => {
    filterListApi(params, setFilterList);
    dispatch(setSelectedFilterIds([]));
    dispatch(setVariantFilter([]));
  }, [params]);

  const changesetchecked = (e, itemId, id, headId, type) => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });

    let variantmenuSubArr = [...variantfilter];

    if (variantmenuSubArr?.indexOf(itemId) !== -1) {
      variantmenuSubArr = variantmenuSubArr?.filter(
        (manufactId) => manufactId != itemId,
      );
    } else {
      variantmenuSubArr.push(itemId);
    }
    dispatch(setVariantFilter(variantmenuSubArr));
    if (selectedval?.indexOf(id) !== -1) {
      dispatch(setSelectedFilterIds(selectedval?.filter((ind) => ind != id)));
    } else {
      dispatch(setSelectedFilterIds([...selectedval, id]));
    }
  };

  const handleSearch = (e, section) => {
    const datasort = section.sort((a, b) => {
      if (a.itemName.toLowerCase().includes(e.target.value.toLowerCase()))
        return -1;
      if (b.itemName.toLowerCase().includes(e.target.value.toLowerCase()))
        return 1;
      return 0;
    });
  };

  return (
    filterList &&
    filterList?.map((data, index) => (
      <Fragment key={index}>
        <Accordion className="mb-[16px] rounded-none border-0 border-b-[1px] border-solid border-[#EAEAEA] pb-[16px] shadow-none [&_button]:!mb-0">
          <AccordionPanel>
            <AccordionTitle className='mb-[16px] bg-white p-0 hover:bg-[bg-white] focus:ring-0 [&~[data-testid="flowbite-accordion-content"]]:!border-t-0'>
              <span className="text-[14px] font-semibold leading-[17px] text-[#303030]">
                {data.sectionName}
              </span>
            </AccordionTitle>
            <AccordionContent className="mt-[12px] p-0">
              {data && data?.sectionItem && data?.sectionItem?.length > 4 && (
                <div className="mb-4 w-full">
                  <input
                    type="text"
                    placeholder={t("orderhistory.Search")}
                    onChange={(e) => handleSearch(e, data?.sectionItem)}
                    className="text-dark search-icon h-9 w-full rounded border border-slate-300 bg-no-repeat ps-8 text-sm text-black placeholder:text-slate-400 focus:border-slate-300 focus:shadow-none focus:ring-0"
                  />
                </div>
              )}
              <div className="block pr-3">
                {data &&
                  data?.sectionItem &&
                  data?.sectionItem?.map((item, index) => (
                    <div
                      className="relative mb-[12px] flex items-center space-x-[8px] last-of-type:mb-0"
                      key={index}
                    >
                      <div className="flex h-[13px] shrink-0 items-center rtl:ml-3">
                        <input
                          id="comments"
                          name={item?.id}
                          type="checkbox"
                          onChange={(e) => {}}
                          checked={selectedval.includes(item?.id)}
                          className="block h-3 w-3 cursor-pointer border-[#373737] text-black checked:bg-[#000000] focus:ring-0 focus:ring-transparent"
                          onClick={(e) =>
                            changesetchecked(
                              e,
                              data?.sectionName + "~" + item?.itemName,
                              item?.id,
                              data?.sectionItem,
                              data?.sectionType,
                            )
                          }
                        />
                      </div>
                      <div className="relative top-[1px] w-full text-sm leading-6">
                        <label
                          htmlFor={item?.id}
                          onClick={(e) =>
                            changesetchecked(
                              e,
                              data?.sectionName + "~" + item?.itemName,
                              item?.id,
                              data?.sectionItem,
                              data?.sectionType,
                            )
                          }
                          className="flex cursor-pointer items-center justify-between text-sm font-normal leading-3 text-[#333333]"
                        >
                          {item?.itemName}
                        </label>
                      </div>
                    </div>
                  ))}
              </div>
            </AccordionContent>
          </AccordionPanel>
        </Accordion>
      </Fragment>
    ))
  );
}
