"use client";
//React and Next Import
import { useEffect, useState } from "react";
//3rd Party
import { useRouter } from "nextjs-toploader/app";
import moment from "moment";
//API
import {
  LikeAndDisLikeAPi,
  PostQuestionApi,
  ReportAbuseAPi,
  getQuestionList,
  getReasonApi,
} from "./QuestionAndAnswerApi";
//Utils
import { imageUrl } from "@/utilities/url";
//Components
import PostQuestion from "./PostQuestion";
import ToastMessage from "@/Components/ToastMessage";
import ReportAndAbuse from "./ReportAndAbuse";
import { useTranslation } from "react-i18next";

export default function QuestionAndAnswer({ token, productDetail }) {
  const [questionList, setQusetionList] = useState([]);
  const [postQuestion, setPostQuestion] = useState(false);
  const [likeStatus, setLikeStatus] = useState([]);
  const [dislikeStatus, setDislikeStatus] = useState([]);
  const [question, setQuestion] = useState("");
  const [questionError, setQuestionError] = useState("");
  const [addQuestionSubmit, setAddQuestionSubmit] = useState(false);
  const [reportAbuseModal, setReportAbuseModal] = useState(false);
  const [reasonList, setReasonList] = useState([]);
  const [reason, setReason] = useState();
  const [remark, setRemark] = useState("");
  const [reasonError, setReasonError] = useState("");
  const [remarkError, setRemarkError] = useState("");
  const [reportSubmit, setReportSubmit] = useState(false);
  const [answerId, setAnswerId] = useState();
  const router = useRouter();
  const [search, setSearch] = useState("");
  const [readOtherAnswer, setReadOtherAnswer] = useState([]);
  const [loader, setLoader] = useState(false);
  const { t } = useTranslation();
  const Skeleton = [1, 2, 3, 4, 5];

  //  get question list function
  const hanldeQuestionAndAnswer = async (skuId, search) => {
    setLoader(true);
    const getQuestionlist = await getQuestionList(skuId, search);
    if (getQuestionlist?.status == 1) {
      setQusetionList(getQuestionlist?.data);
      setLoader(false);
      let updatedLikeList = [...likeStatus];
      let updatedDislikeList = [...dislikeStatus];
      getQuestionlist?.data?.forEach((questionlist, index) => {
        questionlist?.answerList?.forEach((answer, answerIndex) => {
          let likeChild = updatedLikeList[index]
            ? [...updatedLikeList[index]]
            : [];
          let dislikeChild = updatedDislikeList[index]
            ? [...updatedDislikeList[index]]
            : [];

          if (answer?.likeType === 1) {
            likeChild[answerIndex] = true;
            dislikeChild[answerIndex] = false;
          } else if (answer?.likeType === 2) {
            likeChild[answerIndex] = false;
            dislikeChild[answerIndex] = true;
          } else if (answer?.likeType === 0) {
            likeChild[answerIndex] = false;
            dislikeChild[answerIndex] = false;
          }

          updatedLikeList[index] = likeChild;
          updatedDislikeList[index] = dislikeChild;
        });

        // Update the state outside the inner loop to capture all changes
        setLikeStatus(updatedLikeList);
        setDislikeStatus(updatedDislikeList);
      });
    }
  };

  //  get reason function
  const hanldeReasonGet = async (slug) => {
    const getReason = await getReasonApi();
    if (getReason?.status == 1) {
      setReasonList(getReason?.data);
    }
  };

  useEffect(() => {
    if (productDetail?.skuId) {
      hanldeQuestionAndAnswer(productDetail?.skuId, "");
      if (token) {
        hanldeReasonGet();
      }
    }
  }, [productDetail]);

  //  post your question button function
  const handlePostYourQuestion = async () => {
    if (token) {
      setPostQuestion(true);
    } else {
      router.push("/buyer-login");
    }
  };

  //  remove like function
  const handleRemoveLikeClick = async (index, answerIndex, answerId) => {
    if (token) {
      const addLike = await LikeAndDisLikeAPi(answerId, 0);

      if (addLike?.status == 1) {
        ToastMessage({ type: "success", message: addLike?.message });
        hanldeQuestionAndAnswer(productDetail?.skuId, "");
      } else {
        ToastMessage({ type: "error", message: addLike?.message });
      }

      let likeList = [...likeStatus];
      let likeChild = likeStatus[index] ? [...likeStatus[index]] : [];
      likeChild[answerIndex] = false;
      likeList[index] = likeChild;
      setLikeStatus(likeList);

      let disLikeList = [...dislikeStatus];
      let disLikeChild = dislikeStatus[index] ? [...dislikeStatus[index]] : [];
      disLikeChild[answerIndex] = false;
      disLikeList[index] = disLikeChild;
      setDislikeStatus(disLikeList);
    } else {
      router.push("/buyer-login");
    }
  };

  //  add like function
  const handleLikeClick = async (index, answerIndex, answerId, type) => {
    if (token) {
      if (type == 2) {
        const addDislike = await LikeAndDisLikeAPi(answerId, 0);
      }
      const addLike = await LikeAndDisLikeAPi(answerId, 1);

      if (addLike?.status == 1) {
        hanldeQuestionAndAnswer(productDetail?.skuId, "");
        ToastMessage({ type: "success", message: addLike?.message });
      } else {
        ToastMessage({ type: "error", message: addLike?.message });
      }

      let likeList = [...likeStatus];
      let likeChild = likeStatus[index] ? [...likeStatus[index]] : [];
      likeChild[answerIndex] = !likeChild[answerIndex];
      likeList[index] = likeChild;
      setLikeStatus(likeList);

      let disLikeList = [...dislikeStatus];
      let disLikeChild = dislikeStatus[index] ? [...dislikeStatus[index]] : [];
      disLikeChild[answerIndex] = false;
      disLikeList[index] = disLikeChild;
      setDislikeStatus(disLikeList);
    } else {
      router.push("/buyer-login");
    }
  };

  //  add dislike function
  const handleDislikeClick = async (index, answerIndex, answerId, type) => {
    if (token) {
      if (type == 1) {
        const addDislike = await LikeAndDisLikeAPi(answerId, 0);
      }
      const addDislike = await LikeAndDisLikeAPi(answerId, 2);

      if (addDislike?.status == 1) {
        ToastMessage({ type: "success", message: addDislike?.message });
        hanldeQuestionAndAnswer(productDetail?.skuId, "");
      } else {
        ToastMessage({ type: "error", message: addDislike?.message });
      }

      let likeList = [...likeStatus];
      let likeChild = likeStatus[index] ? [...likeStatus[index]] : [];
      likeChild[answerIndex] = false;
      likeList[index] = likeChild;
      setLikeStatus(likeList);

      let disLikeList = [...dislikeStatus];
      let disLikeChild = dislikeStatus[index] ? [...dislikeStatus[index]] : [];
      disLikeChild[answerIndex] = !disLikeChild[answerIndex];
      disLikeList[index] = disLikeChild;
      setDislikeStatus(disLikeList);
    } else {
      router.push("/buyer-login");
    }
  };

  // get input onchnage function
  const handleAddQuestionChange = (e) => {
    setQuestion(e.target.value?.trimStart());
  };

  //  validation function
  const AddQuestionValidation = () => {
    const validobj = {
      question: true,
    };
    if (question !== "") {
      validobj.question = true;
      setQuestionError("");
    } else {
      validobj.question = false;
      setQuestionError("Please enter your question");
    }

    return validobj.question;
  };

  useEffect(() => {
    if (addQuestionSubmit) {
      AddQuestionValidation();
    }
    if (question == "") {
      setQuestionError("");
    }
  }, [question]);

  //  post question function
  const handleClickAddQuestion = async (setLoader) => {
    setLoader(true);
    setAddQuestionSubmit(true);
    if (AddQuestionValidation()) {
      const addquestion = await PostQuestionApi(
        question,
        productDetail.productId,
        productDetail?.skuId,
      );
      if (addquestion?.status == 1) {
        ToastMessage({ type: "success", message: addquestion?.message });
        setPostQuestion(false);
        hanldeQuestionAndAnswer(productDetail?.skuId, "");
        setLoader(false);
        setQuestion("");
      } else {
        ToastMessage({ type: "error", message: addquestion?.message });
        setLoader(false);
        setQuestion("");
      }
    } else {
      setLoader(false);
    }
  };

  // report click function
  const handleReportModal = (id) => {
    if (token) {
      setReportAbuseModal(true);
      setAnswerId(id);
    } else {
      router.push("/buyer-login");
    }
  };

  //  choose report function
  const handleReportChange = (e, type) => {
    if (type == "reason") {
      setReason(e);
    } else if (type == "remark") {
      setRemark(e.trimStart());
    }
  };

  //  report validation function
  const reportValidation = () => {
    const validobj = {
      reason: true,
      remark: true,
    };

    if (remark !== "") {
      validobj.remark = true;
      setRemarkError("");
    } else {
      validobj.remark = false;
      setRemarkError("Please enter remark");
    }

    if (reason) {
      validobj.reason = true;
      setReasonError("");
    } else {
      validobj.reason = false;
      setReasonError("Please select reason");
    }

    if (validobj.reason == true && validobj.remark == true) {
      return true;
    } else {
      return false;
    }
  };

  useEffect(() => {
    if (reportSubmit) {
      reportValidation();
    }
  }, [reason, remark]);

  // report click function
  const handleClickReport = async (type) => {
    if (type == "cancel") {
      setReportAbuseModal(false);
      setReason({});
      setRemark("");
      setAnswerId(0);
    } else if (type == "submit") {
      setReportSubmit(true);
      if (reportValidation()) {
        const addReport = await ReportAbuseAPi(answerId, remark, reason?.value);

        if (addReport?.status == 1) {
          ToastMessage({ type: "success", message: addReport?.message });
          hanldeQuestionAndAnswer(productDetail?.skuId, "");
          setReportAbuseModal(false);
          setReason({});
          setRemark("");
        } else {
          ToastMessage({ type: "error", message: addReport?.message });
        }
      }
    }
  };

  // search question function
  const handleSearchChange = (value) => {
    setSearch(value);
    let timer = setTimeout(() => {
      hanldeQuestionAndAnswer(productDetail?.skuId, value);
    }, 500);
    return () => {
      clearTimeout(timer);
    };
  };

  // remove search function
  const handleSeachCancel = () => {
    setSearch("");
    hanldeQuestionAndAnswer(productDetail?.skuId, "");
  };

  //  read other answer click function
  const handleReadOtherAswer = (index) => {
    const readAnswer = [...readOtherAnswer];
    readAnswer[index] = !readAnswer[index];
    setReadOtherAnswer(readAnswer);
  };

  function HandleDefaultImage({ answer, i, question, index }) {
    let defaultFind = question.answerList.find(
      (value) => value.defaultAnswer == 1,
    );
    return defaultFind != undefined
      ? answer?.defaultAnswer == 1 && (
          <div>
            <div className="mb-3 flex items-start space-x-3 rtl:space-x-[unset]">
              <h3 className="text-black-300 whitespace-nowrap text-lg font-black rtl:ml-3">
                {t("productdetails.A")}
              </h3>
              <p className="text-black-300 text-lg font-normal">
                {answer?.answer}
              </p>
            </div>
            <div className="mb-4 flex flex-col items-start justify-between space-y-3 min-[580px]:flex-row min-[580px]:items-center min-[580px]:space-y-0">
              <div className="flex items-center space-x-2.5">
                <img
                  src={
                    imageUrl +
                    "?path=" +
                    question?.postedBy?.avatarPath +
                    "&name=" +
                    question?.postedBy?.avatar +
                    "&width=60&height=60"
                  }
                  alt={question?.postedBy?.firstName}
                  onError={({ currentTarget }) => {
                    currentTarget.onerror = null;
                    currentTarget.src = "/img/user1.jpg";
                    currentTarget.alt = "user";
                  }}
                  className="h-8 w-8 rounded-full rtl:ml-2.5"
                />
                <p className="text-black-300 text-sm font-normal">
                  {question?.postedBy?.firstName}{" "}
                </p>
                <span className="text-darkgrey text-sm font-normal italic">
                  {moment(answer?.createdDate)?.format("DD MMM, YYYY")}
                </span>
              </div>
              {question?.answerList && (
                <div className="flex items-center space-x-8">
                  <a
                    role="button"
                    className="cursor-pointer text-sm font-normal text-gray-900 rtl:ml-8"
                    onClick={(e) => handleReportModal(answer?.answerId)}
                  >
                    {t("productcontent.RptAbuse")}
                  </a>
                  <div className="flex space-x-2 rtl:space-x-[unset]">
                    {likeStatus[index]?.length > 0 ? (
                      likeStatus[index][i] ? (
                        <button className="hover-bg-slate-400 h-8 w-8 rounded p-1.5 rtl:ml-2">
                          <img
                            src="/img/like-fill.svg"
                            alt="like-fill"
                            onClick={(e) =>
                              handleRemoveLikeClick(index, i, answer?.answerId)
                            }
                          />
                        </button>
                      ) : (
                        <button
                          className="hover-bg-slate-400 h-8 w-8 rounded p-1.5 rtl:ml-2"
                          onClick={(e) =>
                            handleLikeClick(
                              index,
                              i,
                              answer?.answerId,
                              answer?.likeType,
                            )
                          }
                        >
                          <img
                            src="/img/like-tranparent.svg"
                            alt="like-tranparent"
                            className=""
                          />
                        </button>
                      )
                    ) : (
                      <button
                        className="hover-bg-slate-400 h-8 w-8 rounded p-1.5 rtl:ml-2"
                        onClick={(e) =>
                          handleLikeClick(
                            index,
                            i,
                            answer?.answerId,
                            answer?.likeType,
                          )
                        }
                      >
                        <img
                          src="/img/like-tranparent.svg"
                          alt="like-tranparent"
                          className=""
                        />
                      </button>
                    )}

                    <p className="text-black-300 text-lg font-normal">
                      {answer?.likes}
                    </p>
                  </div>
                  <div className="flex space-x-2 rtl:space-x-[unset]">
                    {dislikeStatus[index]?.length > 0 ? (
                      dislikeStatus[index][i] ? (
                        <button className="hover-bg-slate-400 h-8 w-8 rounded p-1.5 rtl:ml-2">
                          <img
                            src="/img/unlike-fill.svg"
                            alt="unlike-fill"
                            onClick={(e) =>
                              handleRemoveLikeClick(index, i, answer?.answerId)
                            }
                            className=""
                          />
                        </button>
                      ) : (
                        <button
                          className="hover-bg-slate-400 h-8 w-8 rounded p-1.5 rtl:ml-2"
                          onClick={(e) =>
                            handleDislikeClick(
                              index,
                              i,
                              answer?.answerId,
                              answer?.likeType,
                            )
                          }
                        >
                          <img
                            src="/img/unlike-tranparent.svg"
                            alt="unlike-tranparent"
                            className=""
                          />
                        </button>
                      )
                    ) : (
                      <button
                        className="hover-bg-slate-400 h-8 w-8 rounded p-1.5 rtl:ml-2"
                        onClick={(e) =>
                          handleDislikeClick(
                            index,
                            i,
                            answer?.answerId,
                            answer?.likeType,
                          )
                        }
                      >
                        <img
                          src="/img/unlike-tranparent.svg"
                          alt="unlike-tranparent"
                          className=""
                        />
                      </button>
                    )}

                    <p className="text-black-300 text-lg font-normal">
                      {answer?.dislikes}
                    </p>
                  </div>
                </div>
              )}
            </div>
          </div>
        )
      : answer?.defaultAnswer != 1 && i <= 0 && (
          <div>
            <div className="mb-3 flex items-start space-x-3 rtl:space-x-[unset]">
              <h3 className="text-black-300 whitespace-nowrap text-lg font-black rtl:ml-3">
                {t("productdetails.A")}
              </h3>
              <p className="text-black-300 text-lg font-normal">
                {question?.answerList[0]?.answer}
              </p>
            </div>
            <div className="mb-4 flex flex-col items-start justify-between space-y-3 min-[580px]:flex-row min-[580px]:items-center min-[580px]:space-y-0">
              <div className="flex items-center space-x-2.5">
                <img
                  src={
                    imageUrl +
                    "?path=" +
                    question?.postedBy?.avatarPath +
                    "&name=" +
                    question?.postedBy?.avatar +
                    "&width=60&height=60"
                  }
                  alt={question?.postedBy?.firstName}
                  onError={({ currentTarget }) => {
                    currentTarget.onerror = null;
                    currentTarget.src = "/img/user1.jpg";
                    currentTarget.alt = "user";
                  }}
                  className="h-8 w-8 rounded-full rtl:ml-2.5"
                />
                <p className="text-black-300 text-sm font-normal">
                  {question?.postedBy?.firstName}{" "}
                </p>
                <span className="text-darkgrey text-sm font-normal italic">
                  {moment(answer?.createdDate)?.format("DD MMM, YYYY")}
                </span>
              </div>
              {question?.answerList && (
                <div className="flex items-center space-x-8">
                  <a
                    role="button"
                    className="cursor-pointer text-sm font-normal text-gray-900 rtl:ml-8"
                    onClick={(e) => handleReportModal(answer?.answerId)}
                  >
                    {t("productcontent.RptAbuse")}
                  </a>
                  <div className="flex space-x-2 rtl:space-x-[unset]">
                    {likeStatus[index]?.length > 0 ? (
                      likeStatus[index][i] ? (
                        <button className="hover-bg-slate-400 h-8 w-8 rounded p-1.5 rtl:ml-2">
                          <img
                            src="/img/like-fill.svg"
                            alt="like-fill"
                            onClick={(e) =>
                              handleRemoveLikeClick(index, i, answer?.answerId)
                            }
                          />
                        </button>
                      ) : (
                        <button
                          className="hover-bg-slate-400 h-8 w-8 rounded p-1.5 rtl:ml-2"
                          onClick={(e) =>
                            handleLikeClick(
                              index,
                              i,
                              answer?.answerId,
                              answer?.likeType,
                            )
                          }
                        >
                          <img
                            src="/img/like-tranparent.svg"
                            alt="like-tranparent"
                            className=""
                          />
                        </button>
                      )
                    ) : (
                      <button
                        className="hover-bg-slate-400 h-8 w-8 rounded p-1.5 rtl:ml-2"
                        onClick={(e) =>
                          handleLikeClick(
                            index,
                            i,
                            answer?.answerId,
                            answer?.likeType,
                          )
                        }
                      >
                        <img
                          src="/img/like-tranparent.svg"
                          alt="like-tranparent"
                          className=""
                        />
                      </button>
                    )}

                    <p className="text-black-300 text-lg font-normal">
                      {answer?.likes}
                    </p>
                  </div>
                  <div className="flex space-x-2 rtl:space-x-[unset]">
                    {dislikeStatus[index]?.length > 0 ? (
                      dislikeStatus[index][i] ? (
                        <button className="hover-bg-slate-400 h-8 w-8 rounded p-1.5 rtl:ml-2">
                          <img
                            src="/img/unlike-fill.svg"
                            alt="unlike-fill"
                            onClick={(e) =>
                              handleRemoveLikeClick(index, i, answer?.answerId)
                            }
                            className=""
                          />
                        </button>
                      ) : (
                        <button
                          className="hover-bg-slate-400 h-8 w-8 rounded p-1.5 rtl:ml-2"
                          onClick={(e) =>
                            handleDislikeClick(
                              index,
                              i,
                              answer?.answerId,
                              answer?.likeType,
                            )
                          }
                        >
                          <img
                            src="/img/unlike-tranparent.svg"
                            alt="unlike-tranparent"
                            className=""
                          />
                        </button>
                      )
                    ) : (
                      <button
                        className="hover-bg-slate-400 h-8 w-8 rounded p-1.5 rtl:ml-2"
                        onClick={(e) =>
                          handleDislikeClick(
                            index,
                            i,
                            answer?.answerId,
                            answer?.likeType,
                          )
                        }
                      >
                        <img
                          src="/img/unlike-tranparent.svg"
                          alt="unlike-tranparent"
                          className=""
                        />
                      </button>
                    )}

                    <p className="text-black-300 text-lg font-normal">
                      {answer?.dislikes}
                    </p>
                  </div>
                </div>
              )}
            </div>
          </div>
        );
  }

  return (
    <>
      <div className="mb-14">
        <div className="mb-8 flex flex-col items-start justify-between space-y-4 min-[460px]:flex-row min-[460px]:items-center min-[460px]:space-y-0">
          <h3 className="text-xl font-black text-black sm:text-2xl">
            {t("productcontent.QnA")}
          </h3>
          <a
            data-modal-target="static-modal"
            data-modal-toggle="static-modal"
            onClick={handlePostYourQuestion}
            role="button"
            className="border-boldblack text-bold-black btn flex h-[49px] cursor-pointer items-center justify-center whitespace-nowrap rounded border px-6 text-sm font-bold uppercase text-black hover:bg-slate-50 sm:text-base"
          >
            {t("productcontent.PstQ")}
          </a>
        </div>
        <div className="relative mb-8 w-full">
          <input
            type="text"
            value={search}
            placeholder={t("orderhistory.Search")}
            onChange={(e) => handleSearchChange(e.target.value.trimStart())}
            className="border-grey-400 text-black-300 h-11 w-full rounded-md border px-4 ps-12 text-sm font-normal focus-visible:outline-0"
          />
          <img
            src="/img/search-stroke.svg"
            alt="search-stroke"
            className="top-35 absolute left-4"
          />
          {search !== "" && (
            <button
              className="top-35 absolute right-4"
              onClick={handleSeachCancel}
            >
              <img src="/img/close-input.svg" alt="close-input" />
            </button>
          )}
        </div>
        {loader == false ? (
          questionList?.length > 0 ? (
            questionList?.map((question, index) => (
              <div
                className="border-grey200 mb-6 border-b pb-6"
                key={`question${index}`}
              >
                <div className="mb-2.5 flex items-start space-x-3">
                  <h3 className="text-black-300 whitespace-nowrap text-lg font-black">
                    {t("productdetails.Q")}
                  </h3>
                  <p className="text-black-300 text-lg font-black">
                    {question?.question}
                  </p>
                </div>

                <div>
                  {question?.answerList?.map((answer, i) =>
                    readOtherAnswer[index] ? (
                      <div key={`answer${i}`}>
                        <div className="mb-3 flex items-start space-x-3">
                          <h3 className="text-black-300 whitespace-nowrap text-lg font-black">
                            {t("productdetails.A")}
                          </h3>
                          <p className="text-black-300 text-lg font-normal">
                            {answer?.answer}
                          </p>
                        </div>
                        <div className="mb-4 flex flex-col items-start justify-between space-y-3 min-[580px]:flex-row min-[580px]:items-center min-[580px]:space-y-0">
                          <div className="flex items-center space-x-2.5">
                            <img
                              // src='/img/profile-logo.png'
                              src={
                                imageUrl +
                                "?path=" +
                                question?.postedBy?.avatarPath +
                                "&name=" +
                                question?.postedBy?.avatar +
                                "&width=60&height=60"
                              }
                              alt={question?.postedBy?.firstName}
                              onError={({ currentTarget }) => {
                                currentTarget.onerror = null;
                                currentTarget.src = "/img/user1.jpg";
                                currentTarget.alt = "user";
                              }}
                              className="h-8 w-8 rounded-full rtl:ml-2.5"
                            />
                            <p className="text-black-300 text-sm font-normal">
                              {question?.postedBy?.firstName}{" "}
                            </p>
                            <span className="text-darkgrey text-sm font-normal italic">
                              {moment(answer?.createdDate)?.format(
                                "DD MMM, YYYY",
                              )}
                            </span>
                          </div>
                          {question?.answerList && (
                            <div className="flex items-center space-x-8">
                              <a
                                role="button"
                                className="cursor-pointer text-sm font-normal text-gray-900 rtl:ml-8"
                                onClick={(e) =>
                                  handleReportModal(answer?.answerId)
                                }
                              >
                                {t("productcontent.RptAbuse")}
                              </a>
                              <div className="flex space-x-2 rtl:space-x-[unset]">
                                {likeStatus[index]?.length > 0 ? (
                                  likeStatus[index][i] ? (
                                    <button className="hover-bg-slate-400 h-8 w-8 rounded p-1.5 rtl:ml-2">
                                      <img
                                        src="/img/like-fill.svg"
                                        alt="like-fill"
                                        onClick={(e) =>
                                          handleRemoveLikeClick(
                                            index,
                                            i,
                                            answer?.answerId,
                                          )
                                        }
                                      />
                                    </button>
                                  ) : (
                                    <button
                                      className="hover-bg-slate-400 h-8 w-8 rounded p-1.5 rtl:ml-2"
                                      onClick={(e) =>
                                        handleLikeClick(
                                          index,
                                          i,
                                          answer?.answerId,
                                          answer?.likeType,
                                        )
                                      }
                                    >
                                      <img
                                        src="/img/like-tranparent.svg"
                                        alt="like-tranparent"
                                        className=""
                                      />
                                    </button>
                                  )
                                ) : (
                                  <button
                                    className="hover-bg-slate-400 h-8 w-8 rounded p-1.5 rtl:ml-2"
                                    onClick={(e) =>
                                      handleLikeClick(
                                        index,
                                        i,
                                        answer?.answerId,
                                        answer?.likeType,
                                      )
                                    }
                                  >
                                    <img
                                      src="/img/like-tranparent.svg"
                                      alt="like-tranparent"
                                      className=""
                                    />
                                  </button>
                                )}

                                <p className="text-black-300 text-lg font-normal">
                                  {answer?.likes}
                                </p>
                              </div>
                              <div className="flex space-x-2 rtl:space-x-[unset]">
                                {dislikeStatus[index]?.length > 0 ? (
                                  dislikeStatus[index][i] ? (
                                    <button className="hover-bg-slate-400 h-8 w-8 rounded p-1.5 rtl:ml-2">
                                      <img
                                        src="/img/unlike-fill.svg"
                                        alt="unlike-fill"
                                        onClick={(e) =>
                                          handleRemoveLikeClick(
                                            index,
                                            i,
                                            answer?.answerId,
                                          )
                                        }
                                        className=""
                                      />
                                    </button>
                                  ) : (
                                    <button
                                      className="hover-bg-slate-400 h-8 w-8 rounded p-1.5 rtl:ml-2"
                                      onClick={(e) =>
                                        handleDislikeClick(
                                          index,
                                          i,
                                          answer?.answerId,
                                          answer?.likeType,
                                        )
                                      }
                                    >
                                      <img
                                        src="/img/unlike-tranparent.svg"
                                        alt="unlike-tranparent"
                                        className=""
                                      />
                                    </button>
                                  )
                                ) : (
                                  <button
                                    className="hover-bg-slate-400 h-8 w-8 rounded p-1.5 rtl:ml-2"
                                    onClick={(e) =>
                                      handleDislikeClick(
                                        index,
                                        i,
                                        answer?.answerId,
                                        answer?.likeType,
                                      )
                                    }
                                  >
                                    <img
                                      src="/img/unlike-tranparent.svg"
                                      alt="unlike-tranparent"
                                      className=""
                                    />
                                  </button>
                                )}

                                <p className="text-black-300 text-lg font-normal">
                                  {answer?.dislikes}
                                </p>
                              </div>
                            </div>
                          )}
                        </div>
                      </div>
                    ) : (
                      <HandleDefaultImage
                        answer={answer}
                        i={i}
                        question={question}
                        index={index}
                        key={`answer${i}`}
                      />
                    ),
                  )}
                  {question?.answerList?.length > 1 ? (
                    <div>
                      <a
                        role="button"
                        onClick={(e) => handleReadOtherAswer(index)}
                        className="text-link animation relative cursor-pointer text-lg font-normal"
                      >
                        {t("productcontent.RdAns")}
                      </a>
                    </div>
                  ) : (
                    <></>
                  )}
                </div>
              </div>
            ))
          ) : (
            <>
              <span className="text-black-300">
                {t("productcontent.NoQstns")}
              </span>
              <div className="bg-grey400 my-6 h-px w-full 2xl:my-14"></div>
            </>
          )
        ) : (
          Skeleton.map((_, index) => (
            <div className="mb-6" key={`questionAndAnswerLoader${index}`}>
              <div role="status" className="max-w-sm animate-pulse">
                <div className="mb-4 h-2.5 w-48 rounded-full bg-gray-200 dark:bg-gray-700"></div>
                <div
                  role="status"
                  className="max-w-lg animate-pulse space-y-2.5"
                >
                  <div className="flex w-full max-w-[400px] items-center">
                    <div className="h-2.5 w-full rounded-full bg-gray-300 dark:bg-gray-600"></div>
                    <div className="ms-2 h-2.5 w-80 rounded-full bg-gray-200 dark:bg-gray-700"></div>
                    <div className="ms-2 h-2.5 w-full rounded-full bg-gray-300 dark:bg-gray-600"></div>
                  </div>
                </div>
                <div className="mt-4 flex items-center">
                  <svg
                    className="me-3 h-10 w-10 text-gray-200 dark:text-gray-700"
                    aria-hidden="true"
                    xmlns="http://www.w3.org/2000/svg"
                    fill="currentColor"
                    viewBox="0 0 20 20"
                  >
                    <path d="M10 0a10 10 0 1 0 10 10A10.011 10.011 0 0 0 10 0Zm0 5a3 3 0 1 1 0 6 3 3 0 0 1 0-6Zm0 13a8.949 8.949 0 0 1-4.951-1.488A3.987 3.987 0 0 1 9 13h2a3.987 3.987 0 0 1 3.951 3.512A8.949 8.949 0 0 1 10 18Z" />
                  </svg>
                  <div>
                    <div className="mb-2 h-2.5 w-32 rounded-full bg-gray-200 dark:bg-gray-700"></div>
                    <div className="h-2 w-48 rounded-full bg-gray-200 dark:bg-gray-700"></div>
                  </div>
                </div>
              </div>
            </div>
          ))
        )}
      </div>

      <PostQuestion
        postQuestion={postQuestion}
        setPostQuestion={setPostQuestion}
        productDetail={productDetail}
        handleAddQuestionChange={handleAddQuestionChange}
        handleClickAddQuestion={handleClickAddQuestion}
        question={question}
        questionError={questionError}
      />

      <ReportAndAbuse
        reportAbuseModal={reportAbuseModal}
        setReportAbuseModal={setReportAbuseModal}
        handleReportChange={handleReportChange}
        handleClickReport={handleClickReport}
        reasonList={reasonList}
        reason={reason}
        remark={remark}
        reasonError={reasonError}
        remarkError={remarkError}
      />
    </>
  );
}
