"use client";
//React and Next
import { Fragment, useState } from "react";
import Link from "next/link";
//Redux
import { useDispatch } from "react-redux";
import { quotationid } from "@/redux/slices/userSlice";
//3rd party
import {
  Popover,
  PopoverButton,
  PopoverPanel,
  Transition,
} from "@headlessui/react";
import moment from "moment";
//Utils
import { imageUrl } from "@/utilities/url";
import { PriceCalculate } from "@/utilities/PriceCalculate";
//Components
import PaginationComp from "@/Components/Pagination";
import CustomCurrencyFormatter from "@/Components/CustomCurrencyFormatter";
import { useTranslation } from "react-i18next";

export default function QuotationCard({
  quatationList,
  loader,
  handleStatusFilter,
  getQuotationlistApiCount,
  nPages1,
  currentPage,
  onPageChange,
}) {
  const [renderPanel, setRenderPanel] = useState(false);
  const dispatch = useDispatch();
  const skele = [1, 2, 3, 4, 5];
  const { t } = useTranslation();
  const [status, setStatus] = useState("All");
  const handleQuotationDetail = (data) => {
    dispatch(quotationid(data));
  };

  return (
    <div className="w-full px-0 py-6 sm:px-8">
      <div className="flex flex-wrap justify-between items-center gap-2.5 mb-4 text-center sm:text-start">
        <h5 className="text-lg font-black text-black-300">
          {t("accountsettings.MyQuotations")} ({getQuotationlistApiCount})
        </h5>
        <Popover
          className={`relative  w-48 group z-40 ${
            renderPanel && "drop-shadow-md"
          }`}
        >
          <>
            <PopoverButton
              className="flex items-center justify-between w-full gap-2 py-2 text-sm font-normal leading-6 bg-white rounded ps-6 pe-2 group-hover:rounded-b-none focus-visible:outline-none text-black-300"
              onBlur={() => setRenderPanel(false)}
              onClick={() => {
                setRenderPanel(!renderPanel);
              }}
            >
              <p>
                {t("productlist.sortBy")}
                <span className="ml-2">{status}</span>
              </p>
              <img src="/img/right.svg" alt="right" />
            </PopoverButton>

            <Transition
              as={Fragment}
              enter="transition ease-out duration-200"
              enterFrom="opacity-0 translate-y-1"
              enterTo="opacity-100 translate-y-0"
              leave="transition ease-in duration-150"
              leaveFrom="opacity-100 translate-y-0"
              leaveTo="opacity-0 translate-y-1"
            >
              <PopoverPanel
                className="top-[69%] right-0 z-10 absolute bg-white rounded rounded-t-none w-full overflow-hidden"
                onBlur={() => setRenderPanel(false)}
              >
                {({ close }) => (
                  <div className="px-4 py-2">
                    <div
                      className="group relative flex justify-between items-center hover:bg-gray-50 py-2.5 ps-6 pe-2 rounded-lg leading-6 cursor-pointer"
                      onClick={(e) => {
                        setRenderPanel(true);
                        close();
                        handleStatusFilter(0);
                        setStatus(`${t("myprofile.pending")}`);
                      }}
                    >
                      <p className="text-sm font-normal text-black-300">
                        {t("myprofile.pending")}
                      </p>
                    </div>
                    <div
                      className="group relative flex justify-between items-center hover:bg-gray-50 py-2.5 ps-6 pe-2 rounded-lg leading-6 cursor-pointer"
                      onClick={(e) => {
                        setRenderPanel(true);
                        close();
                        handleStatusFilter(1);
                        setStatus(`${t("myprofile.approved")}`);
                      }}
                    >
                      <p className="text-sm font-normal text-black-300">
                        {t("myprofile.approved")}
                      </p>
                    </div>

                    <div
                      className="group relative flex justify-between items-center hover:bg-gray-50 py-2.5 ps-6 pe-2 rounded-lg leading-6 cursor-pointer"
                      onClick={(e) => {
                        setRenderPanel(true);
                        close();
                        handleStatusFilter(-1);
                        setStatus(`All`);
                      }}
                    >
                      <p className="text-sm font-normal text-black-300">
                        {t("priceFilter.All")}
                      </p>
                    </div>
                  </div>
                )}
              </PopoverPanel>
            </Transition>
          </>
        </Popover>
      </div>
      <div>
        {quatationList && quatationList.length != 0
          ? loader &&
            quatationList?.map((data, index) => (
              <Link
                prefetch={false}
                key={index}
                href="/account-settings/quotation-details"
                className="block mb-4 border rounded border-grey"
                onClick={() => handleQuotationDetail(data)}
              >
                <div className="flex justify-between gap-2.5 bg-blue-100 px-4 sm:px-6 py-5 rounded-t h-auto sm:h-14">
                  <h5 className="text-base font-bold leading-5 text-black-300">
                    {t("myprofile.Quotation")}
                    {data?.quotationNo}
                  </h5>
                  <p className="text-sm font-semibold leading-5 text-black-300">
                    {t("myprofile.QuotedOn")} :{" "}
                    <span className="font-normal">
                      {data?.modifiedDate
                        ? moment(data?.modifiedDate).format(
                            "MMMM Do YYYY, h:mm:ss a"
                          )
                        : moment(data?.createdDate).format(
                            "MMMM Do YYYY, h:mm:ss a"
                          )}
                    </span>
                  </p>
                </div>

                {data?.quotationProduct?.map((datas, index) => (
                  <div key={index} className="px-4 sm:px-6">
                    <div className="flex justify-between gap-2.5 py-4 border-b border-lightgrey-100">
                      <div className="flex lg:flex-row flex-col items-start lg:rtl:space-x-[unset] lg:space-x-4 space-y-4 lg:space-y-0">
                        <div className="flex items-center justify-center w-24 h-24 lg:rtl:ml-4">
                          <img
                            src={
                              imageUrl +
                              "?path=" +
                              datas?.productInformationDetail?.productImage?.[0]
                                ?.containerName +
                              "&name=" +
                              datas?.productInformationDetail?.productImage?.[0]
                                ?.image +
                              "&width=240&height=240"
                            }
                            alt={datas?.productInformationDetail?.name}
                            className="w-auto h-full mx-w-auto"
                            onError={({ currentTarget }) => {
                              currentTarget.onerror = null;
                              currentTarget.src = "/img/no-image.png";
                              currentTarget.alt = "no-image";
                            }}
                          />
                        </div>
                        <div>
                          <h5 className="mb-2 text-base font-medium text-black-300 line-clamp-3">
                            {datas?.productNameTrans
                              ? datas?.productNameTrans
                              : datas?.productName}
                          </h5>
                          <p className="mb-3 text-base font-bold text-black-300">
                            <CustomCurrencyFormatter
                              price={PriceCalculate(
                                datas?.basePrice,
                                datas?.productPrice,
                                datas?.productPrice !== 0 ? 1 : "",
                                datas?.taxType,
                                datas?.taxValue
                              )}
                            />
                          </p>
                          <span className="text-sm font-medium text-black-300">
                            {t("myprofile.Quantity")} : {datas?.quantity}
                          </span>
                        </div>
                      </div>
                      <div>
                        {datas?.quotationStatusId == 1 ? (
                          <div className="flex justify-center items-center space-x-1 rtl:space-x-[unset] bg-lightgreen px-4 py-2 rounded">
                            <img
                              src="/img/tick.svg"
                              alt="tick"
                              className="rtl:ml-1"
                            />
                            <p className="text-base font-semibold text-green-200">
                              {t("myprofile.approved")}
                            </p>
                          </div>
                        ) : (
                          <div className="flex justify-center items-center space-x-1 rtl:space-x-[unset] bg-pending px-4 py-2 rounded">
                            <img
                              src="/img/pending.svg"
                              alt="pending"
                              className="rtl:ml-1"
                            />
                            <p className="text-base font-semibold text-orange500">
                              {t("myprofile.pending")}
                            </p>
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                ))}
              </Link>
            ))
          : loader && (
              <div className="mx-auto mt-32 min-h-[40vh]">
                <div className="flex flex-col items-center justify-center gap-8 p-4 text-center sm:flex-row">
                  <img src="/img/no-data.svg" alt="no-data" />
                  <div>
                    <h5 className="mb-1 text-xl font-black text-black-300">
                      {t("myprofile.NoQuote")}
                    </h5>
                  </div>
                </div>
              </div>
            )}
        {loader == false && (
          <div className="border rounded border-color">
            {skele.map(
              (_, index) =>
                loader == false && (
                  <div
                    key={index}
                    role="status"
                    className="flex items-start justify-between px-6 py-4 border-b border-color animate-pulse"
                  >
                    <div className="md:flex md:items-center rtl:space-x-reverse md:space-x-4 md:space-y-0">
                      <div className="flex items-center justify-center w-32 h-24 max-w-full bg-gray-300 rounded dark:bg-gray-700">
                        <svg
                          className="w-10 h-10 text-gray-200 dark:text-gray-600"
                          aria-hidden="true"
                          xmlns="http://www.w3.org/2000/svg"
                          fill="currentColor"
                          viewBox="0 0 20 18"
                        >
                          <path d="M18 0H2a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h16a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2Zm-5.5 4a1.5 1.5 0 1 1 0 3 1.5 1.5 0 0 1 0-3Zm4.376 10.481A1 1 0 0 1 16 15H4a1 1 0 0 1-.895-1.447l3.5-7A1 1 0 0 1 7.468 6a.965.965 0 0 1 .9.5l2.775 4.757 1.546-1.887a1 1 0 0 1 1.618.1l2.541 4a1 1 0 0 1 .028 1.011Z" />
                        </svg>
                      </div>
                      <div className="w-full">
                        <div className="bg-gray-200 dark:bg-gray-700 mb-2.5 rounded-full max-w-[440px] h-2"></div>
                        <div className="bg-gray-200 dark:bg-gray-700 mb-2.5 rounded-full max-w-[460px] h-2"></div>
                        <div className="bg-gray-200 dark:bg-gray-700 rounded-full max-w-[360px] h-2"></div>

                        <div className="h-2 mt-5 bg-gray-200 rounded-full dark:bg-gray-700 w-52"></div>
                      </div>
                    </div>
                    <div className="w-32 h-10 bg-gray-300 rounded dark:bg-gray-700"></div>
                  </div>
                )
            )}
          </div>
        )}
      </div>
      <div className="relative flex flex-col w-full mt-0 sm:flex-row">
        {getQuotationlistApiCount > 5 && (
          <PaginationComp
            nPages1={nPages1}
            currentPage={currentPage}
            onPageChange={onPageChange}
          />
        )}
      </div>
    </div>
  );
}
