"use client";

// React and Next
import { useEffect, useState } from "react";
import { useRouter } from "nextjs-toploader/app";

// API
import { Checkout } from "@/api/clientsideaction/buyerapi";
import { FailedOrderRetryPaymentApi } from "@/api/clientsideaction/order";

// Utils
import { imageUrl } from "@/utilities/url";

// 3rd Party
// Redux
import { useDispatch, useSelector } from "react-redux";
import { GetPaymentListApi } from "./PaymentApi";
import {
  checkoutCouponData,
  CheckoutPaymentMethod,
  couponApplied,
  couponCode,
  couponDiscountValue,
  enteredCouponCode,
} from "@/redux/slices/checkout";

// Components
import ToastMessage from "@/Components/ToastMessage";
import CheckoutPriceDetails from "@/Components/Checkout/CheckoutPriceDetails";
import PaymentsLoader from "./PaymentsLoader";
import { useTranslation } from "react-i18next";

const Payment = ({ token, addonlist }) => {
  const dispatch = useDispatch();
  const { t } = useTranslation();
  const disable = false;
  const router = useRouter();
  const [paymentList, setPaymentList] = useState([]);
  const [isPaymentListLoading, setPaymentListStatus] = useState(false);
  const [paymentMethod, setPaymentMethod] = useState(0);
  const [btBuyNowLoader, setBtBuyNowLoader] = useState(false);
  const [cartdata, setCartdata] = useState([]);
  const cartPath = useSelector((s) => s.CheckoutRedux.cartItems);
  const cartItems = useSelector((s) => s.CheckoutRedux.finalCartItems);
  const enableBackOrder = useSelector((s) => s.CheckoutRedux.enableBackOrder);
  const loggedOutCheckout = useSelector(
    (s) => s.CheckoutRedux.loggedOutCheckout,
  );
  const failedOrderCheckout = useSelector(
    (s) => s.CheckoutRedux.failedOrderCheckout,
  );
  const enableFailedOrder = useSelector(
    (s) => s.CheckoutRedux.enableFailedOrder,
  );
  const defaultShippingAddress = useSelector(
    (s) => s.CheckoutRedux.finalShippingAddress,
  );
  const defaultbillingAddress = useSelector(
    (s) => s.CheckoutRedux.finalBillingAddress,
  );
  const couponData = useSelector((s) => s.CheckoutRedux.checkoutCouponData);
  const couponDiscountValues = useSelector(
    (s) => s.CheckoutRedux.couponDiscountValue,
  );
  const couponCodes = useSelector((s) => s.CheckoutRedux.couponCode);

  const cartProductType = cartItems?.some(
    (product) => product?.productType == "physical",
  );

  // Product Type
  const isMixedCart =
    cartdata?.some((p) => p.productType === "physical") &&
    cartdata?.some((p) => p.productType === "virtual");

  //  get payment method list
  useEffect(() => {
    let active = true;

    const fetchPaymentList = async () => {
      try {
        setPaymentListStatus(true);

        const res = await GetPaymentListApi();
        if (!active) return;

        if (res?.status === 1) {
          setPaymentList(res.data);
        } else {
          ToastMessage({ type: "error", message: res?.message });
        }
      } catch (err) {
        console.error(err);
      } finally {
        active && setPaymentListStatus(false);
      }
    };

    fetchPaymentList();

    return () => {
      active = false;
    };
  }, []);
  useEffect(() => {
    dispatch(CheckoutPaymentMethod(paymentMethod));
  }, []);
  useEffect(() => {
    setCartdata(cartItems);
  }, []); // or [cartItems] if cart can change

  // payment method onchange function
  const handlePaymentChange = (e) => {
    setPaymentMethod(e.target.value);
    dispatch(CheckoutPaymentMethod(+e.target.value));
  };

  const handleCouponClear = () => {
    dispatch(checkoutCouponData(""));
    dispatch(couponDiscountValue(0));
    dispatch(couponCode(""));
    dispatch(enteredCouponCode(""));
    dispatch(couponApplied(false));
  };

  const handlePayNow = () => {
    if (token) {
      if (enableFailedOrder && !enableBackOrder) {
        const obj = JSON.stringify({
          orderPrefixId: failedOrderCheckout?.orderPrefixId,
          paymentPluginId: paymentMethod,
          isMobile: failedOrderCheckout?.isMobile,
        });

        FailedOrderRetryPaymentApi(obj, setBtBuyNowLoader, router);
      } else if (!enableBackOrder) {
        if (defaultShippingAddress && defaultbillingAddress) {
          setBtBuyNowLoader(true);
          let email = localStorage.getItem("email");

          const obj = JSON.stringify({
            shippingFirstName: cartProductType
              ? defaultShippingAddress?.firstName
              : null,
            shippingLastName: cartProductType
              ? defaultShippingAddress?.lastName
              : null,
            shippingCompany: cartProductType
              ? defaultShippingAddress?.firstName
              : null,
            shippingAddress_1: cartProductType
              ? defaultShippingAddress?.address1
              : null,
            shippingAddress_2: cartProductType
              ? defaultShippingAddress?.address2
              : null,
            shippingCity: cartProductType ? defaultShippingAddress?.city : null,
            shippingPostCode: cartProductType
              ? defaultShippingAddress?.postcode?.toString()
              : null,
            shippingCountryId: cartProductType
              ? defaultShippingAddress?.countryId
              : null,
            phoneNumber: defaultShippingAddress?.phoneNo,
            paymentFirstName: defaultbillingAddress?.firstName,
            paymentLastName: defaultbillingAddress?.lastName,
            paymentCompany: defaultbillingAddress?.firstName,
            paymentAddress_1: defaultbillingAddress?.address1,
            paymentAddress_2: defaultbillingAddress?.address2,
            paymentCity: defaultbillingAddress?.city,
            paymentPostCode: defaultbillingAddress?.postcode?.toString(),
            paymentCountryId: defaultbillingAddress?.countryId,
            paymentZone: defaultbillingAddress?.zone?.name,
            shippingAddressFormat: "",
            emailId: email,
            paymentMethod: paymentMethod,
            productDetails: cartdata?.map((data) => ({
              ...data,
              quantity: data.productCount,
            })),
            couponCode: couponCodes,
            couponDiscountAmount: couponDiscountValues,
            couponData: couponData ? couponData : "",
            taxNumber: "",
            state: defaultShippingAddress?.zone?.zoneId,
            shippingZone: cartProductType
              ? defaultShippingAddress?.zone?.name
              : null,
            paymentMobileNumber: defaultbillingAddress?.phoneNo,
            productType: cartProductType ? "physical" : "virtual",
          });

          Checkout(obj, dispatch, setBtBuyNowLoader, handleCouponClear, router);
        } else {
          if (!defaultShippingAddress && !defaultbillingAddress) {
            ToastMessage({
              type: "error",
              message: `${t("toastMessage.shippingAndBillingAddress")}`,
            });
          } else if (!defaultShippingAddress) {
            ToastMessage({
              type: "error",
              message: `${t("toastMessage.shippingAddress")}`,
            });
          } else if (!defaultbillingAddress) {
            ToastMessage({
              type: "error",
              message: `${t("toastMessage.billingAddress")}`,
            });
          }
        }
      }
    } else {
      if (!enableBackOrder) {
        const obj = JSON.stringify({
          shippingFirstName: loggedOutCheckout?.addAddress?.name,
          shippingLastName: loggedOutCheckout?.addAddress?.lname,
          shippingCompany: loggedOutCheckout?.addAddress?.name,
          shippingAddress_1: loggedOutCheckout?.addAddress?.address,
          shippingAddress_2: loggedOutCheckout?.addAddress?.landMark,
          shippingCity: loggedOutCheckout?.addAddress?.city,
          shippingPostCode: loggedOutCheckout?.addAddress?.pincode?.toString(),
          shippingCountryId: loggedOutCheckout?.country?.value,
          phoneNumber: loggedOutCheckout?.addAddress?.number,
          paymentFirstName: loggedOutCheckout?.addAddress?.name,
          paymentLastName: loggedOutCheckout?.addAddress?.lname,
          paymentCompany: loggedOutCheckout?.addAddress?.name,
          paymentAddress_1: loggedOutCheckout?.addAddress?.address,
          paymentAddress_2: loggedOutCheckout?.addAddress?.landMark,
          paymentCity: loggedOutCheckout?.addAddress?.city,
          paymentPostCode: loggedOutCheckout?.addAddress?.pincode?.toString(),
          paymentCountryId: loggedOutCheckout?.country?.value,
          paymentZone: loggedOutCheckout?.state?.label,
          shippingAddressFormat: "",
          emailId: loggedOutCheckout?.addAddress?.email,
          paymentMethod: paymentMethod,
          productDetails: loggedOutCheckout?.cartdata,
          couponCode: "",
          couponDiscountAmount: 0,
          couponData: "",
          taxNumber: "",
          state: loggedOutCheckout?.state?.value,
          shippingZone: loggedOutCheckout?.state?.label,
          paymentMobileNumber: loggedOutCheckout?.addAddress?.number,
          password:
            loggedOutCheckout?.createAccount == true
              ? loggedOutCheckout?.password
              : undefined,
        });

        Checkout(obj, dispatch, setBtBuyNowLoader, () => {}, router);
      }
    }
  };

  return (
    <div className="m-auto mb-20 max-w-screen-xl px-4 py-4 sm:px-10">
      <h3 className="mb-[10px] p-[20px_0] text-[20px] font-semibold leading-[24px] text-[#202124]">
        {t("checkout.Pay")}
      </h3>
      <div className="grid grid-cols-[1fr_auto] gap-[60px] max-xl:gap-[16px] max-lg:grid-cols-1">
        <div className="w-full">
          {isPaymentListLoading ? (
            <PaymentsLoader />
          ) : paymentList?.length ? (
            paymentList?.map((payment, i) => {
              const isCOD = payment?.pluginName === "CashOnDelivery";
              const disableCOD = isCOD && isMixedCart;
              return (
                (payment?.pluginName != "CashOnDelivery" ||
                  cartProductType) && (
                  <div className={`mb-[20px] last-of-type:mb-0`} key={i}>
                    <label
                      htmlFor={payment?.id}
                      className={`flex items-center gap-[10px] rounded-[12px] border border-solid border-[#D6D6D6] p-[20px] focus:!border-[#212121] peer-checked:border-[#212121] max-sm:p-[16px] ${
                        disableCOD
                          ? "cursor-default opacity-50"
                          : "cursor-pointer"
                      } ${paymentMethod == payment?.id ? "border-black" : ""}`}
                    >
                      <div className="grid min-w-[24px]">
                        <input
                          type="radio"
                          name="payment"
                          id={payment?.id}
                          value={payment?.id}
                          checked={paymentMethod == payment?.id}
                          onChange={(e) => handlePaymentChange(e)}
                          disabled={disableCOD}
                          className={`print-color-auto peer relative m-0 h-[24px] w-[24px] min-w-[24px] appearance-none rounded-full border-[4px] !border-white bg-white bg-contain bg-center bg-no-repeat align-top outline outline-1 !outline-offset-0 outline-[#CCCCCC] checked:h-[22px] checked:w-[22px] checked:min-w-[22px] checked:border-[4px] checked:!border-white checked:!bg-[#000000] checked:bg-none checked:outline checked:outline-[2px] checked:!outline-[#000000] focus:ring-0 ${disableCOD ? "cursor-default" : "cursor-pointer"}`}
                        />
                      </div>
                      <div className="grow">
                        <div className="flex items-start gap-[10px]">
                          <div className="flex grow items-center gap-[10px]">
                            <img
                              src={
                                imageUrl +
                                "?path=" +
                                payment?.pluginAvatarPath +
                                "&name=" +
                                payment?.pluginAvatar +
                                "&width=300&height=300"
                              }
                              alt={payment?.pluginName}
                              onError={({ currentTarget }) => {
                                currentTarget.onerror = null;
                                currentTarget.src = "/img/no-image.png";
                                currentTarget.alt = "no-image";
                              }}
                              className="h-[40px] w-[40px] object-contain"
                            />
                            <h4 className="text-[24px] font-semibold leading-[30px] text-[#1D1D1F]">
                              {payment?.pluginName}
                            </h4>

                            {/* Tooltip */}
                            <div
                              className={`group relative w-fit ${
                                disableCOD &&
                                payment.pluginName == "CashOnDelivery"
                                  ? "block"
                                  : "hidden"
                              }`}
                            >
                              <img
                                src="/img/infotip.svg"
                                alt="info"
                                className="h-5 w-5 !cursor-pointer opacity-50"
                              />

                              {/* Tooltip content*/}
                              <div className="absolute -top-10 left-1/2 hidden -translate-x-1/2 whitespace-nowrap rounded bg-black px-3 py-1 text-xs text-white group-hover:block">
                                Only available for physical products
                              </div>
                            </div>
                          </div>
                          {/* <div className="flex items-center gap-[5px] bg-[#E0EEDE] p-[2px_6px] rounded min-w-[75px] font-normal text-[#1B5214] text-[14px] leading-6">
                  <img src="/img/check-green.svg" alt="" />
                  <p>Active</p>
                </div> */}
                        </div>
                        {/* <p className="font-normal text-[#434445] text-[16px] leading-[24px]">
                A trusted global payment system for sending, receiving,
                and managing money.
              </p> */}
                      </div>
                    </label>
                  </div>
                )
              );
            })
          ) : (
            <div className="mb-[20px] text-black last-of-type:mb-0">
              {t("checkout.paymentsNotEnabled")}
            </div>
          )}
        </div>

        {/* Price Details */}
        <CheckoutPriceDetails
          token={token}
          handlePayNow={handlePayNow}
          cartPath={cartPath}
          addonlist={addonlist}
          loader={btBuyNowLoader}
          valid={paymentMethod != 0 ? true : false}
        />
      </div>
    </div>
  );
};

export default Payment;
