import axiosInstance from "@/api/interceptor";
import ToastMessage from "@/Components/ToastMessage";
import { getWishListCount } from "@/redux/slices/userSlice";

//*widget api
export const widgetPostApi = async (id, dispatch, wishCount) => {
  let requestData = {
    productId: id,
    productOptionValueId: "",
  };
  const data = await axiosInstance.post(
    `/customer/add-product-to-wishlist`,
    requestData,
  );
  if (data?.data?.status == 1) {
    dispatch(getWishListCount(wishCount + 1));

    ToastMessage({ type: "success", message: data?.data?.message });
  }
  //   return data?.data;
};

export const widgetDeleteApi = async (id, dispatch, wishCount) => {
  const data = await axiosInstance.delete(
    `/customer/wishlist-product-delete/${id}`,
  );
  if (data?.data?.status == 1) {
    dispatch(getWishListCount(wishCount - 1));
    ToastMessage({ type: "success", message: data?.data?.message });
  }
};

export const productCompareApi = async (
  data,
  compareIds,
  setCompareTrigger,
  comparetrigger,
  productIds,
) => {
  const result = await axiosInstance.get(
    "product-store/product-compare?data=" + data + "&productId=" + compareIds,
  );
  if (result?.data?.status == 1) {
    if (result?.data?.message == "Please Choose Products from Same Category") {
      ToastMessage({ type: "error", message: result.data.message });
      localStorage.setItem("compareId", JSON.stringify(productIds));
    } else {
      localStorage.setItem("compareId", JSON.stringify(compareIds));
      setCompareTrigger(comparetrigger + 1);
      ToastMessage({ type: "success", message: result.data.message });
    }
  }
};

export const getWidgetDetail = async (
  slug,
  setWidgetData,
  setWidgetList,
  setLoader,
  limit,
  offset,
  setOffset,
) => {
  setLoader(true);
  const result = await axiosInstance.get(
    `list/widget-detail/${slug}?limit=${limit}&offset=${offset}`,
  );
  if (result?.data?.status == 1) {
    if (result?.data?.message == "invalid widget Id") {
      notFound();
    } else {
      setWidgetData(result?.data?.data);
      const fetched = result?.data?.data?.widgetItems || [];
      if (fetched.length > 0) {
        setWidgetList((prev) => [...prev, ...fetched]);
        setOffset((prev) => prev + limit);
      }
    }
  }
  setLoader(false);
};

export const getWidgetDetailCount = async (slug, setWidgetListCount) => {
  const result = await axiosInstance.get(`list/widget-detail/${slug}?count=1`);
  if (result?.data?.status == 1) {
    setWidgetListCount(result?.data?.data);
  }
};

export const WidgetProductList = async (setProductList, setLoader) => {
  const result = await axiosInstance.get(`list/widget-list`);
  if (result?.data?.status == 1) {
    setProductList(result?.data?.data);
    setLoader(true);
  } else {
    setLoader(true);
  }
};

export function GetCompareId() {
  if (typeof window !== "undefined") {
    const compareId = localStorage.getItem("compareId")
      ? localStorage.getItem("compareId")
      : undefined;
    if (compareId) {
      return JSON.parse(compareId);
    } else {
      return compareId;
    }
  }
}
