"use client";
// React and Next
import { useEffect, useState } from "react";

// 3rd Party
import Select from "react-select";
import { useRouter } from "nextjs-toploader/app";

// API
import {
  Get_Support_category_List_Api,
  Get_Support_subcategory_List_Api,
  Media_Post_api,
  Support_ticket_Post_api,
} from "./SupportApi";

// Utils
import { allowImageSize, imageUrl } from "@/utilities/url";

// Components
import ToastMessage from "@/Components/ToastMessage";
import Sidebar from "./SideBar";
import Breadcrumb from "@/Components/Breadcrumbs";
import { Button_loader } from "./Loader/ButtonLoader";
import { Page_loader } from "./Loader/PageLoader";
import { useTranslation } from "react-i18next";

const Create_Support_Ticket = () => {
  const [Support_CategoryList_Api_Result, setSupport_CategoryList_Api_Result] =
    useState([]);

  const [Parent_category_Id, setParent_category_Id] = useState("");
  const [ticket_topic_options, setticket_topic_options] = useState([]);
  const [ticket_topic_value, setticket_topic_value] = useState("");

  const [Subcategory_List_Api_Result, setSubcategory_List_Api_Result] =
    useState([]);
  const [subcategory_options, setsubcategory_options] = useState([]);
  const [subcategory_value, setsubcategory_value] = useState("");

  const [submit, setsubmit] = useState(0);

  const [ticketSubject_value, setticketSubject_value] = useState("");
  const [ticket_des_value, setticket_des_value] = useState("");

  const [ticket_topic_error, setticket_topic_error] = useState("");
  const [subcategory_error, setsubcategory_error] = useState("");
  const [ticket_subject_error, setticket_subject_error] = useState("");
  const [ticket_des_error, setticket_des_error] = useState("");

  const [selectedFile, setSelectedFile] = useState(null);

  const [media_Api_result, setmedia_Api_result] = useState([]);

  const [submitLoader, setsubmitLoader] = useState(false);
  const [media_Loader, setmedia_Loader] = useState(false);
  const { t } = useTranslation();

  const router = useRouter();

  useEffect(() => {
    Get_Support_category_List_Api(
      setSupport_CategoryList_Api_Result,
      Parent_category_Id
    );
  }, []);

  const updateSelectOptions = (apiResult, setOptions) => {
    const selectOptions = apiResult.map((item) => ({
      label: item.categoryName,
      value: item.id,
    }));
    setOptions(selectOptions);
  };
  useEffect(() => {
    updateSelectOptions(
      Support_CategoryList_Api_Result,
      setticket_topic_options
    );
    updateSelectOptions(Subcategory_List_Api_Result, setsubcategory_options);
  }, [Support_CategoryList_Api_Result, Subcategory_List_Api_Result]);

  const colourStylesError = (hasError = false) => ({
    control: (styles, state) => ({
      ...styles,
      cursor: "pointer",
      borderRadius: "0.25rem",
      height: "2.75rem",
      boxShadow: state.isFocused ? 0 : 0,
      border: hasError ? "1px solid red" : "1px solid #d1d5db", // Default gray border
      "&:hover": {
        border: hasError ? "1px solid red" : "1px solid #a1a1aa", // Lighter hover
      },
    }),
    option: (base, state) => ({
      ...base,
      cursor: "pointer", // Pointer cursor for dropdown options
      backgroundColor: state.isSelected ? "#ffffff" : "#ffffff", // No background change on selection
      "&:hover": { backgroundColor: "#F9F9FC" }, // Background color on hover
      fontSize: ".8125rem",
      padding: "8px",
      color: "#333333", // Text color
    }),
  });

  const handlechange_ticket_topic = (e) => {
    setticket_topic_value(e);
    Get_Support_subcategory_List_Api(e?.value, setSubcategory_List_Api_Result);
  };

  const handlechange_subcategory = (e) => {
    setsubcategory_value(e);
  };

  ///pricing setup
  useEffect(() => {
    if (submit == 1) {
      validation();
    }
  }, [
    ticket_topic_value,
    subcategory_value,
    ticketSubject_value,
    ticket_des_value,
  ]);

  const validation = () => {
    let validateObj = {
      ticket_topic_value: true,
      subcategory_value: true,
      ticketSubject_value: true,
      ticket_des_value: true,
    };

    if (!["", null, undefined].includes(ticket_topic_value)) {
      setticket_topic_error("");
      validateObj.ticket_topic_value = true;
    } else {
      setticket_topic_error(t("support.EnterTopic"));
      validateObj.ticket_topic_value = false;
    }

    if (!["", null, undefined].includes(subcategory_value)) {
      setsubcategory_error("");
      validateObj.subcategory_value = true;
    } else {
      setsubcategory_error(t("support.EnterSubtopic"));
      validateObj.subcategory_value = false;
    }

    if (!["", null, undefined].includes(ticketSubject_value)) {
      setticket_subject_error("");
      validateObj.ticketSubject_value = true;
    } else {
      setticket_subject_error(t("support.EnterSubject"));
      validateObj.ticketSubject_value = false;
    }

    if (!["", null, undefined].includes(ticket_des_value)) {
      setticket_des_error("");
      validateObj.ticket_des_value = true;
    } else {
      setticket_des_error(t("support.EnterDescription"));
      validateObj.ticket_des_value = false;
    }

    if (
      validateObj.ticket_topic_value == true &&
      validateObj.subcategory_value == true &&
      validateObj.ticketSubject_value == true &&
      validateObj.ticket_des_value == true
    ) {
      return true;
    } else {
      return false;
    }
  };

  const handleclick_submit = (e) => {
    setsubmit(1);
    if (validation()) {
      setsubmitLoader(true);
      const attachments_array = media_Api_result.length
        ? media_Api_result.map(({ file, path }) => ({
            fileName: file,
            filePath: path,
          }))
        : [];

      let obj = {
        attachments: attachments_array,
        categoryId: ticket_topic_value?.value,
        description: ticket_des_value,
        message: ticket_des_value,
        subCategoryId: subcategory_value?.value,
        subject: ticketSubject_value,
      };
      Support_ticket_Post_api(obj, router, setsubmitLoader);
    }
  };

  //  File Input Handler
  const handleFileChange = (e) => {
    const file = e.target.files?.[0];
    if (file) {
      const allowedTypes = [
        "application/pdf",
        "image/jpeg",
        "image/png",
        "image/jpg",
      ];
      // const maxSize = 5 * 1024 * 1024; // 5MB

      if (!allowedTypes.includes(file.type)) {
        ToastMessage({
          type: "error",
          message: "Unsupported file format. Please upload PDF or IMAGE.",
        });
        return;
      }

      if (Math.round(file.size / 1024) > allowImageSize) {
        ToastMessage({ type: "error", message: "File size exceeds 2MB." });
        return;
      }

      setSelectedFile(file);

      const reader = new FileReader();
      reader.onloadend = () => {
        const base64Data = reader.result;
        const isPDF = file.type === "application/pdf";

        const obj = {
          documentId: 3,
          fileName: file.name,
          fileType: isPDF ? 1 : 0,
          image: base64Data,
          path: isPDF ? "documents/" : "ticket/",
        };
        setmedia_Loader(true);
        Media_Post_api(obj, setmedia_Api_result, setmedia_Loader);
      };

      reader.readAsDataURL(file);
    }
  };

  const handlechange_ticketSubject = (e) => {
    // Trim leading spaces and update the value
    let value = e.target.value.trimStart();
    setticketSubject_value(value);
  };

  const handlechange_ticket_des = (e) => {
    // Trim leading spaces and update the value
    let value = e.target.value.trimStart();
    setticket_des_value(value);
  };

  const handleclick_cancel = (e) => {
    router.push(`/account-settings/support`);
  };

  const handleclick_delete_file = (e, index) => {
    e.preventDefault();
    setmedia_Api_result((prev) => prev.filter((_, i) => i !== index));
  };

  const breadCrumb = [
    {
      text: t("Breadcrumb.home"),
      url: "/",
    },
    {
      text: t("Breadcrumb.accountSettings"),
      url: "/account-settings",
    },
    {
      text: t("Breadcrumb.Support"),
      url: "/account-settings/support",
    },
    {
      text: t("Breadcrumb.Ticket"),
      url: "javascript:void(0)",
    },
  ];

  return (
    <div className="mx-auto px-4 py-2.5 max-w-screen-2xl">
      <Breadcrumb breadCrumb={breadCrumb} />

      <main className="flex rtl:space-x-[unset] md:space-x-8 mx-auto mt-4 mb-7">
        <Sidebar />
        <div className="w-full p-0 md:p-0 md:pe-8">
          <div className="bg-[#F5F6F8] p-[20px_25px] rounded-[8px]">
            <p className="font-normal text-[#191919] text-sm leading-[17px]">
              {t("support.Content")}
            </p>
          </div>

          <div className="mt-[30px]">
            {/* <div className='gap-[20px] grid grid-cols-[1fr_auto] max-md:grid-cols-1'> */}

            <div className="mb-[20px] max-w-[660px] cursor-pointer">
              <label
                htmlFor=""
                className="mb-1 font-medium text-[#353740] text-sm"
              >
                {t("support.Category")}*{" "}
                <button placement="top" role="button" className="tooltip-btn">
                  <img src="assets/imgs/info-outline.svg" alt="" />
                </button>
              </label>

              <Select
                name="select"
                styles={colourStylesError(ticket_topic_error !== "")}
                className={`block text-black w-full px-0 mt-0 bg-transparent border-0 appearance-none z-1 focus:outline-none focus:ring-0 focus:border-black border-[#e6e6e8] rounded-lg cursor-pointer ${
                  ticket_topic_error !== ""
                    ? "[&_.css-13cymwt-control]:!border-red-600"
                    : "[&_.css-13cymwt-control]:!border-[#E6E6E8]"
                }`}
                options={ticket_topic_options}
                onChange={handlechange_ticket_topic}
                value={ticket_topic_value}
                placeholder={""}
              />

              <p className="mt-1 text-xs font-normal text-red-invalid">
                {ticket_topic_error}
              </p>
            </div>

            {["", null, undefined, 0].includes(subcategory_options.length) ? (
              <></>
            ) : (
              <div className="mb-[20px] max-w-[660px] cursor-pointer">
                <label
                  htmlFor=""
                  className="mb-1 font-medium text-[#353740] text-sm"
                >
                  {t("support.SubCategory")}*{" "}
                  <button placement="top" role="button" className="tooltip-btn">
                    <img src="assets/imgs/info-outline.svg" alt="" />
                  </button>
                </label>

                <Select
                  name="select"
                  styles={colourStylesError(subcategory_error !== "")}
                  className={`block text-black w-full px-0 mt-0 bg-transparent border-0 appearance-none z-1 focus:outline-none focus:ring-0 focus:border-black border-[#e6e6e8] rounded-l cursor-pointer ${
                    subcategory_error !== ""
                      ? "[&_.css-13cymwt-control]:!border-red-600"
                      : "[&_.css-13cymwt-control]:!border-[#E6E6E8]"
                  } `}
                  options={subcategory_options}
                  onChange={handlechange_subcategory}
                  value={subcategory_value}
                  placeholder={""}
                />
                <p className="mt-1 text-xs font-normal text-red-invalid">
                  {subcategory_error}
                </p>
              </div>
            )}
            <div className="mb-[20px]">
              <label
                htmlFor=""
                className="mb-1 font-medium text-[#353740] text-sm"
              >
                {t("support.Subject")}*{" "}
                <button placement="top" role="button" className="tooltip-btn">
                  <img src="assets/imgs/info-outline.svg" alt="" />
                </button>
              </label>

              <input
                type="text"
                className={`border text-black border-solid rounded-md  h-[40px] resize-none w-full ${
                  ticket_subject_error !== ""
                    ? "border-red-600"
                    : "border-[#E6E6E8]"
                }`}
                value={ticketSubject_value}
                onChange={(e) => handlechange_ticketSubject(e)}
              />
              <p className="mt-1 text-xs font-normal text-red-invalid">
                {ticket_subject_error}
              </p>
            </div>

            <div className="mb-[20px]">
              <label
                htmlFor=""
                className="mb-1 font-medium text-[#353740] text-sm"
              >
                {t("support.Description")}*{" "}
                <button placement="top" role="button" className="tooltip-btn">
                  <img src="assets/imgs/info-outline.svg" alt="" />
                </button>
              </label>
              <div className="gap-[20px] grid grid-cols-[1fr_auto] max-lg:grid-cols-1">
                <div>
                  <textarea
                    type="text"
                    className={`text-black rounded-md border border-solid border-[#E6E6E8] h-[200px] resize-none w-full ${
                      ticket_des_error !== ""
                        ? "border-red-600"
                        : "border-[#E6E6E8]"
                    }`}
                    value={ticket_des_value}
                    onChange={(e) => handlechange_ticket_des(e)}
                  ></textarea>
                  <p className="mt-1 text-xs font-normal text-red-invalid">
                    {ticket_des_error}
                  </p>
                </div>

                <div className="group relative flex flex-col justify-center items-center bg-[#FCFCFC] p-[25px_20px] border border-[#BEC0C5] border-dashed rounded-[8px] w-full min-w-[385px] max-sm:min-w-[300px] h-[200px]">
                  {media_Loader ? (
                    <Page_loader width_val={"30px"} height_val={"30px"} />
                  ) : (
                    <>
                      <input
                        type="file"
                        onChange={handleFileChange}
                        className="top-0 left-0 z-[2] absolute opacity-0 w-full h-full text-[0px] cursor-pointer"
                      />
                      <h3 className="mb-[8px] font-medium text-[#191919] text-sm text-center leading-[20px]">
                        {t("support.Drag")}
                      </h3>
                      <p className="font-normal text-[#757575] text-[13px] text-center leading-[20px]">
                        {t("support.Format")}
                      </p>
                      <p className="hidden top-0 left-0 z-1 absolute place-items-center group-hover:grid bg-[#F6F6F6] rounded-[8px] w-full h-full font-medium text-[#191919] text-[14px] text-center leading-[20px]">
                        Drop here
                      </p>
                    </>
                  )}
                </div>
              </div>
            </div>

            <div className="flex flex-wrap items-center gap-[20px] mt-[16px]">
              {Array.isArray(media_Api_result) &&
                media_Api_result.length > 0 &&
                media_Api_result.map((val, index) => (
                  <div key={index} className="flex items-center gap-2">
                    {/* <img src="/img/pdf-doc.svg" alt="pdf" /> */}
                    {val.file?.toLowerCase().endsWith(".pdf") ? (
                      <img
                        src="/img/pdf-doc.svg"
                        alt="pdf"
                        className="w-5 h-5"
                      />
                    ) : (
                      <img
                        src={
                          imageUrl +
                          "?path=" +
                          val.path +
                          "&name=" +
                          val.file +
                          "&width=20&height=20"
                        }
                        alt={val.file}
                        className="object-cover w-5 h-5"
                      />
                    )}

                    <p className="font-normal text-[#191919] text-[14px] underline underline-offset-4 leading-5">
                      {val.file}
                    </p>
                    <a
                      role="button"
                      onClick={(e) => handleclick_delete_file(e, index)}
                      className="cursor-pointer"
                    >
                      <img src="/img/remove-doc.svg" alt="remove" />
                    </a>
                  </div>
                ))}
            </div>
          </div>

          <div className="flex max-lg:flex-wrap justify-end items-center gap-4 mt-4 mb-[23px] w-full">
            <div className="flex items-center space-x-[10px] rtl:space-x-[unset]">
              <button
                className="flex justify-center items-center bg-[#E8E8E8] hover:bg-[#f5f5f5] px-[20px] border border-[#E8E8E8] rounded-lg h-10 font-medium text-[#191919] text-sm transition-all duration-300 btn"
                onClick={(e) => handleclick_cancel(e)}
              >
                {t("support.Cancel")}
              </button>
              {submitLoader ? (
                <Button_loader
                  class_Name={`flex justify-center items-center  px-[20px] border bg-[#191919] border-black hover:bg-[#3a3939] transition-all duration-300 rounded-lg h-10 font-medium text-sm text-white btn`}
                  width_val={"140px"}
                />
              ) : (
                <button
                  className="flex justify-center items-center bg-[#191919] hover:bg-[#3a3939] px-[20px] border border-black rounded-lg h-10 font-medium text-white text-sm transition-all duration-300 btn"
                  onClick={(e) => handleclick_submit(e)}
                  style={{ width: "140px" }}
                >
                  {t("support.Submit")}
                </button>
              )}
            </div>
          </div>
        </div>
      </main>
    </div>
  );
};

export default Create_Support_Ticket;
