"use client";
//React and Next
import { useEffect, useState } from "react";
//3rd Party
import StarRatings from "react-star-ratings";
//API
import { getVendorRatingApi } from "./serverSide";

export default function VendorRating({ vendorId }) {
  const [ratingReview, setRatingView] = useState(0);
  const hanldeProductDetail = async (slug) => {
    const getProductDetail = await getVendorRatingApi(slug);
    if (getProductDetail?.status == 1) {
      setRatingView(Number(getProductDetail?.data?.RatingCount));
    }
  };

  useEffect(() => {
    if (vendorId) {
      hanldeProductDetail(vendorId);
    }
  }, [vendorId]);

  return (
    <StarRatings
      rating={ratingReview}
      starEmptyColor="#d8d8eb"
      starRatedColor="black"
      starHoverColor="#333"
      starDimension="16px"
      starSpacing="3px"
      numberOfStars={5}
      // changeRating={handleChangeStar}
      name="rating"
      svgIconPath="M12.489,5.03a.665.665,0,0,0-.572-.457L8.3,4.245,6.873.9A.666.666,0,0,0,5.649.9L4.22,4.245.6,4.573A.666.666,0,0,0,.227,5.738l2.732,2.4-.806,3.548a.665.665,0,0,0,.99.72l3.117-1.864L9.377,12.4a.666.666,0,0,0,.991-.72L9.562,8.134l2.732-2.4A.666.666,0,0,0,12.489,5.03ZM6.321,10.5"
      svgIconViewBox="0 0 12.522 12"
    />
  );
}
