"use client";

// React and Next
import { Fragment, useState } from "react";
import Link from "next/link";

// Utils
import { imageUrl } from "@/utilities/url";

// 3rd Party
import StarRatings from "react-star-ratings";

// Components
import CustomCurrencyFormatter from "@/Components/CustomCurrencyFormatter";
import { useTranslation } from "react-i18next";

export default function AddRating({
  productDetail,
  review,
  reviewImage,
  reviewVideo,
  preReviewImage,
  preReviewVideo,
  handleChangeStar,
  starRating,
  setReviewCommand,
  reviewCommand,
  handleClose,
  handleReviewClick,
  handleReviewMedia,
  handleRemoveReviewImage,
  handleRemoveReviewVideo,
  handleRemovePreReviewImage,
  handleRemovePreReviewVideo,
  reviewCommandError,
}) {
  const [AddLoader, setAddLoader] = useState(false);
  const { t } = useTranslation();

  let newImage = [];
  let newVideo = [];
  let newArray = [];

  const [media, setMedia] = useState(null);
  const [selectedImageIndex, setSelectedImageIndex] = useState(0);
  const [reszize, setResize] = useState(false);

  const handleResizeMedia = (value, index) => {
    setResize(true);
    setMedia(value);
    setSelectedImageIndex(index + 1);
  };

  const handleImageChange = (value, index) => {
    setMedia(value);
    setSelectedImageIndex(index + 1);
  };

  const handlePreviousImage = () => {
    newArray?.forEach((imagelist, index) => {
      if (imagelist?.fileName == media?.fileName) {
        if (index == 0) {
          setSelectedImageIndex(newArray?.length);
          const lastImage = newArray?.at(-1);
          setMedia(JSON?.parse(JSON?.stringify(lastImage)));
        } else {
          setSelectedImageIndex(index);
          const previousImage = newArray?.find((img, i) => i == index - 1);
          setMedia(JSON?.parse(JSON?.stringify(previousImage)));
        }
      } else if (imagelist == media) {
        if (index == 0) {
          setSelectedImageIndex(newArray?.length);
          const lastImage = newArray?.at(-1);
          setMedia(lastImage);
        } else {
          setSelectedImageIndex(index);
          const previousImage = newArray?.find((img, i) => i == index - 1);
          setMedia(previousImage);
        }
      }
    });
  };

  const handleNextImage = () => {
    newArray?.forEach((imagelist, index) => {
      if (imagelist == media) {
        if (index + 1 == newArray?.length) {
          setSelectedImageIndex(1);
          const firstImage = newArray?.at(0);
          setMedia(firstImage);
        } else {
          setSelectedImageIndex(index + 2);
          const nextImage = newArray?.find((img, i) => i == index + 1);
          setMedia(nextImage);
        }
      }
    });
  };

  const handleImageVideo = () => {
    preReviewImage.map((value, index) => {
      if (value?.fileType == "image") {
        newImage.push(value);
      }
    });

    reviewImage.map((value, index) => {
      if (value?.startsWith("data:image/")) {
        newImage.push(value);
      }
    });

    preReviewVideo.map((value, index) => {
      if (value?.fileType == "video") {
        newVideo.push(value);
      }
    });

    reviewVideo.map((value, index) => {
      if (value?.startsWith("data:video/")) {
        newVideo.push(value);
      }
    });

    newArray = [...newImage, ...newVideo];

    return newArray.map((value, index) => (
      <Fragment key={index}>
        {value?.fileType == "image" && (
          <div className="group-item relative h-20 w-20 min-w-20 cursor-pointer rounded">
            <img
              src={
                imageUrl +
                "?path=" +
                value?.filePath +
                "&name=" +
                value?.fileName +
                "&width=300&height=300"
              }
              alt={value?.fileName}
              className="h-full w-full object-cover"
            />
            <div className="item absolute left-0 top-0 flex h-full w-full items-start justify-end space-x-1 rounded bg-[#00000029] p-1">
              <a
                onClick={() => handleRemovePreReviewImage(value, index)}
                className="flex h-[18px] w-[18px] items-center justify-center rounded-full bg-white shadow-md hover:bg-slate-100"
              >
                <img src="/img/close.svg" alt="close" className="w-2.5" />
              </a>
              <a
                onClick={() => handleResizeMedia(value, index)}
                className="flex h-[18px] w-[18px] items-center justify-center rounded-full bg-white shadow-md hover:bg-slate-100"
              >
                <img src="/img/expand.svg" alt="expand" className="w-2.5" />
              </a>
            </div>
          </div>
        )}
        {String(value)?.startsWith("data:image/") && (
          <div className="group-item relative h-20 w-20 min-w-20 cursor-pointer rounded">
            <img
              src={value}
              alt="image"
              className="h-full w-full object-cover"
            />
            <div className="item absolute left-0 top-0 flex h-full w-full items-start justify-end space-x-1 rounded bg-[#00000029] p-1">
              <a
                onClick={() => handleRemoveReviewImage(value, index)}
                className="flex h-[18px] w-[18px] items-center justify-center rounded-full bg-white shadow-md hover:bg-slate-100"
              >
                <img src="/img/close.svg" alt="close" className="w-2.5" />
              </a>
              <a
                onClick={() => handleResizeMedia(value, index)}
                className="flex h-[18px] w-[18px] items-center justify-center rounded-full bg-white shadow-md hover:bg-slate-100"
              >
                <img src="/img/expand.svg" alt="expand" className="w-2.5" />
              </a>
            </div>
          </div>
        )}
        {value?.fileType == "video" && (
          <div className="group-item relative h-20 w-20 min-w-20 cursor-pointer rounded">
            <video
              src={`${process.env.NEXT_PUBLIC_SERVER_URL}media/video-preview-s3?name=${value?.fileName}&path=${value?.filePath}`}
              className="h-full w-full object-cover"
            />
            <div className="item absolute left-0 top-0 flex h-full w-full items-start justify-end space-x-1 rounded bg-[#00000029] p-1">
              <a
                onClick={() => handleRemovePreReviewVideo(value, index)}
                className="flex h-[18px] w-[18px] items-center justify-center rounded-full bg-white shadow-md hover:bg-slate-100"
              >
                <img src="/img/close.svg" alt="close" className="w-2.5" />
              </a>
              <a
                onClick={() => handleResizeMedia(value, index)}
                className="flex h-[18px] w-[18px] items-center justify-center rounded-full bg-white shadow-md hover:bg-slate-100"
              >
                <img src="/img/expand.svg" alt="expand" className="w-2.5" />
              </a>
            </div>
          </div>
        )}
        {String(value)?.startsWith("data:video/") && (
          <div className="group-item relative h-20 w-20 min-w-20 cursor-pointer rounded">
            <video src={value} className="h-full w-full object-cover" />
            <div className="item absolute left-0 top-0 flex h-full w-full items-start justify-end space-x-1 rounded bg-[#00000029] p-1">
              <a
                onClick={() => handleRemoveReviewVideo(value, index)}
                className="flex h-[18px] w-[18px] items-center justify-center rounded-full bg-white shadow-md hover:bg-slate-100"
              >
                <img src="/img/close.svg" alt="close" className="w-2.5" />
              </a>
              <a
                onClick={() => handleResizeMedia(value, index)}
                className="flex h-[18px] w-[18px] items-center justify-center rounded-full bg-white shadow-md hover:bg-slate-100"
              >
                <img src="/img/expand.svg" alt="expand" className="w-2.5" />
              </a>
            </div>
          </div>
        )}
      </Fragment>
    ));
  };

  const image = productDetail?.productOriginalImage?.find(
    (img) => img?.defaultImage == 1,
  );

  return (
    <div
      aria-modal="true"
      id="static-modal"
      data-modal-backdrop="static"
      tabIndex="-1"
      aria-hidden="true"
      className={`${
        review
          ? "z-100 bg-light-modal fixed left-0 right-0 top-0 flex h-full max-h-full w-full items-center justify-center overflow-y-auto overflow-x-hidden px-4 md:inset-0 "
          : " hidden"
      }`}
    >
      <div className="max-w-800 relative max-h-full w-full">
        {/* <!-- Modal content --> */}
        <div className="relative bg-white shadow dark:bg-gray-700">
          {/* <!-- Modal header --> */}
          <div className="absolute right-6 top-6 rtl:left-6 rtl:right-[unset]">
            <button
              type="button"
              data-modal-hide="static-modal"
              onClick={handleClose}
            >
              <img src="/img/modal-x.svg" alt="modal-x" />
            </button>
          </div>

          {/* <!-- Modal body --> */}
          <div className="padding-8 p-4">
            <h3 className="text-black-300 mb-1.5 text-xl font-bold">
              {t("productcontent.WrtRev")}
            </h3>
            {/* <p className="text-sm font-normal leading-relaxed text-black-300">
                            {t("productcontent.PstQCont")}
                        </p> */}
            <div className="bg-lightgrey my-6 flex space-x-4 p-4 rtl:space-x-[unset]">
              {productDetail !== undefined && (
                <div className="h-[116px] w-[120px] rounded-md rtl:ml-4">
                  <img
                    src={
                      imageUrl +
                      "?path=" +
                      image?.containerName +
                      "&name=" +
                      image?.image +
                      "&width=200&height=200"
                    }
                    alt={image?.containerName}
                    className="m-auto h-full w-full max-w-full rounded-md object-contain"
                  />
                </div>
              )}
              <div className="flex flex-col justify-between">
                {productDetail !== undefined && (
                  <div className="flex flex-col space-y-2.5">
                    <h3 className="text-boldblack text-base font-semibold">
                      {productDetail?.productNameTrans
                        ? productDetail?.productNameTrans
                        : productDetail?.name}
                    </h3>
                    <p className="text-boldblack mb-1 text-lg font-black">
                      <CustomCurrencyFormatter price={productDetail?.price} />
                    </p>
                  </div>
                )}
                <div className="flex items-center space-x-1.5">
                  <StarRatings
                    rating={starRating}
                    starEmptyColor="#d8d8eb"
                    starRatedColor="black"
                    starHoverColor="#333"
                    starDimension="16px"
                    starSpacing="3px"
                    numberOfStars={5}
                    changeRating={handleChangeStar}
                    name="rating"
                    svgIconPath="M12.489,5.03a.665.665,0,0,0-.572-.457L8.3,4.245,6.873.9A.666.666,0,0,0,5.649.9L4.22,4.245.6,4.573A.666.666,0,0,0,.227,5.738l2.732,2.4-.806,3.548a.665.665,0,0,0,.99.72l3.117-1.864L9.377,12.4a.666.666,0,0,0,.991-.72L9.562,8.134l2.732-2.4A.666.666,0,0,0,12.489,5.03ZM6.321,10.5"
                    svgIconViewBox="0 0 12.522 12"
                  />
                </div>
              </div>
            </div>
            <div className="mb-3">
              <textarea
                onChange={(e) => setReviewCommand(e.target.value.trimStart())}
                value={reviewCommand}
                className={
                  reviewCommandError !== ""
                    ? "text-black-300 h-148 placeholder: focus:border-red border-red w-full rounded-md border px-4 py-3.5 text-sm font-normal text-gray-500"
                    : "text-black-300 h-148 placeholder: border-gray-25 w-full rounded-md border px-4 py-3.5 text-sm font-normal text-gray-500 focus:ring-0"
                }
                placeholder={t("myprofile.MentDetails")}
              ></textarea>
            </div>
            <div className="mb-5">
              <p className="text-bold-black text-sm font-medium">
                {t("productcontent.AddRtg")}
                <Link
                  prefetch={false}
                  href="/terms-conditions"
                  className="text-link ml-1 text-sm font-medium"
                >
                  {t("signin.TandC")}
                </Link>
              </p>
            </div>
            <div className="flex flex-wrap items-center gap-2">
              <input
                type="file"
                id="file"
                onChange={(e) => handleReviewMedia(e)}
                className="hidden"
              />
              <label
                htmlFor="file"
                className="flex h-20 w-20 min-w-20 cursor-pointer items-center justify-center rounded bg-[#F9F9FC]"
              >
                <img src="/img/img-upload-review.svg" alt="img-upload-review" />
              </label>

              {handleImageVideo()}

              {/* Resize Modal */}
              <div
                className={`${
                  reszize
                    ? "bg-light-modal z-150 fixed left-0 top-0 flex h-full w-full items-center justify-center lg:items-start"
                    : "hidden"
                }`}
              >
                <div className="h-full w-full bg-white pb-4">
                  <div className="m-4 flex items-center">
                    <div className="flex w-full items-center justify-center space-x-3 px-6">
                      <h2 className="flex space-x-1 text-xl font-bold text-black">
                        {selectedImageIndex}
                        <span className="mx-1 text-black">
                          {t("productlist.of")}
                        </span>
                        {newArray && newArray?.length}
                      </h2>
                    </div>
                    <button
                      className="flex h-8 w-8 items-center justify-center rounded-full bg-white hover:bg-[#e8e8e8] hover:shadow-lg"
                      onClick={() => {
                        (setResize(false), setMedia({}));
                      }}
                    >
                      {" "}
                      <img className="w-4" src="/img/close.svg" alt="close" />
                    </button>
                  </div>
                  <div className="h-calc flex space-x-6 px-4 rtl:space-x-[unset]">
                    <div className="relative aspect-square h-full w-full min-w-full flex-[13] rounded-2xl bg-gray-100 md:min-w-[75vw] rtl:ml-6">
                      {media?.fileType == "image" && (
                        <img
                          src={
                            imageUrl +
                            "?path=" +
                            media?.filePath +
                            "&name=" +
                            media?.fileName +
                            "&width=600&height=500"
                          }
                          alt={media?.fileName}
                          onError={({ currentTarget }) => {
                            currentTarget.onerror = null;
                            currentTarget.src = "/img/no-image.png";
                            currentTarget.alt = "no-image";
                          }}
                          className="m-auto h-full w-full rounded-2xl object-contain"
                        />
                      )}
                      {String(media)?.startsWith("data:image/") && (
                        <img
                          src={media}
                          alt="image"
                          onError={({ currentTarget }) => {
                            currentTarget.onerror = null;
                            currentTarget.src = "/img/no-image.png";
                            currentTarget.alt = "no-image";
                          }}
                          className="m-auto h-full w-full rounded-2xl object-contain"
                        />
                      )}
                      {media?.fileType == "video" && (
                        <video
                          src={`${process.env.NEXT_PUBLIC_SERVER_URL}media/video-preview-s3?name=${media?.fileName}&path=${media?.filePath}`}
                          className="m-auto h-full w-full object-contain"
                          autoPlay
                          controls
                          controlsList="nodownload"
                        />
                      )}
                      {String(media)?.startsWith("data:video/") && (
                        <video
                          src={media}
                          className="m-auto h-full w-full object-contain"
                          autoPlay
                          controls
                          controlsList="nodownload"
                        />
                      )}
                      <button
                        onClick={(e) => handlePreviousImage()}
                        className="absolute left-2 top-[45%] flex h-12 w-12 items-center justify-center rounded-full bg-white shadow-lg hover:bg-gray-100"
                      >
                        <img
                          src="/img/leftgray- arrow.svg"
                          alt="leftgray-arrow"
                        />
                      </button>
                      <button
                        onClick={(e) => handleNextImage()}
                        className="absolute right-2 top-[45%] flex h-12 w-12 items-center justify-center rounded-full bg-white shadow-lg hover:bg-gray-100"
                      >
                        <img
                          src="/img/rightgray-arrow.svg"
                          alt="rightgray-arrow"
                        />
                      </button>
                    </div>

                    <div className="three-grid lg:h-50vh scroll-hide hidden h-auto flex-[3] gap-3 overflow-y-auto md:grid">
                      {newArray &&
                        newArray?.map(
                          (imagelist, index) =>
                            imagelist?.fileType == "image" && (
                              <a
                                key={`image${index}`}
                                onClick={(e) =>
                                  handleImageChange(imagelist, index)
                                }
                                className={`aspect-square max-h-[236px] w-full cursor-pointer rounded-2xl border-black bg-gray-100 hover:border-2 hover:p-0.5 ${
                                  media?.fileName == imagelist?.fileName &&
                                  "border-2 p-0.5"
                                } `}
                              >
                                <img
                                  src={
                                    imageUrl +
                                    "?path=" +
                                    imagelist?.filePath +
                                    "&name=" +
                                    imagelist?.fileName +
                                    "&width=278&height=236"
                                  }
                                  alt={imagelist?.fileName}
                                  className="m-auto h-full w-full rounded-2xl object-contain"
                                  onError={({ currentTarget }) => {
                                    currentTarget.onerror = null;
                                    currentTarget.src = "/img/no-image.png";
                                    currentTarget.alt = "no-image";
                                  }}
                                />
                              </a>
                            ),
                        )}

                      {newArray &&
                        newArray?.map(
                          (imagelist, index) =>
                            String(imagelist)?.startsWith("data:image/") && (
                              <a
                                key={`image${index}`}
                                onClick={(e) =>
                                  handleImageChange(imagelist, index)
                                }
                                className={`aspect-square max-h-[236px] w-full cursor-pointer rounded-2xl border-black bg-gray-100 hover:border-2 hover:p-0.5 ${
                                  media == imagelist && "border-2 p-0.5"
                                } `}
                              >
                                <img
                                  src={imagelist}
                                  alt="image"
                                  className="m-auto h-full w-full rounded-2xl object-contain"
                                  onError={({ currentTarget }) => {
                                    currentTarget.onerror = null;
                                    currentTarget.src = "/img/no-image.png";
                                    currentTarget.alt = "no-image";
                                  }}
                                />
                              </a>
                            ),
                        )}

                      {newArray &&
                        newArray?.map(
                          (imagelist, index) =>
                            imagelist?.fileType == "video" && (
                              <a
                                key={`video${index}`}
                                onClick={(e) =>
                                  handleImageChange(imagelist, index)
                                }
                                className={`aspect-square max-h-[236px] w-full cursor-pointer rounded-2xl border-black bg-gray-100 hover:border-2 hover:p-0.5 ${
                                  media?.fileName == imagelist?.fileName &&
                                  "border-2 p-0.5"
                                }`}
                              >
                                <video
                                  src={`${process.env.NEXT_PUBLIC_SERVER_URL}media/video-preview-s3?name=${imagelist?.fileName}&path=${imagelist?.filePath}`}
                                  className="m-auto h-full w-full object-contain"
                                ></video>
                              </a>
                            ),
                        )}

                      {newArray &&
                        newArray?.map(
                          (imagelist, index) =>
                            String(imagelist)?.startsWith("data:video/") && (
                              <a
                                key={`video${index}`}
                                onClick={(e) =>
                                  handleImageChange(imagelist, index)
                                }
                                className={`aspect-square max-h-[236px] w-full cursor-pointer rounded-2xl border-black bg-gray-100 hover:border-2 hover:p-0.5 ${
                                  media == imagelist && "border-2 p-0.5"
                                }`}
                              >
                                <video
                                  src={imagelist}
                                  className="m-auto h-full w-full object-contain"
                                ></video>
                              </a>
                            ),
                        )}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          {/* <!-- Modal footer --> */}
          <div className="bg-gray-slate-100 flex items-center justify-end p-4 dark:border-gray-600 md:px-8 md:py-4">
            {AddLoader == true ? (
              <button className="h-[49px] rounded bg-black px-8 text-sm font-semibold uppercase text-white hover:bg-zinc-800">
                <svg
                  aria-hidden="true"
                  role="status"
                  className="me-3 inline h-5 w-24 animate-spin text-white"
                  viewBox="0 0 100 101"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M100 50.5908C100 78.2051 77.6142 100.591 50 100.591C22.3858 100.591 0 78.2051 0 50.5908C0 22.9766 22.3858 0.59082 50 0.59082C77.6142 0.59082 100 22.9766 100 50.5908ZM9.08144 50.5908C9.08144 73.1895 27.4013 91.5094 50 91.5094C72.5987 91.5094 90.9186 73.1895 90.9186 50.5908C90.9186 27.9921 72.5987 9.67226 50 9.67226C27.4013 9.67226 9.08144 27.9921 9.08144 50.5908Z"
                    fill="#E5E7EB"
                  />
                  <path
                    d="M93.9676 39.0409C96.393 38.4038 97.8624 35.9116 97.0079 33.5539C95.2932 28.8227 92.871 24.3692 89.8167 20.348C85.8452 15.1192 80.8826 10.7238 75.2124 7.41289C69.5422 4.10194 63.2754 1.94025 56.7698 1.05124C51.7666 0.367541 46.6976 0.446843 41.7345 1.27873C39.2613 1.69328 37.813 4.19778 38.4501 6.62326C39.0873 9.04874 41.5694 10.4717 44.0505 10.1071C47.8511 9.54855 51.7191 9.52689 55.5402 10.0491C60.8642 10.7766 65.9928 12.5457 70.6331 15.2552C75.2735 17.9648 79.3347 21.5619 82.5849 25.841C84.9175 28.9121 86.7997 32.2913 88.1811 35.8758C89.083 38.2158 91.5421 39.6781 93.9676 39.0409Z"
                    fill="currentColor"
                  />
                </svg>
              </button>
            ) : (
              <button
                onClick={() => handleReviewClick(setAddLoader)}
                className="h-[49px] rounded bg-black px-8 text-sm font-semibold uppercase text-white hover:bg-zinc-800"
              >
                {t("productcontent.SubRev")}
              </button>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}
