import axiosInstance from "@/api/interceptor";

export async function GoogleMapApi(
  setCitypin,
  setCheck,
  statevalue,
  cityval,
  address,
  postcode,
  token,
  countryId
) {
  const data = JSON.stringify({
    address1: address,
    address2: "",
    addressType: 2,
    city: cityval,
    postcode: postcode,
    state: statevalue,
    addressId: 2,
    countryId: countryId,
    company: "",
  });

  const result = await axiosInstance.post("customer-address/live", data);
  if (result && result.data && result.data.status == 1) {
    if (token !== null) {
      GoogleMapApiget(setCitypin, setCheck);
    } else {
      guestGoogleMapApiget(setCitypin, setCheck);
    }
  }
}

export async function GoogleMapApiget(setCitypin, setCheck) {
  const result = await axiosInstance.get("CustomerAddress/get-address-list");
  if (result && result.data && result.data.data) {
    setCitypin(result.data.data.liveAddress);
    setCheck(true);
  }
}

export async function guestGoogleMapApiget(setCitypin, setCheck) {
  const result = await axiosInstance.get("customer-address/live");
  if (result && result.data && result.data.data) {
    setCitypin(result.data.data);
    setCheck(true);
  }
}

export async function CountryIdgetApi(country, setCounrtyId) {
  const result = await axiosInstance.get(
    `list/country-list?countryName=${country}`
  );
  if (result && result.data && result.data.data) {
    setCounrtyId(result.data.data.countryId);
  }
}

