export async function GoogleMapKey(setKey, setKeyval) {
  const result = await axiosInstance.get("list/gmap-key?pluginName=gmap");
  if (result && result.data && result.data.data) {
    setKey(result.data.data.clientId);
    setKeyval(true);
  }
}
