"use client";

// React and Next
import { useEffect, useState } from "react";
import Link from "next/link";

// API
import { RelatedBlogsApi, blogsdetails } from "./blogs";

// URL
import { imageUrl } from "@/utilities/url";

// 3rd Party
import moment from "moment";

// Components
import Contactus from "@/Components/Contactus";
import { useTranslation } from "react-i18next";

function Blogsdetails({ token, params }) {
  const [blogdetails, setBlogdetails] = useState();
  const [relatedBlogs, setRelatedBlogs] = useState([]);
  const [loader, setLoader] = useState(false);
  const { t } = useTranslation();

  //for blogdetails api call
  useEffect(() => {
    handleblogdetails();
    handleRelatedBlog();
  }, []);

  //blogsdetails api call functionality
  const handleblogdetails = async () => {
    const getblogdetails = await blogsdetails(params);
    if (getblogdetails?.status == 1) {
      setBlogdetails(getblogdetails?.data);
      setLoader(true);
    }
  };

  //relatedBlogs api call functionality
  const handleRelatedBlog = async () => {
    const relatedBlog = await RelatedBlogsApi(params);
    if (relatedBlog?.status == 1) {
      setRelatedBlogs(relatedBlog?.data);
    }
  };

  return (
    <>
      <div className="px-4 pt-10 m-auto lg:px-32 max-w-screen-2xl">
        {loader == false && (
          <div
            role="status"
            className="space-y-8 md:flex md:items-center rtl:space-x-reverse md:space-x-8 md:space-y-0 animate-pulse"
          >
            <div className="w-full">
              <div className="bg-gray-200 dark:bg-gray-700 mb-4 rounded-full w-48 h-2.5"></div>
              <div className="bg-gray-200 dark:bg-gray-700 mb-2.5 rounded-full max-w-[480px] h-2"></div>
              <div className="bg-gray-200 dark:bg-gray-700 mb-2.5 rounded-full h-2"></div>
              <div className="bg-gray-200 dark:bg-gray-700 mb-2.5 rounded-full h-2"></div>
              <div className="bg-gray-200 dark:bg-gray-700 mb-2.5 rounded-full h-2"></div>
              <div className="bg-gray-200 dark:bg-gray-700 mb-2.5 rounded-full h-2"></div>
              <div className="bg-gray-200 dark:bg-gray-700 mb-2.5 rounded-full h-2"></div>
              <div className="bg-gray-200 dark:bg-gray-700 mb-2.5 rounded-full h-2"></div>
              <div className="bg-gray-200 dark:bg-gray-700 mb-2.5 rounded-full h-2"></div>
              <div className="bg-gray-200 dark:bg-gray-700 mb-2.5 rounded-full h-2"></div>
              <div className="flex items-center mt-4">
                <svg
                  className="w-10 h-10 text-gray-200 me-3 dark:text-gray-700"
                  aria-hidden="true"
                  xmlns="http://www.w3.org/2000/svg"
                  fill="currentColor"
                  viewBox="0 0 20 20"
                >
                  <path d="M10 0a10 10 0 1 0 10 10A10.011 10.011 0 0 0 10 0Zm0 5a3 3 0 1 1 0 6 3 3 0 0 1 0-6Zm0 13a8.949 8.949 0 0 1-4.951-1.488A3.987 3.987 0 0 1 9 13h2a3.987 3.987 0 0 1 3.951 3.512A8.949 8.949 0 0 1 10 18Z" />
                </svg>
                <div>
                  <div className="bg-gray-200 dark:bg-gray-700 mb-2 rounded-full w-48 h-2.5"></div>
                </div>
              </div>

              <div
                role="status"
                className="flex items-center justify-center mb-10 bg-gray-300 rounded-lg dark:bg-gray-700 animate-pulse blogdetails"
              >
                <svg
                  className="w-10 h-10 text-gray-200 dark:text-gray-600"
                  aria-hidden="true"
                  xmlns="http://www.w3.org/2000/svg"
                  fill="currentColor"
                  viewBox="0 0 16 20"
                >
                  <path d="M5 5V.13a2.96 2.96 0 0 0-1.293.749L.879 3.707A2.98 2.98 0 0 0 .13 5H5Z" />
                  <path d="M14.066 0H7v5a2 2 0 0 1-2 2H0v11a1.97 1.97 0 0 0 1.934 2h12.132A1.97 1.97 0 0 0 16 18V2a1.97 1.97 0 0 0-1.934-2ZM9 13a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2v-2a2 2 0 0 1 2-2h2a2 2 0 0 1 2 2v2Zm4 .382a1 1 0 0 1-1.447.894L10 13v-2l1.553-1.276a1 1 0 0 1 1.447.894v2.764Z" />
                </svg>
                <span className="sr-only">{t("productcontent.loading")}</span>
              </div>
            </div>

            <span className="sr-only">{t("productcontent.loading")}</span>
          </div>
        )}
        {loader == true && (
          <div>
            <div className="flex items-center">
              <Link
                prefetch={false}
                href="/blog"
                className="mb-4 text-base font-normal text-black-300"
              >
                 {t("myprofile.AllBlogs")}
              </Link>
            </div>

            <h3 className="mb-4 text-4xl font-black text-black-300">
              {blogdetails?.[0]?.blogTranslation?.title
                ? blogdetails?.[0]?.blogTranslation?.title
                : blogdetails?.[0]?.title}
            </h3>

            <div className="flex flex-col mb-6 space-y-0 sm:flex-row sm:items-center sm:space-x-2 sm:space-y-0">
              <div className="flex items-center space-x-2.5 rtl:space-x-[unset]">
                <div className="flex items-center space-x-4 rtl:space-x-[unset] rtl:ml-2.5">
                  <img
                    src={
                      imageUrl +
                      "?path=" +
                      blogdetails?.[0]?.createdByImagePath +
                      "&name=" +
                      blogdetails?.[0]?.createdByImage +
                      "&width=120&height=120"
                    }
                    alt={blogdetails?.[0]?.title}
                    className="w-12 h-12 rounded-full rtl:ml-4"
                    onError={({ currentTarget }) => {
                      currentTarget.onerror = null;
                      currentTarget.src = "/img/user1.jpg";
                      currentTarget.alt = "user";
                    }}
                  />
                  <p className="text-base font-normal text-black-300">
                    {t("myprofile.Postby")}
                  </p>
                </div>
                <a
                  role="button"
                  className="text-base font-normal underline text-link"
                >
                  {blogdetails?.[0]?.createdByName}
                </a>
              </div>
              <span className="hidden text-base italic font-normal sm:block text-black-300">
                |
              </span>
              <p className="text-base italic font-normal text-black-300">
                {moment(blogdetails?.[0]?.createdDate).format("MMMM DD, YYYY")}
              </p>
            </div>
            <div className="mb-10">
              <img
                src={`${imageUrl}?path=${blogdetails?.[0]?.imagePath}&name=${blogdetails?.[0]?.image}&width=1400&height=787`}
                alt={blogdetails?.[0]?.title}
                className="w-full h-auto object-contain rounded-lg"
                loading="lazy"
              />
              {/* Backup Style */}
              {/* className="text-xl font-normal text-black-300 list-default bullet margin" */}
              <div
                className="text-black-300 list-default bullet margin editor-style"
                dangerouslySetInnerHTML={{
                  __html: blogdetails?.[0]?.blogTranslation?.description
                    ? blogdetails?.[0]?.blogTranslation?.description
                    : blogdetails?.[0]?.description
                        ?.replaceAll("&amp;", "&")
                        .replaceAll("&lt;", "<")
                        .replaceAll("&gt;", ">")
                        .replaceAll("&quot;", '"')
                        .replaceAll("&#39;", "'")
                        .replaceAll("&sbquo;", "")
                        .replaceAll("&#61;", "=")
                        .replaceAll("&#45;", "-")
                        .replaceAll("&hellip;", "")
                        .replaceAll("&commat;", "@")
                        .replaceAll("&copy;", "")
                        .replaceAll("&#35;", "#")
                        .replaceAll("&ldquo;", "")
                        .replaceAll("&rsquo;", "")
                        .replaceAll("&lsquo;", "")
                        .replaceAll("&trade;", "")
                        .replaceAll("&reg;", "")
                        .replaceAll("&ndash;", "")
                        .replaceAll("&eacute;", "")
                        .replaceAll("&euro;", "")
                        .replaceAll("&pound;", "")
                        .replace(
                          "&lt;/p&gt;&amp;&#35;10;&amp;&#35;10;&lt;p&gt;",
                          ""
                        ),
                }}
              />
            </div>

            <p
              className={`mb-2 font-black text-black-300 text-2xl line-clamp-3 ${
                relatedBlogs?.length == 0 ? "hidden" : ""
              }`}
            >
              {t("homepage.relatedBlogs")}{" "}
            </p>
            <div className="grid grid-cols-1 gap-6 mt-12 gap-y-12 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4">
              {relatedBlogs?.map((data, index) => (
                <Link
                  prefetch={false}
                  key={index}
                  href={`/blog/${data?.relatedBlog?.blogSlug}`}
                  className="flex flex-col space-y-3"
                >
                  <div>
                    <img
                      src={
                        imageUrl +
                        "?path=" +
                        data?.relatedBlog?.imagePath +
                        "&name=" +
                        data?.relatedBlog?.image +
                        "&width=400&height=200"
                      }
                      alt={data?.relatedBlog?.title}
                      className="rounded-lg imgas2"
                    />
                  </div>
                  <div>
                    <div className="flex items-center space-x-1.5 rtl:space-x-[unset] mb-2">
                      <img
                        src="/img/right-arrow.svg"
                        alt="right-arrow"
                        className="rtl:ml-1.5"
                      />
                      <p className="text-xs font-medium text-black-300">
                        {data?.categoryName}
                      </p>
                    </div>
                    <span
                      role="button"
                      className="mb-2 text-2xl font-black cursor-pointer text-black-300 line-clamp-3 hei-96"
                    >
                      {data?.relatedBlog?.relatedBlogTranslation?.title
                        ? data?.relatedBlog?.relatedBlogTranslation?.title
                        : data?.relatedBlog?.title}
                    </span>
                    <div className="flex space-x-2.5 rtl:space-x-[unset]">
                      <p className="flex space-x-0.5 rtl:space-x-[unset] rtl:ml-2.5 font-medium text-black-300 text-xs">
                        <span className="rtl:ml-0.5">{t("myprofile.by")}</span>
                        <span
                          role="button"
                          className="text-xs font-medium underline cursor-pointer text-black-300 hover:text-blue-500 hover:no-underline"
                        >
                          {data?.createdByName}
                        </span>
                      </p>
                      <span className="text-xs italic font-normal text-black-300">
                        {moment(data?.createdDate).format("MMMM DD, YYYY")}
                      </span>
                    </div>
                  </div>
                </Link>
              ))}
            </div>
          </div>
        )}
      </div>

      <Contactus token={token} />
    </>
  );
}

export default Blogsdetails;
