"use client";
//React and Next 
import { useEffect, useState } from "react";
//Redux
import { useSelector } from "react-redux";
//Components
import QuotationId from "./QuotationId";
import QuotationCard from "./QuotationCard";

export default function QuotationCardList() {
  const [priceShow, setPriceShow] = useState(false);
  const [price, setPrice] = useState("");
  const quotationdetailList = useSelector(
    (state) => state.userReducer.quotationdetail
  );

  useEffect(() => {
    if (quotationdetailList?.quotationStatusId == 1) {
      setPriceShow(true);
    } else {
      setPriceShow(false);
    }
    let priceval = 0;
    quotationdetailList?.quotationProduct?.map((data) => {
      priceval =
        priceval +
        (Number(data?.bestQuotedPrice)
          ? Number(data?.bestQuotedPrice) * data?.quantity
          : data?.productPrice * data?.quantity);
    });
    setPrice(priceval);
  }, [quotationdetailList]);

  return (
    <div className="w-full px-0 py-6 sm:px-8">
      <QuotationId
        quotationdetailList={quotationdetailList}
        priceShow={priceShow}
        price={price}
      />
      <QuotationCard quotationdetailList={quotationdetailList} />
    </div>
  );
}
