"use client";
//React and Next
import Link from "next/link";
import { useEffect, useState } from "react";
//Redux
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { quotelist } from "@/redux/slices/userSlice";
//Utils
import { imageUrl } from "@/utilities/url";
import { PriceCalculate } from "@/utilities/PriceCalculate";
//Components
import CustomCurrencyFormatter from "@/Components/CustomCurrencyFormatter";
import { useTranslation } from "react-i18next";

export default function QuotationProductList({
  setCommet,
  comment,
  handleDecrement,
  handleIecrement,
  quantity,
  changevalue,
  setTrigger,
  trigger,
  productList,
}) {
  const [quotelists, setQuotelists] = useState([]);
  const qoutedetails = useSelector((state) => state.userReducer.quote);
  const dispatch = useDispatch();
  const { t } = useTranslation();

  useEffect(() => {
    setQuotelists(JSON.parse(JSON.stringify(qoutedetails)));
  }, [qoutedetails, changevalue]);

  const handleremove = (id) => {
    const qoutedetailsgg = qoutedetails?.filter(function (item) {
      return item?.skuId !== id;
    });
    dispatch(quotelist(qoutedetailsgg));
    setTrigger(trigger + 1);
  };

  return productList?.length >= 1 ? (
    <div className="w-full h-auto rtl:ml-4 md:w-9/12">
      <div className="bg-white rounded">
        {productList?.map((data, index) => (
          <div key={index} className="flex">
            <div className="grid w-3/4 grid-cols-1 gap-4 md:gap-0 sm:grid-cols-2">
              <div>
                {index == 0 && (
                  <div className="px-4 py-3 border-b border-lightgrey-100">
                    <h3 className="text-xs font-normal text-gray-900 text-start">
                      {" "}
                      {productList.length}{" "}
                      {productList.length > 1
                        ? `${t("myprofile.Cart")}`
                        : `${t("myprofile.CartOne")}`}
                    </h3>
                  </div>
                )}
                <div className="flex sm:flex-row flex-col items-start sm:rtl:space-x-[unset] sm:space-x-4 space-y-4 sm:space-y-0 p-4">
                  <img
                    src={
                      imageUrl +
                      "?path=" +
                      data?.containerName +
                      "&name=" +
                      data?.image +
                      "&width=182&height=182"
                    }
                    alt={data?.name}
                    width={96}
                    height={96}
                    className="w-24 h-24 sm:rtl:ml-4"
                    onError={({ currentTarget }) => {
                      currentTarget.onerror = null;
                      currentTarget.src = "/img/no-image.png";
                      currentTarget.alt = "no-image";
                    }}
                  />
                  <div>
                    <Link
                      prefetch={false}
                      href={`/products/${data?.productSlug}`}
                      className="mb-2.5 font-normal text-black-300 text-base line-clamp-1"
                    >
                      {data?.productNameTrans
                        ? data?.productNameTrans
                        : data?.name}
                    </Link>
                    <p className="text-sm font-bold text-black-300">
                      <CustomCurrencyFormatter
                        price={PriceCalculate(
                          data?.price,
                          data?.pricerefer,
                          data?.flag,
                          data?.taxType,
                          data?.taxValue
                        )}
                      />
                    </p>
                  </div>
                </div>
              </div>
              <div>
                {index == 0 && (
                  <div className="px-4 py-3 border-0 sm:border-b border-lightgrey-100">
                    <h3 className="text-xs text-gray-900font-normal text-start">
                      {t("myprofile.Qty")}
                    </h3>
                  </div>
                )}
                <div className="flex items-center space-x-1.5 p-4">
                  <button
                    disabled={data?.quantity == 1}
                    onClick={(e) => handleDecrement(index, data)}
                    className={`bg-boldblack w-8 h-8 flex rtl:ml-1.5 justify-center items-center tooltip rounded-md ${
                      quantity[index] == 1 && "opacity-15"
                    }`}
                  >
                    <img src="/img/minus.svg" alt="minus" />
                    <span className="tooltip-text">
                      {t("tooltip.decrease")}
                    </span>
                  </button>
                  <input
                    type="number"
                    onWheel={(e) => e.target.blur()}
                    value={data?.quantity}
                    readOnly
                    className="w-16 h-8 p-3 text-center border rounded-md border-slate-900 focus:border-black focus:outline-0 focus:ring-0 text-black-200"
                  ></input>
                  <button
                    onClick={(e) => handleIecrement(index, data)}
                    className="flex items-center justify-center w-8 h-8 rounded-md bg-boldblack tooltip"
                  >
                    <img src="/img/plus.svg" alt="plus" />
                    <span className="tooltip-text">
                      {t("tooltip.increase")}
                    </span>
                  </button>
                </div>
              </div>
            </div>
            <div className="w-[37%] sm:w-3/12">
              {index == 0 && (
                <div className="px-4 py-3 border-b border-lightgrey-100">
                  <h3 className="text-xs text-gray-900font-normal text-start">
                    {t("myprofile.Price")}
                  </h3>
                </div>
              )}
              <div className="flex flex-col p-4 space-y-12">
                <div>
                  <h4 className="text-lg font-bold text-black-300">
                    <CustomCurrencyFormatter
                      price={
                        PriceCalculate(
                          data?.price,
                          data?.pricerefer,
                          data?.flag,
                          data?.taxType,
                          data?.taxValue
                        ) * data?.quantity
                      }
                    />
                  </h4>
                </div>
                <button className="flex items-center space-x-1 group">
                  <img
                    src="/img/delete.svg"
                    alt="delete"
                    className="group-hover:hidden rtl:ml-1"
                  />
                  <img
                    src="/img/delete-active.svg"
                    alt="delete-active"
                    className="hidden group-hover:block rtl:ml-1"
                  />
                  <p
                    className="text-xs font-normal leading-3 text-black-300 group-hover:text-red-600"
                    onClick={() => handleremove(data?.skuId)}
                  >
                    {t("myprofile.Remove")}
                  </p>
                </button>
              </div>
            </div>
          </div>
        ))}
        <div className="px-4 py-6 border-t border-lightgrey-100">
          <div className="relative w-full" data-twe-input-wrapper-init>
            <textarea
              type="text"
              value={comment}
              maxLength={255}
              onChange={(e) => setCommet(e.target.value.trimStart())}
              className={
                comment == ""
                  ? "peer block h-36 w-full rounded text-black-200 border border-grey-400 bg-transparent px-3 py-[0.32rem] leading-[1.6] outline-none transition-all duration-200 ease-linear focus:placeholder:opacity-100 peer-focus:text-primary data-[twe-input-state-active]:placeholder:opacity-100 motion-reduce:transition-none dark:text-white dark:placeholder:text-grey-400 dark:autofill:shadow-autofill dark:peer-focus:text-primary [&:not([data-twe-input-placeholder-active])]:placeholder:opacity-0 focus:border-grey-400 focus:shadow-none focus:ring-transparent focus:border-grey-400"
                  : `peer block h-36 w-full rounded text-black-200 border border-grey-400 bg-transparent px-3 py-[0.32rem] leading-[1.6] outline-none transition-all duration-200 ease-linear focus:placeholder:opacity-100 peer-focus:text-primary data-[twe-input-state-active]:placeholder:opacity-100 motion-reduce:transition-none dark:text-white dark:placeholder:text-grey-400 dark:autofill:shadow-autofill dark:peer-focus:text-primary [&:not([data-twe-input-placeholder-active])]:placeholder:opacity-0 focus:border-grey-400 focus:shadow-none focus:ring-transparent`
              }
              id="exampleFormControlInput1"
              placeholder={t("signin.Examplelabel")}
            />
            <label
              htmlFor="exampleFormControlInput1"
              className={
                comment == ""
                  ? "pointer-events-none absolute left-3 top-2  mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[1.01rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.8rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-white"
                  : `translate-y-[-0.8rem] scale-[0.8] pointer-events-none absolute left-3 top-1 mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[0.8rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.9rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-white text-red-valid`
              }
            >
              {t("myprofile.MentDetails")}
            </label>
          </div>
        </div>
      </div>
    </div>
  ) : (
    <div className="mx-auto mt-32 min-h-[40vh]">
      <div className="flex flex-col items-center justify-center gap-8 p-4 text-center sm:flex-row">
        <img src="/img/no-data.svg" alt="no-data" />
        <div>
          <h5 className="mb-1 text-xl font-black text-black-300">
            {t("myprofile.NoQuote")}
          </h5>
        </div>
      </div>
    </div>
  );
}
