"use client";

// React & Next Imports
import { useRouter } from "nextjs-toploader/app";

//3rd Party
import { useGoogleLogin } from "@react-oauth/google";
import { glogin } from "./GoogleLoginApi";
import axios from "axios";

// Component
import { useTranslation } from "react-i18next";

export default function GoogleLogin() {
  const { t } = useTranslation();
  const router = useRouter();

  const handleRoute = () => {
    router.push("/");
  };
  const gmaillogin = useGoogleLogin({
    onSuccess: async (coderes) => {
      const userInfo = await axios
        .get("https://www.googleapis.com/oauth2/v3/userinfo", {
          headers: { Authorization: `Bearer ${coderes.access_token}` },
        })
        .then((res) => res.data);
      if (userInfo) {
        glogin(JSON.stringify(userInfo), handleRoute);
      }
    },
  });
  return (
    <div className="flex items-center justify-center mt-8">
      <button
        className="flex space-x-4 rtl:space-x-[unset] bg-transparent text-black-300 text-sm text-center"
        onClick={() => gmaillogin()}
      >
        <img
          src="/img/google-sign.svg"
          alt="google-sign"
          className="rtl:ml-4"
        />
        <p>{t("signin.gSignIn")}</p>
      </button>
    </div>
  );
}
