"use client";
//React and Next
import { useCallback, useEffect, useState } from "react";
import Link from "next/link";
import { useRouter } from "nextjs-toploader/app";
//3rd Party
import moment from "moment";
//API
import { Get_Support_List_Api, Get_Support_List_CountApi } from "./SupportApi";
//Component
import Sidebar from "./SideBar";
import { Support_Pagination } from "./Support_Pagination";
import Breadcrumb from "@/Components/Breadcrumbs";
import { Support_ListPage_Skeleton } from "./Skeleton/Support_ListPage_Skeleton";
import Contactus from "@/Components/Contactus";
import { useTranslation } from "react-i18next";

export default function SupportList({ token }) {
  const [Support_List_Api_Result, setSupport_List_Api_Result] = useState([]);
  const [currentPage, setCurrentPage] = useState(0);
  const limit = 5;
  const [offset, setoffset] = useState(0);
  const [tot_Count, settot_Count] = useState("");
  const [loader, setLoader] = useState(false);
  const [keyword, setkeyword] = useState("");
  const { t } = useTranslation();
  const router = useRouter();

  const SupportList_Api_Functions = useCallback(() => {
    setLoader(true);
    Get_Support_List_Api(
      setSupport_List_Api_Result,
      setLoader,
      offset,
      limit,
      keyword
    );
    Get_Support_List_CountApi(settot_Count, keyword);
  }, [currentPage, limit, offset, keyword]);

  useEffect(() => {
    SupportList_Api_Functions();
  }, [SupportList_Api_Functions]); // Runs only when dependencies change

  const handlechange_Pagination = (event) => {
    setCurrentPage(event.selected);
    setoffset(event.selected * limit);
  };

  const handleclick_CreateTicket = (e) => {
    router.push(`/account-settings/support/support-create`);
  };
  const handlechange_keyword = (e) => {
    const value = e.target.value;

    // Only update if the first character is not a space
    if (value.length === 0 || value[0] !== " ") {
      setkeyword(value);
    }
  };

  const breadCrumb = [
    {
      text: t("Breadcrumb.home"),
      url: "/",
    },
    {
      text: t("Breadcrumb.accountSettings"),
      url: "/account-settings",
    },
    {
      text: t("myprofile.Support"),
      url: "javascript:void(0)",
    },
  ];
  return (
    <>
      <div className="mx-auto px-4 py-2.5 max-w-screen-2xl">
        <Breadcrumb breadCrumb={breadCrumb} />

        <main className="flex rtl:space-x-[unset] md:space-x-8 mx-auto mt-4 mb-7">
          <div className="hidden lg:block">
            <Sidebar />
          </div>

          <div className="w-full p-0 md:p-0 md:pe-8">
            <div className="flex flex-wrap justify-between items-center gap-4 mb-[23px]">
              <h3 className="font-medium text-[#333333] text-lg">
                {t("support.SupportTicket")}
              </h3>
              <div className="flex items-center space-x-[10px] rtl:space-x-[unset]">
                <div className="relative rtl:ml-4">
                  <input
                    type="text"
                    placeholder={t("support.Search")}
                    className="pr-10 border border-grey-400 rounded-md focus-visible:outline-0 w-full sm:min-w-[280px] h-10 text-[#636363] text-[13px] text-normal"
                    value={keyword}
                    onChange={(e) => handlechange_keyword(e)}
                  />
                  <button
                    className="absolute top-0 right-0 flex items-center justify-center h-10 px-3 btn"
                    disabled
                  >
                    <img src="/img/search-input.svg" alt="search" />
                  </button>
                </div>
                <button
                  className="flex justify-center items-center bg-[#191919] px-[20px] border border-black rounded-lg h-10 font-medium text-white text-sm whitespace-nowrap btn"
                  onClick={(e) => handleclick_CreateTicket(e)}
                >
                  {t("support.NewTicket")}
                </button>
              </div>
            </div>
            <div className="bg-[#F5F6F8] p-[20px_25px] rounded-[8px]">
              <p className="font-normal text-[#191919] text-sm leading-[17px]">
                {t("accountsettings.SupportContent")}
              </p>
            </div>

            <div className="mt-[13px] border border-[#F0F0F0] border-solid rounded w-full overflow-auto">
              <table className="w-full min-w-[735px]">
                <thead className="sticky top-0">
                  <tr>
                    <th className="bg-[#EEEEEE] p-[17px_25px] border-none font-semibold text-[#5A6270] text-sm text-start leading-4">
                      {t("support.Subject")}
                    </th>
                    <th className="bg-[#EEEEEE] p-[17px_25px] border-none font-semibold text-[#5A6270] text-sm text-start leading-4">
                      {t("support.Category")}
                    </th>
                    <th className="bg-[#EEEEEE] p-[17px_25px] border-none font-semibold text-[#5A6270] text-sm text-start leading-4">
                      {t("support.SubCategory")}
                    </th>
                    <th className="bg-[#EEEEEE] p-[17px_25px] border-none font-semibold text-[#5A6270] text-sm text-start leading-4">
                      {t("support.Created")}
                    </th>
                    <th className="bg-[#EEEEEE] p-[17px_25px] border-none font-semibold text-[#5A6270] text-sm text-start leading-4">
                      {t("support.LastAct")}
                    </th>
                    <th className="bg-[#EEEEEE] p-[17px_25px] border-none font-semibold text-[#5A6270] text-sm text-start leading-4">
                      {t("support.Status")}
                    </th>
                  </tr>
                </thead>
                <tbody>
                  {loader ? (
                    <Support_ListPage_Skeleton />
                  ) : Support_List_Api_Result.length === 0 ? (
                    <tr style={{ border: "none" }}>
                      <td
                        colSpan="6"
                        className="text-center"
                        style={{ border: "none" }}
                      >
                        <div className="mx-auto mt-32 min-h-[40vh]">
                          <div className="flex flex-col items-center justify-center gap-8 p-4 text-center sm:flex-row">
                            <img src="/img/no-data.svg" alt="no-data" />
                            <div>
                              <h5 className="mb-1 text-xl font-black text-black-300">
                                {t("myprofile.NoData")}
                              </h5>
                            </div>
                          </div>
                        </div>
                      </td>
                    </tr>
                  ) : (
                    Support_List_Api_Result.map((val, index) => (
                      <tr key={index}>
                        <td className="p-[15px_25px] !border-[#F0F0F0] border-0 border-b-[1px] border-solid text-start">
                          <Link
                            prefetch={false}
                            href={`/account-settings/support/${val.id}`}
                            className="font-semibold text-[#191919] text-sm hover:underline leading-4"
                          >
                            {val.refId + " - " + val.subject}
                          </Link>
                        </td>

                        <td className="p-[15px_25px] !border-[#F0F0F0] border-0 border-b-[1px] border-solid text-start">
                          <h3 className="font-normal text-[#191919] text-sm leading-4">
                            {val.categoryName}
                          </h3>
                        </td>

                        <td className="p-[15px_25px] !border-[#F0F0F0] border-0 border-b-[1px] border-solid text-start">
                          <h3 className="font-normal text-[#191919] text-sm leading-4">
                            {val.subCategoryName}
                          </h3>
                        </td>

                        <td className="p-[15px_25px] !border-[#F0F0F0] border-0 border-b-[1px] border-solid text-start">
                          <p className="font-normal text-[#636363] text-sm leading-4">
                            {[undefined, null, ""].includes(val.createdDate)
                              ? "-"
                              : moment(val?.createdDate)?.format(
                                  "DD MMM YY, hh:mm a"
                                )}
                          </p>
                        </td>

                        <td className="p-[15px_25px] !border-[#F0F0F0] border-0 border-b-[1px] border-solid text-start">
                          <p className="font-normal text-[#636363] text-sm leading-4">
                            {[undefined, null, ""].includes(val.modifiedDate)
                              ? "-"
                              : moment(val?.modifiedDate)?.format(
                                  "DD MMM YY, hh:mm a"
                                )}
                          </p>
                        </td>
                        <td className="p-[15px_25px] !border-[#F0F0F0] border-0 border-b-[1px] border-solid text-start">
                          {val.status == "open" ? (
                            <span className="inline-block bg-[#DFF3F3] p-[3px_5px] rounded font-medium text-[#164766] text-sm leading-4">
                              {t("support.Open")}
                            </span>
                          ) : val.status == "resolved" ? (
                            <span className="inline-block bg-[#DFF3E7] p-[3px_5px] rounded font-medium text-[#166624] text-sm leading-4">
                              {t("support.Resolved")}
                            </span>
                          ) : val.status == "pending" ? (
                            <span className="inline-block bg-[#F3F2DF] p-[3px_5px] rounded font-medium text-[#8C5C0F] text-sm leading-4">
                              {t("support.Pending")}
                            </span>
                          ) : (
                            <span className="inline-block bg-[#DFF3F3] p-[3px_5px] rounded font-medium text-[#164766] text-sm leading-4">
                              {val.status}
                            </span>
                          )}
                        </td>
                      </tr>
                    ))
                  )}
                </tbody>
              </table>
            </div>

            {tot_Count > 5 && (
              <Support_Pagination
                pageCount={Math.ceil(tot_Count / limit) || 1}
                forcePage={currentPage}
                handlechange_Pagination={handlechange_Pagination}
              />
            )}
          </div>
        </main>
      </div>

      <Contactus token={token} />
    </>
  );
}
