"use client";
//React and Next
import Link from "next/link";
import { Fragment, useEffect, useState } from "react";
import { useRouter } from "nextjs-toploader/app";
//3rd Party
import StarRatings from "react-star-ratings";
//API
import {
  GetCompareId,
  getWidgetDetail,
  getWidgetDetailCount,
  productCompareApi,
  widgetDeleteApi,
  widgetPostApi,
} from "./widgetApiClient";
//Redux
import { useDispatch, useSelector } from "react-redux";
import { breadcrumb, subproduct } from "@/redux/slices/userSlice";
//Utils
import { PriceCalculate, PriceTaxCalculate } from "@/utilities/PriceCalculate";
import { imageUrl } from "@/utilities/url";
//Component
import ToastMessage from "@/Components/ToastMessage";
import CustomCurrencyFormatter from "@/Components/CustomCurrencyFormatter";
import { useTranslation } from "react-i18next";

export default function WidgetList({
  token,
  slug,
  addonList
}) {

  let compareProductIds = GetCompareId();
  const router = useRouter();
  const productIds = GetCompareId();
  const dispatch = useDispatch();
  const { t } = useTranslation();
  const wishCount = useSelector((state) => state.userReducer.wishCount);
  const [widgetData, setWidgetData] = useState(null);
  const [widgetList, setWidgetList] = useState([]);
  const [widgetListCount, setWidgetListCount] = useState(0);
  const [trigger, setTrigger] = useState(0);
  const [comparetrigger, setCompareTrigger] = useState(0);
  const [loader, setLoader] = useState(false);

  const limit = 10;
  const [offset, setOffset] = useState(0);


   useEffect(() => {
    window.scrollTo(0, 0);
    if (slug) {
      getWidgetDetailCount(slug, setWidgetListCount);
      getWidgetDetail(
        slug,
        setWidgetData,
        setWidgetList,
        setLoader,
        limit,
        offset,
        setOffset
      );
    }
  }, [slug]);

  // Scroll listener
  useEffect(() => {
    const debounce = (fn, delay) => {
      let timer;
      return (...args) => {
        clearTimeout(timer);
        timer = setTimeout(() => fn(...args), delay);
      };
    };

    const handleScroll = debounce(() => {
      const nearBottom =
        window.innerHeight + window.scrollY >= document.body.offsetHeight - 100;
      if (nearBottom) {
        if (loader) return;

        // Allow initial call when count is not yet available
        if (widgetListCount > 0 && widgetList.length >= widgetListCount) return;

        getWidgetDetail(
          slug,
          setWidgetData,
          setWidgetList,
          setLoader,
          limit,
          offset,
          setOffset
        );
      }
    }, 250);

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, [offset, loader, widgetList?.length, widgetListCount, slug]);


  // for dispatch value
  const handleDetails = (id) => {
    dispatch(breadcrumb(true));
    dispatch(subproduct(false));
  };
  const handleWishiList = (data) => {
    if (token) {
       widgetList?.map((s) => {
        if (data.productId == s.productId) {
          s.wishListStatus = s.wishListStatus == 0 ? 1 : 0;
        }
      });
      if (data?.wishListStatus == 0) {
        widgetDeleteApi(data.productId, dispatch, wishCount);
      } else {
        widgetPostApi(data.productId, dispatch, wishCount);
      }
    } else {
      router.push("/buyer-login");
    }
    setTrigger(trigger + 1);
  };

  const handleAddItemToCompare = (e, productId) => {
    let data = 0;
    if (productIds !== null && productIds !== undefined) {
      var productIdIndex = productIds?.indexOf(productId);
      if (productIdIndex !== -1) {
        let localCompareId = GetCompareId();
        localCompareId.splice(productIdIndex, 1);
        localStorage.setItem("compareId", JSON.stringify(localCompareId));
        setCompareTrigger(comparetrigger + 1);
        ToastMessage({
          type: "success",
          message: t("toastMessage.compareProductRemove"),
        });
      } else {
        const compareIds = [...productIds, productId];
        if (compareIds && compareIds.length > 2) {
          ToastMessage({
            type: "error",
            message: t("toastMessage.compareProductLimit"),
          });
        } else {
          productCompareApi(
            data,
            compareIds,
            setCompareTrigger,
            comparetrigger,
            productIds,
          );
        }
      }
    } else {
      let productIds = [];
      productIds.push(productId);
      productCompareApi(data, productIds, setCompareTrigger, comparetrigger);
    }
  };

  function compareCheckFunction(productId) {
    if (productIds && productIds.length !== 0) {
      let compareCheck = productIds.some((value) => value === productId);
      return compareCheck;
    }
  }

  return (
    <div className="mx-auto max-w-screen-2xl p-4 lg:p-4">
      <h1 className="text-black-200 mb-4 text-xl font-black capitalize sm:mb-8">
        {widgetData?.widgetTranslation?.[0]?.widgetLongTitle
          ? widgetData?.widgetTranslation?.[0]?.widgetLongTitle
          : widgetData?.widgetLongTitle}
      </h1>

      <div className="mt-6 grid grid-cols-1 gap-x-4 gap-y-5 sm:grid-cols-3 lg:grid-cols-5 xl:gap-x-4">
        {widgetList?.length > 0 &&
          widgetList?.map((datachild, index) => (
            <Fragment key={index}>
              {/* Compare product route */}

              {compareProductIds && compareProductIds?.length > 0 && (
                <Link
                  prefetch={false}
                  href={"/product-compare"}
                  className={
                    "fixed bottom-11 right-11 z-10 inline-flex h-10 w-36 cursor-pointer items-center justify-center gap-x-4 rounded-md bg-[var(--color)] px-8 py-4 text-base font-medium uppercase text-white"
                  }
                >
                  {t("compare.Compare")}
                  <span className="bg-green-slate flex min-w-2 items-center justify-center">
                    {compareProductIds?.length}
                  </span>
                </Link>
              )}

              {/* List card */}

              <div
                className="group-item group relative block overflow-hidden rounded-[5px] hover:shadow-[0px_0px_10px_#00000014]"
                key={index}
              >
                {/* Wishlist and compare buttons */}
                <div className="absolute right-2 top-2 z-20 flex items-center space-x-3 rtl:left-2 rtl:right-[unset]">
                  {/* Compare button */}
                  <div
                    onClick={(e) =>
                      handleAddItemToCompare(e, datachild?.productId)
                    }
                    className={
                      compareCheckFunction(datachild?.productId)
                        ? "tooltip3 flex h-10 w-10 cursor-pointer items-center justify-center rounded-full bg-white text-2xl shadow-md"
                        : `item tooltip3 flex h-10 w-10 cursor-pointer items-center justify-center rounded-full bg-white text-2xl shadow-md`
                    }
                  >
                    {compareCheckFunction(datachild?.productId) ? (
                      <img
                        src="/img/compare-outline-active.svg"
                        alt="compare-outline-active"
                        className="w-5"
                      />
                    ) : (
                      <img
                        src="/img/compare-outline.svg"
                        alt="compare-outline"
                        className="w-5"
                      />
                    )}
                    <span className="tooltip-text shadow-md">
                      {t("tooltip.compare")}
                    </span>
                  </div>
                  {/* Wishlist button */}
                  <div
                    onClick={() => handleWishiList(datachild)}
                    className={
                      datachild.wishListStatus == 0
                        ? "item tooltip3 z-20 flex h-10 w-10 cursor-pointer items-center justify-center rounded-full bg-white text-2xl shadow-md"
                        : `tooltip3 flex h-10 w-10 cursor-pointer items-center justify-center rounded-full bg-white text-2xl shadow-md`
                    }
                  >
                    {datachild.wishListStatus == 0 ? (
                      <img
                        src="/img/heart-outline-wisit.svg"
                        alt="heart-outline-wisit"
                        className="w-5"
                      />
                    ) : (
                      <img
                        src="/img/heart-select-red-wisit.svg"
                        alt="heart-select-red-wisit"
                        className="w-5"
                      />
                    )}

                    <span className="tooltip-text shadow-md">
                      {t("Breadcrumb.wishList")}
                    </span>
                  </div>
                </div>

                <Link
                  prefetch={false}
                  href={`/products/${datachild?.productSlug}`}
                  onClick={() => handleDetails(datachild?.productId)}
                >
                  {/* Product Image */}
                  <div className="h-[228px] bg-[#f8f8f8]">
                    <img
                      className="h-full w-full object-contain"
                      src={
                        imageUrl +
                        "?path=" +
                        datachild?.containerName +
                        "&name=" +
                        datachild?.image +
                        "&width=288&height=288"
                      }
                      width={288}
                      height={288}
                      alt={datachild?.name}
                      onError={({ currentTarget }) => {
                        currentTarget.onerror = null;
                        currentTarget.src = "/img/no-image.png";
                        currentTarget.alt = "no-image";
                      }}
                    />
                  </div>

                  {/* Product Title, Price, Rating */}
                  <div className="p-[12px]">
                    <span className="mb-[10px] line-clamp-2 min-h-[40px] text-[14px] font-normal capitalize leading-[20px] text-[#8B8BA3] hover:underline">
                      {datachild?.productNameTrans
                        ? datachild?.productNameTrans
                        : datachild?.name}
                    </span>
                    <div className="mb-[10px] flex flex-wrap items-center gap-[8px]">
                      <p className="whitespace-nowrap text-[18px] font-[900] leading-[22px] text-[#333333]">
                        <CustomCurrencyFormatter
                          price={PriceCalculate(
                            datachild?.price,
                            datachild?.pricerefer,
                            datachild?.flag,
                            datachild?.taxType,
                            datachild?.taxValue,
                          )}
                        />
                      </p>
                      {datachild?.flag == 1 && (
                        <>
                          <p className="whitespace-nowrap text-[14px] font-[600] leading-[17px] text-[#8B8BA3] line-through">
                            {t("productdetails.Mrp")}{" "}
                            <CustomCurrencyFormatter
                              price={PriceTaxCalculate(
                                datachild?.price,
                                datachild?.taxType,
                                datachild?.taxValue,
                              )}
                            />
                          </p>
                          <p className="whitespace-nowrap text-[12px] font-[600] leading-[15px] text-[var(--color)]">
                            {Math.round(
                              ((PriceTaxCalculate(
                                datachild?.price,
                                datachild?.taxType,
                                datachild?.taxValue,
                              ) -
                                PriceTaxCalculate(
                                  datachild?.pricerefer,
                                  datachild?.taxType,
                                  datachild?.taxValue,
                                )) /
                                PriceTaxCalculate(
                                  datachild?.price,
                                  datachild?.taxType,
                                  datachild?.taxValue,
                                )) *
                                100,
                            )}
                            {t("productdetails.OFF")}
                          </p>
                        </>
                      )}
                    </div>
                    {addonList &&
                      addonList?.["rating-review"] &&
                      addonList?.["rating-review"]?.status == 1 &&
                      datachild?.ratingCount > 0 &&
                      datachild?.reviewCount > 0 && (
                        <div className="flex items-center gap-[5px]">
                          <StarRatings
                            rating={
                              datachild?.ratingCount &&
                              parseFloat(datachild?.ratingCount)
                            }
                            starRatedColor="black"
                            starDimension="13px"
                            starSpacing="3px"
                            numberOfStars={5}
                            name="rating"
                            svgIconPath="M12.489,5.03a.665.665,0,0,0-.572-.457L8.3,4.245,6.873.9A.666.666,0,0,0,5.649.9L4.22,4.245.6,4.573A.666.666,0,0,0,.227,5.738l2.732,2.4-.806,3.548a.665.665,0,0,0,.99.72l3.117-1.864L9.377,12.4a.666.666,0,0,0,.991-.72L9.562,8.134l2.732-2.4A.666.666,0,0,0,12.489,5.03ZM6.321,10.5"
                            svgIconViewBox="0 0 12.522 12"
                          />
                          <p className="mt-1 text-[12px] font-normal leading-[20px] text-[#8B8BA3]">
                            {`(${datachild?.reviewCount} ${
                              datachild?.reviewCount <= 1
                                ? t("productcontent.review")
                                : t("productcontent.reviews")
                            })`}
                          </p>
                        </div>
                      )}
                  </div>
                </Link>
              </div>
            </Fragment>
          ))}

        {loader && <WidgetListSkeleton />}
      </div>
    </div>
  );
}

const WidgetListSkeleton = () => {
  return (
    <>
      {Array.from({ length: 10 }).map((_, index) => (
        <div key={index} className="flex flex-col items-center space-y-4">
          <div role="status" className="w-full max-w-md animate-pulse rounded">
            <div className="mb-4 flex h-48 items-center justify-center bg-gray-300">
              <svg
                className="h-10 w-10 text-gray-200"
                aria-hidden="true"
                xmlns="http://www.w3.org/2000/svg"
                fill="currentColor"
                viewBox="0 0 16 20"
              >
                <path d="M14.066 0H7v5a2 2 0 0 1-2 2H0v11a1.97 1.97 0 0 0 1.934 2h12.132A1.97 1.97 0 0 0 16 18V2a1.97 1.97 0 0 0-1.934-2Z" />
              </svg>
            </div>
            <div className="mb-4 h-2.5 w-48 rounded-full bg-gray-200"></div>
            <div className="mb-2 h-2.5 w-32 rounded-full bg-gray-200"></div>
          </div>
        </div>
      ))}
    </>
  );
};
