"use client";
//3rd Party
import Select from "react-select";
//Component
import { useTranslation } from "react-i18next";

export default function ReportAndAbuse({
  reportAbuseModal,
  setReportAbuseModal,
  handleReportChange,
  handleClickReport,
  reasonList,
  reason,
  remark,
  reasonError,
  remarkError,
}) {
 const { t } = useTranslation();
  return (
    <div
      aria-modal="true"
      id="static-modal"
      data-modal-backdrop="static"
      tabIndex="-1"
      aria-hidden="true"
      className={`${
        reportAbuseModal
          ? "overflow-y-auto overflow-x-hidden fixed top-0 right-0 left-0 z-100 justify-center bg-light-modal items-center w-full md:inset-0 h-full px-4 max-h-full flex "
          : " hidden"
      }`}
    >
      <div className="relative w-full max-h-full max-w-800">
        {/* <!-- Modal content --> */}
        <div className="relative bg-white shadow dark:bg-gray-700">
          {/* <!-- Modal header --> */}
          <div className="absolute top-6 right-6">
            <button
              type="button"
              data-modal-hide="static-modal"
              onClick={() => setReportAbuseModal(false)}
            >
              <img src="/img/modal-x.svg" alt="modal-x" />
            </button>
          </div>

          {/* <!-- Modal body --> */}
          <div className="p-4 padding-8">
            <h3 className="mb-1.5 font-bold text-black-300 text-xl">
              {t("productcontent.RptAbuse")}
            </h3>
            <div className="flex items-center justify-between w-full m-4">
              <div className="flex-1 w-full m-4 mb-3 cursor-pointer">
                <label className="text-black-300">
                  {t("productcontent.Reason")}
                </label>
                <Select
                  name="select"
                  options={reasonList?.map((reason) => ({
                    label: reason?.reason,
                    value: reason?.id,
                  }))}
                  value={reason}
                  onChange={(e) => handleReportChange(e, "reason")}
                  className={`text-black-300 cursor-pointer ${
                    reasonError && "error-border-select"
                  } "css-13cymwt-control"`}
                />
              </div>
              <div className="flex-1 m-4 mb-3">
                <label className="text-black-300">
                  {t("productcontent.Remark")}
                </label>
                <textarea
                  value={remark}
                  onChange={(e) =>
                    handleReportChange(e.target.value.trimStart(), "remark")
                  }
                  className={
                    remarkError !== ""
                      ? "w-full  px-4 py-3.5 text-black-300 h-148 placeholder: text-gray-500 text-sm font-normal  rounded-md focus:border-red border border-red"
                      : "w-full  px-4 py-3.5 text-black-300 h-148 placeholder: text-gray-500 text-sm font-normal  rounded-md focus:ring-0 border"
                  }
                  placeholder={t("myprofile.MentDetails")}
                ></textarea>
              </div>
            </div>
          </div>
          {/* <!-- Modal footer --> */}
          <div className="flex justify-end bg-gray-slate-100 md:px-8 md:py-4 dark:border-gray-600">
            <div className="flex items-center justify-end p-4">
              <button
                onClick={(e) => handleClickReport("cancel")}
                className="bg-white hover:bg-black px-8 border border-black rounded h-[49px] font-semibold text-black hover:text-white text-sm uppercase"
              >
                {t("myprofile.Cancel")}
              </button>
            </div>
            <div className="flex items-center justify-end p-4">
              <button
                onClick={(e) => handleClickReport("submit")}
                className="bg-black hover:bg-zinc-800 px-8 rounded h-[49px] font-semibold text-white hover:text-white text-sm uppercase"
              >
                {t("productcontent.SUBMIT")}
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
