// AxiosInstance
import axiosInstance from "@/api/interceptor";

// Utils
import { setLoginCookies } from "@/utilities/cookies";

export async function facebookdetailApi(response) {
  const dataAuth = JSON.stringify({
    emailId: "",
    oauthData: response.authResponse,
  });

  const { data } = await axiosInstance.post(`customer/login`, dataAuth);
  if (data.status == 1) {
    setLoginCookies(data);
    localStorage.setItem("cartItems", JSON.stringify([]));
    localStorage.setItem("email", dataAuth?.email);
    localStorage.setItem("userProfile", JSON.stringify(data.data?.user));
    localStorage.setItem("BearerToken", JSON?.stringify(data?.data?.token));
  }
}
