// AxiosInstance
import axiosInstance from "@/api/interceptor";

// API
import { AddToCartLocal } from "./AddToCart";

// Components
import ToastMessage from "@/Components/ToastMessage";

// common product vendor count api
export async function CommonProductVendorCountApi(id, setVendorCount) {
  const result = await axiosInstance.get(
    `store-common-product/vendor-count/${id}`
  );
  if (result?.data?.status === 1) {
    setVendorCount(result.data.data);
  }
}

//  common product vendor count List api
export async function CommonProductVendorListApi(slug, setVendorList) {
  const result = await axiosInstance.get(
    `store-common-product/common-product-list/${slug}`
  );
  if (result?.data?.status === 1) {
    setVendorList(result.data.data);
  }
}

//  common product add to cart api
export async function addToCartApi(
  type,
  datacart,
  dispatch,
  setSellerOptionModal
) {
  const data = JSON.stringify({
    productId: datacart?.productId,
    productPrice: datacart?.price,
    quantity: datacart?.minQuantityAllowedCart
      ? datacart?.minQuantityAllowedCart
      : 1,
    type: type,
    productOptionValueId: "",
    productVarientOptionId: "",
    varientName: "",
    skuName: datacart?.skuName,
    tirePrice: "",
    vendorId: datacart?.vendorId,
  });
  const result = await axiosInstance.post("cart", data);
  if (result && result.data && result.data.status === 1) {
    result.data.data.quantity = parseInt(
      datacart?.minQuantityAllowedCart ? datacart?.minQuantityAllowedCart : 1
    );
    AddToCartLocal(datacart, dispatch);
    setSellerOptionModal(false);
    ToastMessage({ type: "success", message: result.data.message });
  } else {
    ToastMessage({ type: "error", message: result.data.message });
  }
}

