"use client";
//React and Next
import { Fragment, useEffect, useState } from "react";
import { usePathname } from "next/navigation";
//Utils
import { GetAddonsList } from "@/utilities/cookies";
//API
import {
  getProductSpecification,
  getSpecificationList,
} from "./SpecificationApi";
//Component
import { useTranslation } from "react-i18next";

export default function Specification({ categoryIds }) {
  const [addonlist, setAddonList] = useState();
  const [specificationList, setSpecificationList] = useState([]);
  const [productSpecification, setProductSpecification] = useState([]);
  const pathname = usePathname();
  const slug = pathname.split("/").at(-1);
  const [attributes, setAttributes] = useState([]);
  const [specificationValue, setSpecificationValue] = useState([]);
  const { t } = useTranslation();

  const handleAddonListGet = async () => {
    const addon = await GetAddonsList();
    setAddonList(addon);
  };

  useEffect(() => {
    getProductSpecification(slug, setProductSpecification);
    handleAddonListGet();
    if (categoryIds?.length > 0) {
      getSpecificationList(
        categoryIds.toString(),
        setAttributes,
        setSpecificationList,
      );
    }
  }, [slug]);

  function mergeAttributes(attributeList, productAttributes) {
    const mergedArrtibutes = attributeList.map((attribute) => {
      const matchingObj = productAttributes.find(
        (productAttribute) => productAttribute.attributeId == attribute.id,
      );
      if (matchingObj) {
        return matchingObj;
      } else {
        return { attributeValue: "-" };
      }
    });
    return mergedArrtibutes;
  }

  function mergeSpecificationValues(data) {
    const result = [];
    const map = new Map();

    data.forEach((item) => {
      const { specificationId, specificationValue } = item;

      if (!map.has(specificationId)) {
        map.set(specificationId, []);
      }

      specificationValue.forEach((specVal, index) => {
        if (!map.get(specificationId)[index]) {
          map.get(specificationId)[index] = [];
        }

        map.get(specificationId)[index].push(...specVal);
      });
    });

    map.forEach((specificationValue, specificationId) => {
      result.push({
        specificationId,
        specificationValue: specificationValue.map((arr) => [...arr]),
      });
    });

    return result;
  }

  useEffect(() => {
    if (productSpecification?.length > 0 && attributes?.length > 0) {
      const updatedSpecificationValues = [];

      productSpecification.forEach((specification) => {
        const correspondingAttribute = attributes.find(
          (attr) => attr.specificationId === specification.specificationId,
        );
        attributes.forEach((specificationlist) => {
          if (
            specificationlist?.specificationId === specification.specificationId
          ) {
            const attributeGroupsValues = specification.attributeGroups.map(
              (attributeGroup) => {
                return mergeAttributes(
                  specificationlist.attributes,
                  attributeGroup.attributes,
                );
              },
            );

            updatedSpecificationValues.push({
              specificationId: specification.specificationId,
              specificationValue: attributeGroupsValues,
            });
          }
        });
        const finalSpecification = mergeSpecificationValues(
          updatedSpecificationValues,
        );
        setSpecificationValue(finalSpecification);
      });
    }
  }, [productSpecification, attributes]);

  return (
    <div>
      <h4 className="mb-4 text-2xl font-black text-black">
        {t("productdetails.Specification")}
      </h4>
      {addonlist?.["product-attribute"] &&
      addonlist?.["product-attribute"]?.additionalInfo?.isSimplified == 1 ? (
        productSpecification?.length > 0 ? (
          productSpecification?.map(
            (specification, specIndex) =>
              specification?.attributeGroups?.length > 0 && (
                <Fragment key={specIndex}>
                  <h5 className="text-black-300 my-8 text-lg font-bold">
                    {specification?.specificationNameTrans !== ""
                      ? specification?.specificationNameTrans
                      : specification?.specificationName}
                  </h5>
                  <div className="grid grid-cols-1 gap-y-6 lg:grid-cols-2">
                    {specification?.attributeGroups[0]?.attributes?.map(
                      (attribute, attributeIndex) => (
                        <div
                          className="grid grid-cols-2 items-start gap-8"
                          key={`attribute${attributeIndex}`}
                        >
                          <h4 className="text-black-300 text-lg font-bold">
                            {attribute?.attributeNameTrans !== ""
                              ? attribute?.attributeNameTrans
                              : attribute?.attributeName}
                          </h4>
                          <p className="text-black-300 text-lg font-normal">
                            {attribute?.attributeValues
                              ?.map((value) =>
                                value?.valueTrans !== ""
                                  ? value?.valueTrans
                                  : value?.value,
                              )
                              ?.join(", ")}
                          </p>
                        </div>
                      ),
                    )}
                  </div>
                  <div className="bg-grey400 my-8 h-px w-full"></div>
                </Fragment>
              ),
          )
        ) : (
          <>
            <p>{t("productcontent.NoSpecs")}</p>
            <div className="bg-grey400 my-6 h-px w-full 2xl:my-14"></div>
          </>
        )
      ) : specificationValue.length > 0 ? (
        specificationList?.map((specification, specificationIndex) =>
          specificationValue?.map(
            (specificatiovalue, specvalueIndex) =>
              specification?.id == specificatiovalue?.specificationId && (
                <div
                  className="border-grey200 mb-6 overflow-auto border-b pb-6"
                  key={`specification${specvalueIndex}`}
                >
                  <h5 className="text-black-300 my-4 text-lg font-bold">
                    {/* {specification?.specificationNameTrans !== '' ? specification?.specificationNameTrans : specification?.name} */}
                    {specification?.name}
                  </h5>
                  <table className="w-full table-auto border-collapse whitespace-nowrap sm:whitespace-normal">
                    {specificationValue?.map(
                      (specificatiovalue, specIndex) =>
                        specification?.id ==
                          specificatiovalue?.specificationId && (
                          <thead key={`specificationHeading${specIndex}`}>
                            <tr>
                              {specification?.attributeGroups?.map(
                                (attributeGroup, index) =>
                                  attributeGroup?.attributes?.map(
                                    (attribute, attributeIndex) => (
                                      <th
                                        key={`attributeName${attributeIndex}`}
                                        className="bg-lightgrey border-darkgrey text-black-300 border border-dashed px-3 py-4 text-left text-base font-normal"
                                      >
                                        {/* {attribute?.attributeNameTrans !== '' ? attribute?.attributeNameTrans : attribute?.name} */}
                                        {attribute?.name}
                                      </th>
                                    ),
                                  ),
                              )}
                            </tr>
                          </thead>
                        ),
                    )}

                    <tbody>
                      {specificationValue?.map(
                        (specificatiovalue, index) =>
                          specification?.id ==
                            specificatiovalue?.specificationId &&
                          specificatiovalue?.specificationValue?.map(
                            (attibutes, attributeIndex) => (
                              <tr key={`attribute${attributeIndex}`}>
                                {attibutes?.map(
                                  (attributevalue, attributeValueIndex) =>
                                    attributevalue?.attributeValue == "-" ? (
                                      <td
                                        key={`attributeValue${attributeValueIndex}`}
                                        className="border-darkgrey text-black-300 border border-dashed px-3 py-4 text-center text-base font-normal"
                                      >
                                        {attributevalue?.attributeValue}
                                      </td>
                                    ) : (
                                      <td
                                        key={`attributeValue${attributeValueIndex}`}
                                        className="border-darkgrey text-black-300 border border-dashed px-3 py-4 text-base font-normal"
                                      >
                                        {attributevalue?.attributeValues
                                          ?.map((value) =>
                                            value?.valueTrans !== ""
                                              ? value?.valueTrans
                                              : value?.value,
                                          )
                                          ?.join(", ")}
                                      </td>
                                    ),
                                )}
                              </tr>
                            ),
                          ),
                      )}
                    </tbody>
                  </table>
                </div>
              ),
            // {specificationIndex == specificationValue.length > 1 &&
            //             <div className='w-full h-px my-8 bg-grey400'></div>}
          ),
        )
      ) : (
        <>
          <span className="text-black-300">{t("productcontent.NoSpecs")}</span>
          <div className="bg-grey400 my-6 h-px w-full 2xl:my-14"></div>
        </>
      )}
    </div>
  );
}
