//React and Next
import Link from "next/link";
//3rdParty
import moment from "moment";
//Utils
import { imageUrl } from "@/utilities/url";
import { PriceTaxCalculate } from "@/utilities/PriceCalculate";
//Components
import CustomCurrencyFormatter from "@/Components/CustomCurrencyFormatter";
import { useTranslation } from "react-i18next";

export default function QuotationCard({ quotationdetailList }) {
  const { t } = useTranslation();

  return quotationdetailList?.quotationProduct?.map((data, index) => (
    <div key={index} className="p-4 mb-4 border rounded sm:p-5 border-grey">
      <div className="flex md:flex-row flex-col justify-between items-start md:rtl:space-x-[unset] md:space-x-3 space-y-3 sm:mb-8">
        <div className="flex sm:flex-row flex-col items-start sm:rtl:space-x-[unset] sm:space-x-4 space-y-4 sm:space-y-0 md:rtl:ml-3 w-full sm:w-auto">
          <img
            src={
              imageUrl +
              "?path=" +
              data?.productInformationDetail?.productImage?.[0]?.containerName +
              "&name=" +
              data?.productInformationDetail?.productImage?.[0]?.image +
              "&width=250&height=250"
            }
            alt={data?.productInformationDetail?.name}
            onError={({ currentTarget }) => {
              currentTarget.onerror = null;
              currentTarget.src = "/img/no-image.png";
              currentTarget.alt = "no-image";
            }}
            className="mx-auto sm:mx-[unset] sm:rtl:ml-4 w-24 h-24 object-contain"
          />
          <div>
            <Link
              prefetch={false}
              href={`/products/${data?.productInformationDetail?.productSlug}`}
              className="text-base font-medium leading-6 text-black-300 line-clamp-3"
            >
              {data?.productNameTrans
                ? data?.productNameTrans
                : data?.productName}
            </Link>
            <div className="flex items-center mb-px space-x-2">
              <p className="text-base font-bold text-black-300">
                <CustomCurrencyFormatter
                  price={PriceTaxCalculate(
                    data?.bestQuotedPrice
                      ? data?.bestQuotedPrice
                      : data?.productPrice
                      ? data?.productPrice
                      : data?.basePrice,
                    data?.taxType,
                    data?.taxValue
                  )}
                />
              </p>
              {data?.bestQuotedPrice && (
                <span className="text-sm font-bold line-through text-green600">
                  <CustomCurrencyFormatter
                    price={PriceTaxCalculate(
                      data?.productPrice ? data?.productPrice : data?.basePrice,
                      data?.taxType,
                      data?.taxValue
                    )}
                  />
                </span>
              )}
            </div>
            {data?.bestQuotedPrice && (
              <span className="mb-1.5 font-normal text-green600 text-xs italic">
                {t("myprofile.BestPrice")}
              </span>
            )}
            <p className="text-sm font-medium text-black-300">
              {t("myprofile.Quantity")} : {data?.quantity}
            </p>
          </div>
        </div>
        <div className="w-full sm:w-auto">
          {data?.quotationStatusId == 1 ? (
            <div className="flex justify-center items-center space-x-1 rtl:space-x-[unset] bg-lightgreen px-4 py-2 rounded">
              <img src="/img/tick.svg" alt="tick" className="rtl:ml-1" />
              <p className="text-base font-semibold text-green-200">
                {t("myprofile.Approved")}
              </p>
            </div>
          ) : (
            <div className="flex justify-center items-center space-x-1 rtl:space-x-[unset] bg-pending px-4 py-2 rounded">
              <img src="/img/pending.svg" alt="pending" className="rtl:ml-1" />
              <p className="text-base font-semibold text-orange500">
                {t("myprofile.Pending")}
              </p>
            </div>
          )}
        </div>
      </div>
      {data?.replyMessage && (
        <>
          <div className="flex flex-col items-start justify-between mb-3 md:flex-row md:items-center">
            <div className="flex items-center space-x-2.5 rtl:space-x-[unset]">
              <h3 className="rtl:ml-2.5 font-black text-black text-base">
                {t("myprofile.SellMsg")}
              </h3>
              <p className="text-xs italic font-normal text-black">
                {moment(quotationdetailList?.createdDate).format(
                  "MMMM Do YYYY, h:mm:ss a"
                )}
              </p>
            </div>
            <Link
              prefetch={false}
              href={`/shop/${data?.vendorQuotation?.vendor?.displayNameUrl}`}
              className="flex items-center space-x-2 text-sm font-medium cursor-pointer text-link"
            >
              <img
                src={
                  imageUrl +
                  "?path=" +
                  data?.vendorQuotation?.vendor?.companyLogoPath +
                  "&name=" +
                  data?.vendorQuotation?.vendor?.companyLogo +
                  "&width=30&height=30"
                }
                alt={data?.vendorQuotation?.vendor?.companyName}
                className="w-5 h-5"
                onError={({ currentTarget }) => {
                  currentTarget.onerror = null;
                  currentTarget.src = "/img/user1.jpg";
                  currentTarget.alt = "user";
                }}
              />
              <p>{data?.vendorQuotation?.vendor?.companyName}</p>
            </Link>
          </div>
          <div className="px-4 py-3 bg-light-grey100">
            <p className="text-sm font-normal text-black-300">
              {data?.replyMessage}
            </p>
          </div>
        </>
      )}
    </div>
  ));
}
