"use client";

// React
import { useEffect, useState } from "react";

// API
import { GetProductReviewCountApi } from "./ratingApi";

// 3rd Party
import StarRatings from "react-star-ratings";

// Components
import { useTranslation } from "react-i18next";

const AverageRating = ({
  productDetail,
  reviewAndRatingCount,
  setReviewAndRatingCount,
  tabChangeScroll,
}) => {
  const { t } = useTranslation();
  const [isAverageRatingLoading, setIsAverageRatingLoading] = useState(true);

  useEffect(() => {
    handleGetAverageProductRating(productDetail?.skuId);
  }, [productDetail?.skuId]);

  const handleGetAverageProductRating = async (skuId) => {
    if (skuId) {
      setIsAverageRatingLoading(true);
      const productDetails = await GetProductReviewCountApi(skuId);
      setReviewAndRatingCount(productDetails?.data);
      setIsAverageRatingLoading(false);
    }
  };

  return isAverageRatingLoading ? (
    <AverageRatingSkeleton />
  ) : (
    <div className="flex items-center space-x-2">
      {reviewAndRatingCount &&
      reviewAndRatingCount?.rating &&
      reviewAndRatingCount?.reviewCount ? (
        <>
          {" "}
          <div className="text-black-300 flex items-center space-x-[5px] text-sm font-bold leading-normal">
            {reviewAndRatingCount?.rating &&
              parseFloat(
                reviewAndRatingCount && reviewAndRatingCount?.rating,
              ).toFixed(1)}{" "}
            <span className="mb-[5px] ltr:ml-[5px] rtl:mr-[5px]">
              <StarRatings
                rating={
                  reviewAndRatingCount?.rating &&
                  parseFloat(
                    reviewAndRatingCount && reviewAndRatingCount?.rating,
                  )
                }
                starRatedColor="black"
                starDimension="13px"
                starSpacing="3px"
                numberOfStars={5}
                name="rating"
                svgIconPath="M12.489,5.03a.665.665,0,0,0-.572-.457L8.3,4.245,6.873.9A.666.666,0,0,0,5.649.9L4.22,4.245.6,4.573A.666.666,0,0,0,.227,5.738l2.732,2.4-.806,3.548a.665.665,0,0,0,.99.72l3.117-1.864L9.377,12.4a.666.666,0,0,0,.991-.72L9.562,8.134l2.732-2.4A.666.666,0,0,0,12.489,5.03ZM6.321,10.5"
                svgIconViewBox="0 0 12.522 12"
              />
            </span>{" "}
          </div>
          <a
            onClick={() => tabChangeScroll("#section3")}
            role="button"
            key="4"
            className="text-black-300 cursor-pointer text-sm font-normal underline"
          >
            {reviewAndRatingCount &&
              reviewAndRatingCount?.reviewCount &&
              reviewAndRatingCount?.reviewCount !== 0 &&
              reviewAndRatingCount?.reviewCount +
                " " +
                `${
                  reviewAndRatingCount?.reviewCount <= 1
                    ? t("productcontent.review")
                    : t("productcontent.reviews")
                }`}
          </a>
        </>
      ) : (
        <p className="text-black-300 my-[3px] text-sm font-bold">
          {t("productdetails.ProdRev")}
        </p>
      )}
    </div>
  );
};

export default AverageRating;

export function AverageRatingSkeleton() {
  return (
    <div className="flex h-[26px] animate-pulse items-center space-x-2">
      {/* Rating number */}
      <div className="flex items-center space-x-[5px]">
        <div className="h-4 w-8 rounded bg-gray-200" />

        {/* Stars */}
        <div className="flex space-x-[3px]">
          {Array.from({ length: 5 }).map((_, i) => (
            <div key={i} className="h-[13px] w-[13px] rounded bg-gray-200" />
          ))}
        </div>
      </div>

      {/* Review count link */}
      <div className="h-4 w-20 rounded bg-gray-200" />
    </div>
  );
}
