"use client";
//React and Next
import { useEffect, useState } from "react";
import { useRouter } from "nextjs-toploader/app";
//Redux
import { useDispatch, useSelector } from "react-redux";
import { quotelist } from "@/redux/slices/userSlice";
//Utils
import { GetAddonList } from "@/utilities/localStorage";
//Components
import Contactus from "@/Components/Contactus";
import QuotationProductList from "./QuotationProductList";
import QuotationContact from "./QuotationContact";
import { useTranslation } from "react-i18next";

export default function QuotationRequest({ token }) {
  const [comment, setCommet] = useState("");
  const quantity = [];
  const changevalue = 0;
  const [trigger, setTrigger] = useState(0);
  const addonList = GetAddonList();
  const qoutedetails = useSelector((state) => state.userReducer.quote);
  const [productList, setProductList] = useState([]);
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const router = useRouter();

  const handleIecrement = (index, data) => {
    const updatedProductList = productList.map((product) => {
      if (
        product?.productId === data.productId &&
        product?.skuId === data?.skuId
      ) {
        return { ...product, quantity: product.quantity + 1 };
      }

      return product;
    });

    dispatch(quotelist(updatedProductList));
    setTrigger(trigger + 1);
  };

  const handleDecrement = (index, data) => {
    const updatedProductList = productList.map((product) => {
      if (
        product?.productId === data.productId &&
        product?.skuId === data?.skuId
      ) {
        return { ...product, quantity: product.quantity - 1 };
      }
      return product;
    });

    dispatch(quotelist(updatedProductList));
    setTrigger(trigger + 1);
  };

  useEffect(() => {
    if (addonList?.["product-quotation"]?.status == 0) {
      router.push("/");
    }
  }, []);

  useEffect(() => {
    setProductList(JSON.parse(JSON.stringify(qoutedetails)));
  }, [qoutedetails, changevalue]);

  return (
    addonList?.["product-quotation"]?.status == 1 && (
      <div className="p-6 mt-1 bg-lightgrey">
        <div className="mx-auto max-w-screen-2xl">
          <h3 className="mb-4 text-lg font-black text-black-300">
            {t("myprofile.Quotations")}
          </h3>
          <div className="flex md:flex-row flex-col rtl:space-x-[unset] md:space-x-4 space-y-4 md:space-y-0 mb-20">
            <QuotationProductList
              setCommet={setCommet}
              comment={comment}
              handleDecrement={handleDecrement}
              handleIecrement={handleIecrement}
              quantity={quantity}
              changevalue={changevalue}
              setTrigger={setTrigger}
              trigger={trigger}
              productList={productList}
            />
            <QuotationContact comment={comment} />
          </div>
        </div>

        <Contactus token={token} />
      </div>
    )
  );
}
