"use client";
import axiosInstance from "@/api/interceptor";

//  coupon apply api
export async function ApplyCouponApi(coupon, emailId, productDetail) {
  const data = {
    couponCode: coupon,
    emailId: emailId,
    productDetail: productDetail,
  };
  const result = await axiosInstance.post(`customer-coupon/apply-coupon`, data);
  return result?.data;
}
