"use client";
//API
import axiosInstance from "@/api/interceptor";
//Component
import ToastMessage from "@/Components/ToastMessage";

//Address profile api
export const Get_Support_List_Api = async (
  setSupport_List_Api_Result,
  setLoader,
  offset,
  limit,
  keyword
) => {
  const result = await axiosInstance.get(
    `customer-support-ticket?limit=${limit}&offset=${offset}&keyword=${keyword}`
  );
  if (result?.data?.status == 1) {
    setSupport_List_Api_Result(result?.data?.data);
    setLoader(false);
  } else {
    setLoader(false);
  }
};

export const Get_Support_List_CountApi = async (settot_Count, keyword) => {
  const result = await axiosInstance.get(
    `customer-support-ticket?count=1&keyword=${keyword}`
  );
  if (result?.data?.status == 1) {
    settot_Count(result?.data?.data);
  }
};

export const Get_Support_category_List_Api = async (
  setSupport_CategoryList_Api_Result,
  Parent_category_Id
) => {
  let parentcat_ID = ["", undefined, null].includes(Parent_category_Id)
    ? 0
    : Parent_category_Id;

  const result = await axiosInstance.get(
    `customer-support-ticket/ticket-category?categoryType=0&status=1&parentCategoryId=${parentcat_ID}`
  );
  if (result?.data?.status == 1) {
    setSupport_CategoryList_Api_Result(result?.data?.data);
  }
};

