export async function Media_Post_api(
  data,
  setmedia_Api_result,
  setmedia_Loader
) {
  try {
    const result = await axiosInstance.post("media/upload-file", data);

    if (result && result.data?.status === 1) {
      const uploadedData = result.data.data;

      setmedia_Api_result((prev) => {
        return Array.isArray(prev) ? [...prev, uploadedData] : [uploadedData];
      });

      // ToastMessage({ type: "success", message: result.data.message });
    } else {
      ToastMessage({ type: "error", message: result.data.message });
    }

    setmedia_Loader(false);
  } catch (error) {
    ToastMessage({
      type: "error",
      message: "Something went wrong during upload.",
    });
    setmedia_Loader(false);
  }
}

export async function Support_ticket_Post_api(data, router, setsubmitLoader) {
  const result = await axiosInstance.post("customer-support-ticket", data);

  if (result && result.data && result.data.status === 1) {
    ToastMessage({ type: "success", message: result.data.message });
    router.push(`/account-settings/support`);
    setsubmitLoader(false);
  } else {
    ToastMessage({ type: "error", message: result.data.message });
    setsubmitLoader(false);
  }
}

