//Redux
import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  variantFilter: [],
  attributeFilter: [],
  selectedFilterIds: [],
};

export const VariantRedux = createSlice({
  name: "Variant",
  initialState,
  reducers: {
    setVariantFilter: (state, action) => {
      state.variantFilter = action.payload;
    },
    setAttributeFilter: (state, action) => {
      state.attributeFilter = action.payload;
    },
    setSelectedFilterIds: (state, action) => {
      state.selectedFilterIds = action.payload;
    },
  },
});

export const { setVariantFilter, setAttributeFilter, setSelectedFilterIds } =
  VariantRedux.actions;

export default VariantRedux.reducer;
