import axiosInstance from "@/api/interceptor";

//  get pricing group list api
export async function PriceGroupListApi(sku, setPriceGroupList, dispatch) {
  const result = await axiosInstance.get(`price-group-detail/sku/${sku}`);
  if (result?.data?.status === 1) {
    const sortValue = result.data.data?.sort(
      (a, b) => a.maxQuantity - b.maxQuantity,
    );
    setPriceGroupList(result.data.data);
  }
}
